import {useMemo} from 'react'
import {useAtomValue} from 'jotai'

import {useSessionState} from '@bpinc/ad-session-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {personalTabUserAtom} from '../atoms'


export function usePersonalTabName() {
    const selfUserId = useSessionState('userId')
    const user = useAtomValue(personalTabUserAtom)
    const {t} = useLocalizationState()

    return useMemo(() => {
        return selfUserId !== user.id
            ? t.EMAIL.queuePostfix(user.name)
            : t.EMAIL.emailPersonalQueue_header()
    }, [selfUserId, t.EMAIL, user])
}
