import {Dispatch, useEffect} from 'react'

import {usePageVisibility} from '@bpinc/lib-helpers'

import {Action} from '../reducer'

export function usePageVisibilityChangedDispatcher(dispatch: Dispatch<Action>) {
    const pageIsVisible = usePageVisibility()
    
    useEffect(() => {
        dispatch({type: 'pageVisibilityChanged', pageIsVisible})
    }, [dispatch, pageIsVisible])
}
