import type {FC, ReactNode} from 'react'
import React from 'react'
import type {Table} from '@tanstack/react-table'
import {flexRender} from '@tanstack/react-table'
import type {PrimitiveAtom} from 'jotai'
import {useAtom} from 'jotai'

import type {QueueItem, QueueItem as QueueItemType} from '@bpinc/ad-request-urls'
import {SmTable} from '@bpinc/lib-external-ui-elements'
import {useEventListener} from '@bpinc/lib-helpers'
import {useMenuKeysHandler as useTableKeysHandler} from '@bpinc/lib-ui-elements'

import {QueueItemRow} from './QueueItemRow'


interface QueueTableProps {
    noSelection?: boolean
    HeaderContent?: ReactNode
    isLoading?: boolean
    table: Table<QueueItemType>
    activeQueueItemAtom: PrimitiveAtom<QueueItem | undefined>
}

export const QueueTable: FC<QueueTableProps> = ({
    table,
    noSelection,
    activeQueueItemAtom,
    HeaderContent,
}) => {
    const [activeQueueItem, setActiveQueueItem] = useAtom(activeQueueItemAtom)

    const handlerTableKeysHandler = useTableKeysHandler(
        'queue-item',
        'queue-table',
    )
    useEventListener('keydown', handlerTableKeysHandler)

    const headers = table.getHeaderGroups()[0].headers
    const header = headers[0]
    return (
        <div className="overflow-auto">
            <SmTable data-element="queue-table" compact className="table-fixed">
                <colgroup>
                    {headers.map(h => (
                        <col
                            key={h.id}
                            style={{
                                width: h.getSize() || 'auto',
                            }}
                        />
                    ))}
                </colgroup>
                <SmTable.Header className="relative">
                    <SmTable.Row>
                        {!noSelection && (
                            <SmTable.HeaderCell
                                style={{
                                    width: header.getSize() || 'auto',
                                }}
                            >
                                {flexRender(
                                    header.column.columnDef.header,
                                    header.getContext(),
                                )}
                            </SmTable.HeaderCell>
                        )}
                        <th colSpan={headers.length - 1}>
                            {HeaderContent}
                        </th>
                    </SmTable.Row>
                </SmTable.Header>

                <SmTable.Body>
                    {table.getRowModel().rows.map(row => (
                        <QueueItemRow
                            setActiveQueueItem={setActiveQueueItem}
                            isActive={
                                activeQueueItem?.queueId ===
                                row.original.queueId
                            }
                            key={row.id}
                            row={row}
                        />
                    ))}
                </SmTable.Body>
            </SmTable>
        </div>
    )
}
