import type {FC, ReactNode} from 'react'
import React, {useRef} from 'react'

import {Popup} from '@bpinc/lib-ui-elements'


interface CaseHeaderInfoProps {
    title: string
    info?: string
    tooltip?: ReactNode
}

export const CaseHeaderInfo: FC<CaseHeaderInfoProps> = ({title, info, tooltip}) => {
    const ref = useRef<HTMLDivElement | null>(null)
    if (!info) return null

    return (
        <div ref={ref}>
            {title}
            {': '}
            {info && tooltip ? (
                <Popup mountNode={ref.current} mode="hover" hoverable content={tooltip} contentClassName="z-[1]">
                    <span className="underline decoration-dashed">{info}</span>
                </Popup>
            ) : (
                info
            )}
        </div>
    )
}
