import {useMemo} from 'react'
import {useAtomValue} from 'jotai'

import {useDirectoryState} from '@bpinc/ad-directory-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {teamIdAtom} from '../atoms'


export function useTeamTabName() {
    const teamId = useAtomValue(teamIdAtom)
    const selfTeamId = useSessionState('teamId')
    const teams = useDirectoryState('teams')
    const {t} = useLocalizationState()

    return useMemo(() => {
        const team = teams.find(t => t.id === teamId)
        const teamName = team && team.id !== selfTeamId ? team.name : ''

        return teamName
            ? t.EMAIL.queuePostfix(teamName)
            : t.EMAIL.emailTeamQueue_header()
    }, [selfTeamId, t.EMAIL, teamId, teams])
}
