import {useAtomValue} from 'jotai'

import {useContactsState} from '@bpinc/ad-contacts-state-context'

import {mergingContactsAtom} from '../atoms'
import type {MergeContactData} from './types'
import {useMergeFormElements} from './useMergeFormElements'
import {useMergeFormFields} from './useMergeFormFields'
import {useMergeFormRows} from './useMergeFormRows'


export function useMergeFormData() {
    const formElements = useMergeFormElements()
    const mergingContacts = useAtomValue(mergingContactsAtom)
    const contactsData = useContactsState(s => mergingContacts.map(id => {
        const {contactDetailsLoading, details, data, isLoading} = s.contacts[id] || {}

        return {
            isLoading: (isLoading || contactDetailsLoading) ?? true,
            details,
            data: data || undefined,
        } satisfies MergeContactData
    }))

    const formRows = useMergeFormRows(formElements, contactsData)
    const mergeContactsData = useMergeFormFields(formRows, contactsData)

    return {
        formRows,
        contactsData,
        mergeContactsData,
    }
}
