import React from 'react'
import type {Table} from '@tanstack/react-table'

import type {CasesListItem} from '@bpinc/ad-case-state-types'
import {SmTable} from '@bpinc/lib-external-ui-elements'

import {CasesTableHeader} from './CasesTableHeader'
import {CasesTableRow} from './CasesTableRow'


type CasesTableProps = {
    table: Table<CasesListItem>
}

export const CasesTable = ({
    table,
}: CasesTableProps) => {
    const headers = table.getLeafHeaders()

    return (
        <div
            className="overflow-auto border-t"
            onKeyDown={e => {
                if (['Enter', ' '].includes(e.key)) {
                    e.preventDefault()
                }
            }}
        >
            <SmTable compact striped className="w-full table-fixed border-none">
                <colgroup>
                    {headers.map(h => (
                        <col
                            key={h.id}
                            data-id={h.id}
                            style={{
                                width: h.column.columnDef.size || 'auto',
                            }}
                        />
                    ))}
                </colgroup>
                <SmTable.Header className="sticky top-0 z-[1]">
                    <CasesTableHeader table={table} />
                </SmTable.Header>
                <SmTable.Body>
                    {table.getRowModel().rows.map(row => (
                        <CasesTableRow
                            key={row.id}
                            row={row}
                        />
                    ))}
                </SmTable.Body>
            </SmTable>
        </div>
    )
}
