import type {FC} from 'react'
import React from 'react'

import {CasePage} from '@bpinc/ad-case-ui'
import {OldEvaluationApp} from '@bpinc/ad-evaluation-ui'
import {InteractionWorkspacePage} from '@bpinc/ad-interaction-workspace-ui'
import {AppRoutes} from '@bpinc/ad-navigation-state-context'
import {NonCallCases} from '@bpinc/ad-non-call-cases-ui'
import {SearchPage} from '@bpinc/ad-searchpage-ui'
import type {Privileges} from '@bpinc/ad-session-types'
import {SmMessage} from '@bpinc/lib-external-ui-elements'
import {createComponentLoader, ErrorBoundary} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {MainAreaKB} from '../mainpage/components/KnowledgeBase/MainAreaKB'


const {ChatCenterPageLoader} = createComponentLoader('chatCenter', () => import('chatCenter'), 'ChatCenterPage')
const {SupervisionPageLoader} = createComponentLoader('supervision', () => import('supervision'), 'SupervisionPage')
const {CampaignsPageLoader} = createComponentLoader('campaigns', () => import('campaigns'), 'CampaignsPage')
const {CalendarPageLoader} = createComponentLoader('calendar', () => import('calendar'), 'CalendarPage')
const {InteractionSearchPageLoader} = createComponentLoader('interactionSearch', import('interactionSearch'), 'InteractionSearchPage')
const {HomePageLoader} = createComponentLoader('homePage', () => import('homePage'), 'HomePage')
const {EvaluationPageLoader} = createComponentLoader('omniQm', () => import('omniQm'), 'EvaluationPage')

const ErrorBoundaryFallback: FC = () => {
    const {t} = useLocalizationState()

    return <SmMessage error>{t.ERROR_MESSAGES.unhandled_router_error()}</SmMessage>
}

export type AppRoutesType = typeof AppRoutes[keyof typeof AppRoutes]
interface RouteConfig {
    path: AppRoutesType
    checkPrivileges?: (privileges: Privileges) => boolean
    Component: React.FC
    load?: () => Promise<Record<string, unknown>>
    allowSubRoutes?: boolean
}

type RoutesConfig = {
    [key in AppRoutesType]: RouteConfig
}

export const routesConfig: RoutesConfig = {
    [AppRoutes.interactionWorkspace]: {
        path: AppRoutes.interactionWorkspace,
        Component: withErrorBoundaryFallback(InteractionWorkspacePage),
    },
    [AppRoutes.calendar]: {
        path: AppRoutes.calendar,
        load: () => import('calendar'),
        checkPrivileges: privileges => !!privileges.CAN_USE_CALENDAR,
        Component: withErrorBoundaryFallback(CalendarPageLoader),
    },
    [AppRoutes.searchContact]: {
        path: AppRoutes.searchContact,
        Component: withErrorBoundaryFallback(SearchPage),
        allowSubRoutes: true,
    },
    [AppRoutes.searchCase]: {
        path: AppRoutes.searchCase,
        Component: withErrorBoundaryFallback(SearchPage),
        allowSubRoutes: true,
    },
    [AppRoutes.search]: {
        path: AppRoutes.search,
        Component: withErrorBoundaryFallback(SearchPage),
        allowSubRoutes: true,
    },
    [AppRoutes.cases]: {
        path: AppRoutes.cases,
        checkPrivileges: privileges => !!privileges.SYS_HANDLE_EMAILS,
        Component: withErrorBoundaryFallback(CasePage),
    },
    [AppRoutes.chat]: {
        path: AppRoutes.chat,
        load: () => import('chatCenter'),
        checkPrivileges: privileges => !!privileges.CAN_START_INTERNAL_CHAT,
        Component: withErrorBoundaryFallback(ChatCenterPageLoader),
    },
    [AppRoutes.interactionSearch]: {
        path: AppRoutes.interactionSearch,
        Component: withErrorBoundaryFallback(InteractionSearchPageLoader),
    },
    [AppRoutes.home]: {
        path: AppRoutes.home,
        Component: withErrorBoundaryFallback(HomePageLoader),
        allowSubRoutes: true,
    },
    [AppRoutes.supervision]: {
        load: () => import('supervision'),
        path: AppRoutes.supervision,
        checkPrivileges: privileges =>
            !!privileges.SYS_VIEW_LIST_OF_AGENTS ||
            !!privileges.SYS_VIEW_QUEUE_STATUS,
        Component: withErrorBoundaryFallback(SupervisionPageLoader),
    },
    [AppRoutes.nonCallCases]: {
        path: AppRoutes.nonCallCases,
        Component: withErrorBoundaryFallback(NonCallCases),
    },
    [AppRoutes.campaigns]: {
        path: AppRoutes.campaigns,
        checkPrivileges: privileges => !!privileges.CAN_OPERATE_CAMPAIGNS,
        Component: withErrorBoundaryFallback(CampaignsPageLoader),
    },
    // Save access to old app until new React app is ready
    [AppRoutes.oldOmniQm]: {
        path: AppRoutes.oldOmniQm,
        checkPrivileges: privileges =>
            !!privileges.EVALUATE_OWN_INTERACTIONS ||
            !!privileges.CONFIRM_EVALUATIONS_OF_SELF ||
            !!privileges.CONFIRM_EVALUATIONS_OF_SUPERVISED_AGENTS ||
            !!privileges.DELETE_EVALS_COMPLETED_BY_ANYONE ||
            !!privileges.DELETE_EVALS_COMPLETED_BY_THEMSELVES ||
            !!privileges.ASSIGN_EVALUATIONS_AND_CALIBRATIONS ||
            !!privileges.MANAGE_EVALUATIONS_ACCROSS_TEAMS ||
            !!privileges.SEE_EVALS_OF_SELF,
        Component: withErrorBoundaryFallback(OldEvaluationApp),
    },
    [AppRoutes.omniQm]: {
        path: AppRoutes.omniQm,
        checkPrivileges: privileges =>
            !!privileges.EVALUATE_OWN_INTERACTIONS ||
            !!privileges.CONFIRM_EVALUATIONS_OF_SELF ||
            !!privileges.CONFIRM_EVALUATIONS_OF_SUPERVISED_AGENTS ||
            !!privileges.DELETE_EVALS_COMPLETED_BY_ANYONE ||
            !!privileges.DELETE_EVALS_COMPLETED_BY_THEMSELVES ||
            !!privileges.ASSIGN_EVALUATIONS_AND_CALIBRATIONS ||
            !!privileges.MANAGE_EVALUATIONS_ACCROSS_TEAMS ||
            !!privileges.SEE_EVALS_OF_SELF,
        Component: withErrorBoundaryFallback(EvaluationPageLoader),
    },
    [AppRoutes.knowledgeBase]: {
        path: AppRoutes.knowledgeBase,
        Component: withErrorBoundaryFallback(MainAreaKB),
    },
}

function withErrorBoundaryFallback(Component: React.FC) {
    return () => (
        <ErrorBoundary fallback={ErrorBoundaryFallback}>
            <Component />
        </ErrorBoundary>
    )
}
