import React, {useCallback, useEffect, useMemo} from 'react'
import type {Table} from '@tanstack/react-table'
import {useAtomValue} from 'jotai'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useDirectoryState, useDirectoryStateMethods} from '@bpinc/ad-directory-state-context'
import type {QueueItem} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {IconForward} from '@bpinc/lib-icons'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {useSelectedRows} from '../../useSelectedRow'
import {AssignEmailModal} from '../AssignEmailModal'
import {teamActiveQueueItemAtom, teamIdAtom} from '../atoms'
import {useCreateNoteMutation} from '../mutations'
import {TeamSelector} from './TeamSelector'
import {useCloseAsSpamHandler} from './useCloseAsSpamHandler'
import {useDeleteCaseHandler} from './useDeleteCaseHandler'


export function useTeamControls(table: Table<QueueItem>) {
    const {t} = useLocalizationState()
    const selfTeamId = useSessionState('teamId')
    const teamId = useAtomValue(teamIdAtom) || selfTeamId

    const {teams, isTeamsLoading} = useDirectoryState([
        'teams',
        'isTeamsLoading',
    ])

    const {getAgentTeams} = useDirectoryStateMethods()

    //TODO: Find the better place for this hook
    useEffect(() => {
        if (!isTeamsLoading && !teams.length) {
            getAgentTeams()
        }
    }, [teams, isTeamsLoading, getAgentTeams])

    const currentTeamIsSupervised = useMemo(() => {
        return teams.find(team => team.id === teamId)?.supervised
    }, [teamId, teams])

    const selectedRows = useSelectedRows(teamActiveQueueItemAtom, table)
    const {moveQueueItems} = useCaseStateMethods()

    const {userId: selfUserId, privileges} = useSessionState()

    const handleGrab = useCallback(
        function handleGrabCallback() {
            assertDefined(selfUserId)
            moveQueueItems({
                directions: {
                    fromQueueType: 'TEAM',
                    toQueueType: 'PERSONAL',
                    toUserId: selfUserId,
                },
                itemsToMove: selectedRows,
            })
            table.resetRowSelection()
        },
        [moveQueueItems, selectedRows, selfUserId, table],
    )

    const handleDelete = useDeleteCaseHandler(selectedRows, table)
    const handleCloseAsSpam = useCloseAsSpamHandler(selectedRows, table)

    const {mutate: createNote} = useCreateNoteMutation()

    const openAssignEmailModal = useModalComponentOpener(AssignEmailModal)
    const handleAssignEmail = useCallback(() => {
        openAssignEmailModal({
            queueItemsToMove: selectedRows,
            createNote,
        })
    }, [createNote, openAssignEmailModal, selectedRows])

    return useMemo(
        function memoizeControls() {
            return (
                <div className="flex justify-between px-2 pb-2 pt-3">
                    <div className="flex">
                        {currentTeamIsSupervised && (
                            <SemanticButton
                                icon
                                basic
                                onClick={handleAssignEmail}
                                tooltip={t.EMAIL.email_assign_button_tooltip()}
                            >
                                <IconForward />
                            </SemanticButton>
                        )}
                        <SemanticButton basic primary onClick={handleGrab}>
                            {t.EMAIL.email_grab_button()}
                        </SemanticButton>
                    </div>
                    <div>
                        <SemanticButton basic color="orange" onClick={handleCloseAsSpam}>
                            {t.EMAIL.button_spam_text()}
                        </SemanticButton>
                        {privileges.SYS_DELETE_CASES && (
                            <SemanticButton
                                basic
                                color="red"
                                onClick={handleDelete}
                                icon="trash"
                                tooltip={t.EMAIL.button_trash_tooltip()}
                            />
                        )}
                        <TeamSelector teams={teams} />
                    </div>
                </div>
            )
        },
        [
            currentTeamIsSupervised,
            handleAssignEmail,
            handleCloseAsSpam,
            handleDelete,
            handleGrab,
            privileges.SYS_DELETE_CASES,
            t.EMAIL,
            teams,
        ],
    )
}
