import type {FC} from 'react'
import React from 'react'

import {AvatarLegacy} from '@bpinc/lib-avatar-ui'

import {useGetAgentAvatarUrl} from '../../../hooks/useGetAgentAvatarUrl'
import {convertTeamName} from '../utils/helpers'


interface AgentCellProps {
    agentName: string
    team: string | null
    agentLogin: string | null
}

export const AgentCell: FC<AgentCellProps> = React.memo(({agentName, team, agentLogin}) => {
    const getAvatarUrl = useGetAgentAvatarUrl()
    const avatarUrl = getAvatarUrl(agentLogin)

    return (
        <div className="flex h-14 items-center">
            <AvatarLegacy
                headPhones
                fullName={agentName}
                type="user"
                size={32}
                url={avatarUrl}
                className="ml-1.5 mr-4 mt-1 grow-0"
            />
            <div className="flex flex-col gap-y-1 truncate">
                <span className="truncate text-base font-bold leading-[17px] text-eval-text-dark">
                    {agentName}
                </span>
                {team && (
                    <div className="truncate text-sm leading-[17px] text-eval-text-light">
                        {convertTeamName(team)}
                    </div>
                )}
            </div>
        </div>
    )
})
