import React from 'react'

import {useContactDisplayName} from '@bpinc/ad-contacts-state-hooks'
import {useInteractionWorkspaceState} from '@bpinc/ad-main-page-ui-context'
import type {ScreenPopTab} from '@bpinc/ad-screen-pop-state-types'
import {IconClose} from '@bpinc/lib-icons'
import {SemanticButton} from '@bpinc/lib-ui-elements'


type TabTitleContentProps = {
    tab: ScreenPopTab
}

export const TabTitleContent: React.FC<TabTitleContentProps> = ({tab}) => {
    const {
        removeItemTab,
    } = useInteractionWorkspaceState()
    const contactId = tab.type === 'profile' ? tab.contactId : undefined
    const tabTitleText = useContactDisplayName(contactId) || tab.title

    return (
        <>
            <span>{tabTitleText}</span>
            {tab.type === 'iframe-html' || tab.type === 'iframe' && (
                <SemanticButton
                    transparent
                    className="ml-1 flex h-6 w-6 items-center justify-center opacity-70 transition-opacity duration-300 hover:opacity-100"
                    onClick={e => {
                        e.preventDefault()
                        e.stopPropagation()
                        removeItemTab(tab.id)
                    }}
                >
                    <IconClose className="h-3.5 w-3.5" />
                </SemanticButton>
            )}
        </>
    )
}
