import {useCallback, useEffect, useRef} from 'react'
import type {UseFormReturn} from 'react-hook-form'
import {isEqual} from 'lodash-es'

import {useDidUnmount} from '@bpinc/lib-helpers'

import {useSaveDraftMutation} from '../../mutations'
import type {EmailFormFields} from './types'


interface UseSaveDraftParams {
    emailId: string
    form: UseFormReturn<EmailFormFields>
}
/** Saves draft every 5 seconds and on component unmount */
export function useSaveDraftInterval({emailId, form}: UseSaveDraftParams) {
    const prevStateRef = useRef(form.getValues())

    const {mutate: saveDraft} = useSaveDraftMutation(emailId)

    const saveChangedDraft = useCallback(() => {
        const state = form.getValues()
        if (!isEqual(state, prevStateRef.current)) {
            //Save draft
            saveDraft(state)
            //Update previous state
            prevStateRef.current = state

        }
    }, [form, saveDraft])

    // Save draft every 5 seconds
    useEffect(() => {
        const interval = setInterval(() => {
            saveChangedDraft()
        }, 5_000)
        return () => clearInterval(interval)
    }, [saveChangedDraft])

    useDidUnmount(saveChangedDraft)
}
