import React, {useEffect, useMemo} from 'react'

import {useActivitiesState, useActivitiesStateMethods} from '@bpinc/ad-activities-state-context'
import type {
    ActivitiesListItemServiceType,
    ActivitiesListItemType,
    PartyType,
    PartyUserShortType,
} from '@bpinc/ad-activities-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {AvatarLegacy} from '@bpinc/lib-avatar-ui'
import {LegacyIconCallInbound, LegacyIconCallOutbound, LegacyIconChat} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {formatLongDate, getActivityOpener, getParties} from '../utils/helpers'
import {ActivityMessage} from './ActivityMessage'


interface Props {
    activity: ActivitiesListItemType
    services?: ActivitiesListItemServiceType[]
    agents: PartyUserShortType[] | undefined
}

export const Activity: React.FC<Props> = ({activity, services, agents}) => {
    const activities = useActivitiesState('activities')
    const {getActivity} = useActivitiesStateMethods()
    const {privileges, userId: selfUserId} = useSessionState()
    const getAvatarUrl = useGetAvatarUrl()
    const {formatFullName} = useFormatting()
    const {t} = useLocalizationState()
    const activityItemContentBlockClassName = tcn('mt-[5px] bg-[--c-activity-item-content] p-2 first:m-0')
    const activityItemClassName = tcn('flex flex-row')
    const activityItemContentHeadingClassName = tcn(
        'mb-2 flex flex-row leading-[16px] text-gray-500',
        '[&_svg]:mr-1 [&_svg]:inline-block [&_svg]:h-4 [&_svg]:w-4 [&_svg]:align-middle',
    )

    const id = activity.globalInteractionId

    const isSelf = agents?.some(agent => agent.userId === selfUserId)

    const privilegesAreReady = !!Object.values(privileges).length

    const hasSufficientPrivileges =
        privileges.CAN_SEE_ACTIVITIES ||
        privileges.LISTEN_ALL_RECORDINGS ||
        (isSelf && privileges.CAN_SEE_OWN_ACTIVITIES)

    useEffect(() => {
        if (privilegesAreReady && hasSufficientPrivileges && activity.mediaType === 'CHAT') {
            getActivity({id})
        }
    }, [activity, id, getActivity, privilegesAreReady, hasSufficientPrivileges])

    const {data, isLoading} = activities[id] || {data: [], isLoading: false}

    const parties = useMemo(() => {
        if (data) {
            let resultParties: { [key: string]: PartyType } = {}
            data.forEach(item => {
                resultParties = {
                    ...resultParties,
                    ...getParties(item.messages),
                }
            })
            return resultParties
        }
    }, [data])

    if (isLoading) return <div>{t.GLOBAL.loading()}</div>

    const renderAvatar = (party: Partial<PartyType>) => {
        const url = getAvatarUrl({
            userId: party.userId,
            contactId: party.contactId,
        })

        return (
            <div className="flex-none px-2">
                <AvatarLegacy
                    size={40}
                    url={url}
                    firstName={party.firstName}
                    lastName={party.lastName}
                    type={party.userId ? 'user' : 'contact'}
                />
            </div>
        )
    }

    const renderServices = (agent: PartyUserShortType) => {
        const serviceName = services?.find(service => service.serviceId === agent.serviceId)?.serviceName
        const disposition = agent.disposition

        if (!disposition && !agent.notes) {
            return
        }

        return (
            <div className={activityItemContentBlockClassName} key={agent.partyId}>
                <ActivityMessage className="p-0" agent={agent} text={agent.notes} />
                {serviceName && disposition && <div className="mt-2 text-gray-500">{`${serviceName}: ${disposition}`}</div>}
            </div>
        )
    }

    const renderVoiceContent = (agent: PartyUserShortType) => {
        if (!agent) return null

        const HeaderIcon =
            activity.direction === 'OUTBOUND'
                ? LegacyIconCallOutbound
                : LegacyIconCallInbound

        return (
            <div className={activityItemClassName}>
                {renderAvatar(agent)}
                <div className="flex-auto">
                    <div className={activityItemContentBlockClassName}>
                        <div className={activityItemContentHeadingClassName}>
                            <HeaderIcon />
                            {t.ACTIVITY_HISTORY.heading_user_started_call_on_date(
                                formatFullName(agent.firstName, agent.lastName),
                                'call',
                                formatLongDate(agent.startTime?.date),
                            )}
                        </div>
                    </div>
                    {agents?.map(renderServices)}
                </div>
            </div>
        )
    }

    const renderChatContent = () => {
        if (!data) return null

        return data.map((item, i) => {
            const opener = getActivityOpener(item)
            const openerName = opener?.firstName ?? '' + ' ' + opener?.lastName ?? ''
            const messages = item.messages.filter(message => message.event)

            return (
                <div key={i} className={activityItemClassName}>
                    {renderAvatar(opener)}
                    <div className="flex-auto">
                        <div className={activityItemContentBlockClassName}>
                            <div className={activityItemContentHeadingClassName}>
                                <LegacyIconChat />
                                {t.ACTIVITY_HISTORY.heading_user_started_call_on_date(
                                    openerName,
                                    'chat',
                                    formatLongDate(opener.timestamp),
                                )}
                            </div>
                            {messages.map((message, j) => (
                                <ActivityMessage key={j} message={message} parties={parties} />
                            ))}
                        </div>
                        {agents?.map(renderServices)}
                    </div>
                </div>
            )
        })
    }

    return (
        <div className="flex flex-auto flex-col">
            {activity.mediaType === 'VOICE' && agents?.map(renderVoiceContent)}
            {activity.mediaType === 'CHAT' && renderChatContent()}
        </div>
    )
}
