import React, {type FC, useCallback} from 'react'
import type {Table} from '@tanstack/react-table'
import {useAtomValue} from 'jotai'

import {useCaseState, useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {QueueItem} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {teamActiveQueueItemAtom} from '../atoms'
import {SetCaseStatusButton} from '../SetCaseStatusButton'
import {ViewButton} from '../ViewButton'
import {useCloseAsSpamHandler} from './useCloseAsSpamHandler'
import {useDeleteCaseHandler} from './useDeleteCaseHandler'


interface TeamCaseFooterContentProps {
    table?: Table<QueueItem>
    canViewCase?: boolean
}

export const TeamCaseFooterContent: FC<TeamCaseFooterContentProps> = ({table, canViewCase}) => {
    const {userId: selfUserId, privileges} = useSessionState([
        'userId',
        'privileges',
    ])
    const activeQueueItem = useAtomValue(teamActiveQueueItemAtom)
    const {caseId, queueId, queueType} = activeQueueItem ?? {}
    const {t} = useLocalizationState()
    const {openCase, moveQueueItems} = useCaseStateMethods()

    const handleOpen = useCallback(() => {
        assertDefined(queueId)
        assertDefined(caseId)

        openCase({
            caseId,
            queueId,
            queueType,
        })
    }, [queueId, queueType, caseId, openCase])

    const handleGrab = useCallback(() => {
        assertDefined(activeQueueItem)
        assertDefined(selfUserId)

        moveQueueItems({
            itemsToMove: [activeQueueItem],
            directions: {
                toQueueType: 'PERSONAL',
                fromQueueType: 'TEAM',
                toUserId: selfUserId,
            },
        })
    }, [activeQueueItem, moveQueueItems, selfUserId])

    assertDefined(activeQueueItem)
    const handleDelete = useDeleteCaseHandler([activeQueueItem], table)
    const handleCloseAsSpam = useCloseAsSpamHandler([activeQueueItem], table)
    const caseItem = useCaseState(s =>
        caseId ? s.cases[caseId]?.data : undefined,
    )
    const caseStatus = caseItem?.status
    const pendingReasonsId = caseItem?.pendingReasonId
    assertDefined(caseStatus)
    assertDefined(caseId)
    assertDefined(activeQueueItem.service)

    return (
        <div className="flex w-full flex-wrap justify-between gap-y-2">
            <div className="flex flex-wrap gap-y-2">
                {canViewCase && <ViewButton queueItem={activeQueueItem} />}
                <SemanticButton basic primary onClick={handleOpen}>
                    {t.EMAIL.email_open_button()}
                </SemanticButton>
                <SemanticButton
                    basic
                    primary
                    tooltip={t.EMAIL.email_grab_button_tooltip()}
                    onClick={handleGrab}
                >
                    {t.EMAIL.email_grab_button()}
                </SemanticButton>
                <SemanticButton
                    basic
                    color="orange"
                    onClick={handleCloseAsSpam}
                >
                    {t.EMAIL.button_spam_text()}
                </SemanticButton>
                {privileges.SYS_DELETE_CASES && (
                    <SemanticButton
                        basic
                        color="red"
                        onClick={handleDelete}
                        icon="trash"
                        tooltip={t.EMAIL.button_trash_tooltip()}
                    />
                )}
            </div>
            <div>
                <SetCaseStatusButton
                    key={caseId}
                    caseId={caseId}
                    initialPendingReasonId={pendingReasonsId}
                    service={activeQueueItem.service}
                    initialStatus={caseStatus}
                />
            </div>
        </div>
    )
}
