import type {FC} from 'react'
import React, {useCallback} from 'react'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useOnKeyPressHandler} from '@bpinc/lib-helpers'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface CaseFooterContentProps {
    caseId: string
}

export const CaseFooterContent: FC<CaseFooterContentProps> = ({caseId}) => {
    const {t} = useLocalizationState()

    const {openCase} = useCaseStateMethods()

    const handleCaseOpenClick = useCallback(() => {
        if (caseId) {
            openCase({caseId})
        }
    }, [caseId, openCase])

    const onKeyPressHandler = useOnKeyPressHandler(handleCaseOpenClick)

    return (
        <SemanticButton
            basic
            primary
            onClick={handleCaseOpenClick}
            onKeyDown={onKeyPressHandler}
        >
            {t.EMAIL.email_open_button()}
        </SemanticButton>
    )

}
