import type {ReactElement} from 'react'
import React, {useEffect, useState} from 'react'
import {useAtom, useSetAtom} from 'jotai'

import {
    MAX_COMMUNICATOR_WIDTH_IN_PERCENT,
    MIN_COMMUNICATOR_WIDTH,
    useCommunicatorUIState,
} from '@bpinc/ad-main-page-ui-context'
import {AppRoutes, useNavigationState} from '@bpinc/ad-navigation-state-context'
import {useResizeObserver} from '@bpinc/lib-helpers'
import {type FCWithChildren} from '@bpinc/lib-types-helpers'
import {parallelItemsAtom, parallelItemsHaveEnoughSpaceAtom} from '@bpinc/state-interaction-ui'


const MAX_COMM_WIDTH = Number(`0.${MAX_COMMUNICATOR_WIDTH_IN_PERCENT}`)

export const MainPageResizeController: FCWithChildren<ReactElement> =
    React.memo(props => {
        const [element, setElement] = useState<HTMLElement | null>(null)
        const elementObserver = useResizeObserver(element)
        const getLocation = useNavigationState('getLocation')
        const [parallelItems, parallelItemsDispatch] = useAtom(parallelItemsAtom)
        const containerWidth = elementObserver?.contentRect.width
        const setParallelInteractionsWidth = useCommunicatorUIState(
            s => s.setParallelInteractionsWidth,
        )
        const setHasEnoughSpaceForParallelItems = useSetAtom(
            parallelItemsHaveEnoughSpaceAtom,
        )

        useEffect(function detectIfExtraParallelInteractionFits() {
            if (!containerWidth) {
                return
            }

            const extraInteractionFits =
                containerWidth * MAX_COMM_WIDTH >
                (parallelItems.filter(i => i.activeItemId).length + 2) *
                    MIN_COMMUNICATOR_WIDTH

            setHasEnoughSpaceForParallelItems(extraInteractionFits)
        }, [
            containerWidth,
            getLocation,
            parallelItems,
            parallelItems.length,
            setHasEnoughSpaceForParallelItems,
        ])

        useEffect(function removeParallelInteractionIfDoesntFit() {
            const showParallelItems =
                getLocation() === AppRoutes.interactionWorkspace &&
                parallelItems.length
            if (!showParallelItems || !containerWidth) {
                return
            }

            const parallelInteractionsDontFit =
                containerWidth * MAX_COMM_WIDTH <
                (parallelItems.length + 1) * MIN_COMMUNICATOR_WIDTH

            if (parallelInteractionsDontFit) {
                const index = parallelItems.length - 1
                parallelItemsDispatch({type: 'remove', payload: {index}})
                setParallelInteractionsWidth(w => w - MIN_COMMUNICATOR_WIDTH)
            }
        }, [
            containerWidth,
            getLocation,
            parallelItems,
            parallelItems.length,
            parallelItemsDispatch,
            setParallelInteractionsWidth,
        ])

        return (
            <div
                ref={setElement}
                className="relative flex h-full flex-auto justify-start overflow-hidden"
                data-testid="main-page"
            >
                {props.children}
            </div>
        )
    })
