import {formatEmail} from '@bpinc/ad-formatting-helpers'
import type {Email, EmailAddress, EmailAttachment, EmailRequest} from '@bpinc/ad-request-urls'
import type {Attachment} from '@bpinc/lib-attachments-state'
import {formatHTMLAsMultiLineString} from '@bpinc/lib-html-utils'

import type {EmailFormFields, EmailSearchItem} from './types'


export function emailAddressToEmailSearchItem(email: EmailAddress): EmailSearchItem {
    return {
        id: email.address,
        displayText: formatEmail(email),
        address: email.address,
        name: email.name,
    }
}


function emailSearchItemToEmailAddress(item: EmailSearchItem): EmailAddress {
    return {
        address: item.address,
        name: item.name,
    }
}


export function emailFormFieldsToEmailRequest(emailId: string, fields: EmailFormFields): EmailRequest {
    return {
        emailId,
        html: fields.html,
        text: formatHTMLAsMultiLineString(fields.html),
        subject: fields.subject,
        attachments: fields.attachments,
        to: fields.to.map(emailSearchItemToEmailAddress),
        cc: fields.cc.map(emailSearchItemToEmailAddress),
        bcc: fields.bcc.map(emailSearchItemToEmailAddress),
        //TODO: Understand what should be there:
        // articleId: undefined,
        //It seems like current user id if there was a template used for the email
        // headerFooterUserId: undefined,
        // originalMessageInserted: false,
    }
}

export function emailFormFieldsToEmail(fields: EmailFormFields, attachments: Attachment<EmailAttachment>[]): Partial<Email> {
    return {
        content: fields.html,
        subject: fields.subject,
        attachments: attachments.map(({id, file}) => ({
            id,
            name: file.name,
            size: file.size,
        })),
        toAddresses: fields.to.map(emailSearchItemToEmailAddress),
        ccAddresses: fields.cc.map(emailSearchItemToEmailAddress),
        bccAddresses: fields.bcc.map(emailSearchItemToEmailAddress),
    }
}
