import React from 'react'
import {useAtom} from 'jotai'
import {flow, property} from 'lodash-es'

import {ResizePanel} from '@bpinc/lib-ui-elements'

import {selectedCaseTabWidthAtom} from '../atoms'
import {CasesList} from './CasesList'
import {CaseView} from './CaseView'


export const CasesLayout: React.FC = () => {
    const [casesListWidth, setCasesListWidth] = useAtom(selectedCaseTabWidthAtom)

    return (
        <section className="flex h-full items-start overflow-hidden">
            <ResizePanel
                className="h-full"
                minSize={400}
                maxSizeInPercent={70}
                initialSize={casesListWidth}
                onDragEnd={flow(property('size'), setCasesListWidth)}
                direction="right"
                containerClass="h-full"
            >
                <aside className="flex h-full min-w-0 flex-auto flex-col border-r border-gray-200">
                    <CasesList />
                </aside>
            </ResizePanel>
            <article className="relative flex h-full w-full flex-1 flex-col overflow-hidden">
                <CaseView />
            </article>
        </section>
    )
}
