import React, {useEffect, useState} from 'react'
import type {RowSelectionState, VisibilityState} from '@tanstack/react-table'
import {getCoreRowModel, useReactTable} from '@tanstack/react-table'
import {useAtomValue, useSetAtom} from 'jotai'

import {useCaseState, useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {Loader} from '@bpinc/lib-ui-elements'

import {
    casesServiceAtom,
    casesSortingAtom,
    casesStatusesAtom,
    casesTeamIdAtom,
    searchQueryAtom,
    selectedCaseIdAtom,
} from '../atoms'
import {CasesListHeader} from './CasesListHeader'
import {CasesTable} from './CasesTable'
import {DEFAULT_VISIBLE_COLUMNS} from './constants'
import {useCasesTableColumns} from './useCasesTableColumns'


export const CasesList: React.FC = () => {
    const query = useAtomValue(searchQueryAtom)
    const setSelectedCaseId = useSetAtom(selectedCaseIdAtom)
    const statuses = useAtomValue(casesStatusesAtom)
    const sorting = useAtomValue(casesSortingAtom)
    const service = useAtomValue(casesServiceAtom)
    const teamId = useAtomValue(casesTeamIdAtom)
    const {casesList, casesListLoading} = useCaseState([
        'casesList',
        'casesListLoading',
    ])
    const {getCasesList} = useCaseStateMethods()

    const [columnVisibility, setColumnVisibility] = useState<VisibilityState>(DEFAULT_VISIBLE_COLUMNS)
    const [rowSelection, setRowSelection] = useState<RowSelectionState>({})

    const table = useReactTable({
        columns: useCasesTableColumns(),
        data: casesList,
        getCoreRowModel: getCoreRowModel(),
        getRowId: row => row.id,
        state: {
            rowSelection,
            columnVisibility,
        },
        onRowSelectionChange: setRowSelection,
        onColumnVisibilityChange: setColumnVisibility,
    })

    useEffect(() => {
        const timeout = setTimeout(() => {
            getCasesList({
                query,
                statuses,
                service,
                teamId,
                sortBy: sorting.sortBy,
                asc: sorting.asc,
            })
        }, 500)

        return () => {
            timeout && clearTimeout(timeout)
        }
    }, [query, statuses, getCasesList, sorting, service, teamId])

    useEffect(function selectFirstCase() {
        setSelectedCaseId(casesList[0]?.id)
    }, [casesList, setSelectedCaseId])

    return (
        <div className="relative flex flex-1 flex-col  overflow-hidden">
            <CasesListHeader table={table} />
            <CasesTable table={table} />
            <Loader active={casesListLoading} color="primary" size="huge" />
        </div>
    )
}
