import type {FC, ReactNode} from 'react'
import React from 'react'
import classNames from 'classnames'


export const ProfileHeaderWrapper: FC<{
    children: ReactNode
}> = ({
    children,
}) => {
    return (
        <div className="flex w-full flex-row justify-between border-b border-gray-300 p-2.5">
            {children}
        </div>
    )
}

export const ProfileHeaderColumn: FC<{
    children?: ReactNode
    type?: 'avatar' | 'controls'
}> = ({
    children,
    type,
}) => {
    return (
        <div
            className={classNames('flex-auto', {
                ['flex-none pr-2.5']: type === 'avatar',
                ['relative flex justify-end items-end whitespace-nowrap min-w-[200px] flex-none']: type === 'controls',
            })}
        >
            {children}
        </div>
    )
}
