import React, {type FC, useEffect, useMemo} from 'react'
import {useAtom} from 'jotai'

import {useDirectoryState, useDirectoryStateMethods} from '@bpinc/ad-directory-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {Dropdown} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {casesTeamIdAtom} from '../atoms'


const ALL_TEAMS_ID = 'all_teams'

export const TeamSelector: FC = () => {
    const {t} = useLocalizationState()
    const selfTeamId = useSessionState('teamId')
    const [teamId, setTeamId] = useAtom(casesTeamIdAtom)

    const {teams, isTeamsLoading} = useDirectoryState([
        'teams',
        'isTeamsLoading',
    ])

    const {getAgentTeams} = useDirectoryStateMethods()

    useEffect(() => {
        if (!isTeamsLoading && !teams.length) {
            getAgentTeams()
        }
    }, [teams, isTeamsLoading, getAgentTeams])

    const options = useMemo(() => {
        return [
            {text: t.GLOBAL.all_teams(), value: ALL_TEAMS_ID},
            ...teams.filter(team => team.supervised || team.id === selfTeamId).map(team => ({
                text: team.name,
                value: team.id,
            })),
        ]
    }, [selfTeamId, t.GLOBAL, teams])

    if (options.length < 2 || isTeamsLoading) {
        return null
    }

    return (
        <Dropdown
            className="ml-2"
            options={options}
            onChange={(_evt, data) => {
                if (typeof data.value === 'string') {
                    setTeamId(data.value === ALL_TEAMS_ID ? undefined : data.value)
                }
            }}
            value={teamId || ALL_TEAMS_ID}
            inline
        />
    )
}
