import React from 'react'

import type {InteractionMetadata, Transcript as TranscriptType} from '@bpinc/ad-evaluation-state-types'
import {MS_IN_SECOND} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {Intents} from './Intents'
import type {CommonMessageProps} from './Message'
import {AgentMessage, ClientMessage, SuggestionMessage, SystemMessage} from './Message'


const SYSTEM_USERID = 'system'
const SYSTEM_USER = 'Chat System Msg'
const SUGGESTION_USERID = 'suggestion'

type TranscriptProps = {
    meta: InteractionMetadata
    transcripts: TranscriptType[]
    currentTimeline: number
    duration: number
}

export const Transcript: React.FC<TranscriptProps> = ({
    meta,
    transcripts,
    currentTimeline,
    duration,
}) => {
    const {t: {EVALUATION_PAGE: messages}} = useLocalizationState()
    const {
        selectedParty: {
            intents,
        },
    } = meta

    return (
        <div className="scrollbar-on-hover flex flex-1 flex-col gap-7 overflow-y-auto">
            <div className="bg-white pb-4 pt-2">
                {intents && <Intents intents={intents} />}
                <div className="flex flex-col">
                    {transcripts.map((transcript, idx) => {
                        // Until new requirements are provided
                        if (isNotAllowedType(transcript)) {
                            return null
                        }

                        const user = transcript.user
                        const prevTranscript = transcripts[idx - 1]
                        const nextTranscript = transcripts[idx + 1]
                        const timeStart = Math.floor(transcript.timeOffset / MS_IN_SECOND)
                        const timeEnd = nextTranscript ? Math.floor(nextTranscript.timeOffset / MS_IN_SECOND) : duration
                        const active = timeStart <= currentTimeline && currentTimeline < timeEnd

                        const props: Omit<CommonMessageProps, 'user'> = {
                            transcript,
                            active,
                            timeStart,
                            showUser: prevTranscript ? !isSameSender(prevTranscript, transcript) : true,
                        }

                        if (isSystemMessage(transcript)) {
                            return <SystemMessage key={idx} user={messages.system()} {...props} />
                        }

                        if (isSuggestionMessage(transcript)) {
                            return <SuggestionMessage key={idx} user={messages.suggestion()} {...props} />
                        }

                        if (isClientMessage(transcript)) {
                            return <ClientMessage key={idx} user={user || messages.client()} {...props} />
                        }

                        return <AgentMessage key={idx} user={user || messages.agent()} {...props} />
                    })}
                </div>
            </div>
        </div>
    )
}

function isNotAllowedType(t: TranscriptType) {
    return t.type && t.type !== 'EMAIL'
}

function isSystemMessage(t: TranscriptType) {
    return t.userId === SYSTEM_USERID || t.user === SYSTEM_USER
}

function isSuggestionMessage(t: TranscriptType) {
    return t.userId === SUGGESTION_USERID
}

function isClientMessage(t: TranscriptType) {
    return !t.agent
}

function isSameSender(t0: TranscriptType, t1: TranscriptType) {
    if (isSystemMessage(t0) || isSystemMessage(t1)) {
        return isSystemMessage(t0) && isSystemMessage(t1)
    }
    if (isSuggestionMessage(t0) || isSuggestionMessage(t1)) {
        return isSuggestionMessage(t0) && isSuggestionMessage(t1)
    }
    if (isClientMessage(t0) || isClientMessage(t1)) {
        return isClientMessage(t0) && isClientMessage(t1)
    }

    return t0.userId === t1.userId && t0.user === t1.user
}
