import React from 'react'
import {Route, Routes} from 'react-router-dom'

import {AppRoutes} from '@bpinc/ad-navigation-state-context'
import {createComponentLoader} from '@bpinc/lib-ui-elements'

import {RightBarKB} from '../KnowledgeBase/RightBarKB'


const hideKBOnRoutes = [
    AppRoutes.campaigns,
    AppRoutes.knowledgeBase,
    AppRoutes.oldOmniQm,
    AppRoutes.omniQm,
    AppRoutes.supervision,
]

const {RightBarLoader} = createComponentLoader('supervision', () => import('supervision'), 'RightBar')

export const RightBar: React.FC = () => {
    return (
        <Routes>
            <Route
                path={AppRoutes.supervision}
                Component={RightBarLoader}
            />
            {hideKBOnRoutes.map(path => (
                <Route
                    key={path}
                    path={path}
                    Component={null}
                />
            ))}
            <Route
                path="*"
                Component={RightBarKB}
            />
        </Routes>
    )
}
