import {useEffect} from 'react'
import type {UseFormReturn} from 'react-hook-form'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {ActiveDraft} from '@bpinc/ad-interaction-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import type {EmailReplyTemplateResult} from '@bpinc/ad-transport-types'
import {stringToHTML, useEvent} from '@bpinc/lib-helpers'

import {useSaveDraftMutation} from '../../mutations'
import {useGetEmailQuery} from '../../queries/useGetEmailQuery'
import {HTML_FOOTER_CLASS, HTML_HEADER_CLASS} from './constants'
import type {EmailFormFields} from './types'


interface UseEmailTemplateFetcherParams {
    draft: ActiveDraft
    form: UseFormReturn<EmailFormFields>
}


export function useEmailTemplateFetcher({draft, form}: UseEmailTemplateFetcherParams) {
    const selfUserId = useSessionState('userId')
    const {data: email, isFetched} = useGetEmailQuery(draft.emailId)
    const {data: refEmail} = useGetEmailQuery(draft.referencedEmailId ?? '')
    const language = refEmail?.langTag ?? undefined

    const {getEmailTemplate} = useCaseStateMethods()
    const {mutate: saveDraft} = useSaveDraftMutation(draft.emailId)
    const saveDraftHandler = useEvent(saveDraft)
    const headerFooterUserId = email?.headerFooterUserId ?? undefined

    useEffect(() => {
        fetchEmailTemplate()

        async function fetchEmailTemplate() {
            if (isFetched && (!headerFooterUserId || headerFooterUserId !== selfUserId)) {
                const emailTemplate = await getEmailTemplate(language)

                //Save headerFooterUserId as selfUserId
                form.setValue('headerFooterUserId', selfUserId)

                if (!emailTemplate) {
                    return
                }

                if (!headerFooterUserId) {
                    insertHeaderFooter(emailTemplate)
                } else {
                    replaceExistingHeaderFooter(emailTemplate)
                }

                //Save draft
                saveDraftHandler(form.getValues())

                const emailContentNode = findEmailContentNode(draft.emailId)
                //TODO: Add focus (with MutationObserver in the EmailForm.tsx?)
                emailContentNode?.scrollIntoView()
            }

            function insertHeaderFooter(emailTemplate: EmailReplyTemplateResult) {
                form.setValue('html', emailTemplate.header + `<div>${form.getValues('html')}</div>` + emailTemplate.footer)
            }

            function replaceExistingHeaderFooter(emailTemplate: EmailReplyTemplateResult) {
                const htmlString = form.getValues('html')
                const html = stringToHTML(htmlString)

                if (emailTemplate.header) {
                    const oldHeader = html.querySelector(HTML_HEADER_CLASS)
                    oldHeader?.parentNode?.replaceChild(stringToHTML(emailTemplate.header), oldHeader)
                }

                if (emailTemplate.footer) {
                    const oldFooter = html.querySelector(HTML_FOOTER_CLASS)
                    oldFooter?.parentNode?.replaceChild(stringToHTML(emailTemplate.footer), oldFooter)
                }

                form.setValue('html', html.innerHTML)
            }

            function findEmailContentNode(emailId: string) {
                const input = document.querySelector(`[data-email-editor-id="${emailId}"]>div`)
                const node = input?.querySelector(HTML_HEADER_CLASS)?.nextSibling

                if (node instanceof HTMLElement) {
                    return node
                }
            }
        }
    }, [draft.emailId, isFetched, headerFooterUserId, language, selfUserId, getEmailTemplate, form, saveDraftHandler])

}
