import type {QueueType} from '@bpinc/ad-request-urls'


export const queryKeys = {
    queue: (queueType?: QueueType, params?: unknown) => ['queue', queueType, params].filter(Boolean),
    queueCount: (params?: unknown) => ['queueCount', params].filter(Boolean),
    queueCountByService: () => ['queueCountByService'],
    getEmail: (emailId: string) => ['getEmail', emailId],
    searchEmails: (search: string) => ['searchEmails', search],
}
