import {useMemo} from 'react'
import type {UseFormHandleSubmit} from 'react-hook-form/dist/types/form'
import {useAtomValue} from 'jotai'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactDetailsType, ContactDetailsUnflatten} from '@bpinc/ad-contacts-state-types'
import {flatten, unflatten} from '@bpinc/lib-flat'
import {truthy} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'

import {mergingContactsAtom} from '../atoms'
import {useMergeContactsActions} from '../useMergeContactsActions'
import type {ContactDetailsUnflattenContact, MergeContactFormType} from './types'


type UseOnSubmitParams = {
    handleSubmit: UseFormHandleSubmit<MergeContactFormType>
}

export function useOnSubmit({
    handleSubmit,
}: UseOnSubmitParams) {
    const {t} = useLocalizationState()
    const mergingContacts = useAtomValue(mergingContactsAtom)
    const {closeContactsMerge} = useMergeContactsActions()
    const {mergeContacts, getContactDetails} = useContactsStateMethods()
    const {openModal} = useModalsStateMethods()

    return useMemo(
        function memoizeSubmit() {
            return handleSubmit(async function onSubmit(data) {
                const continueSubmit = await new Promise(resolve => {
                    openModal({
                        title: t.GLOBAL.confirm(),
                        body: t.MERGE_FORM.confirm_merge(),
                        actions: {
                            ok: {
                                title: t.GLOBAL.continue(),
                                action: () => resolve(true),
                            },
                            cancel: {
                                title: t.GLOBAL.cancel(),
                                action: () => resolve(false),
                            },
                        },
                        afterClose: () => resolve(false),
                    })
                })

                if (!continueSubmit) {
                    return
                }

                const {id: targetContactId, ...mergedContactData} = data
                const currentDetails = await getContactDetails({id: targetContactId})
                if (!currentDetails) {
                    return
                }
                const details = getUnfalttenContactDetails()

                await mergeContacts({
                    id: targetContactId,
                    mergedDetails: details,
                    removeIds: mergingContacts.filter(id => id !== targetContactId),
                })

                closeContactsMerge()

                function getUnfalttenContactDetails() {
                    const {
                        Contact: {emails, addresses, phones, messengers, ...restContactData} = {},
                        ...restData
                    } = mergedContactData

                    const contactDetailsUnflatten = {
                        ...restData,
                        Contact: {
                            ...restContactData,
                            phones: truthy(phones || []).map(p => JSON.parse(p)) as ContactDetailsUnflattenContact['phones'],
                            addresses: truthy(addresses || []).map(p => JSON.parse(p)) as ContactDetailsUnflattenContact['addresses'],
                            emails: truthy(emails || []).map(p => JSON.parse(p)) as ContactDetailsUnflattenContact['emails'],
                            messengers: truthy(messengers || []).map(p => JSON.parse(p)) as ContactDetailsUnflattenContact['messengers'],
                        },
                    } satisfies ContactDetailsUnflatten
                    const flattenContactDetails = flatten<ContactDetailsUnflatten, ContactDetailsType>(contactDetailsUnflatten, {maxDepth: 2})

                    return unflatten<ContactDetailsType, ContactDetailsUnflatten>({...currentDetails, ...flattenContactDetails})
                }
            })
        },
        [closeContactsMerge, getContactDetails, handleSubmit, mergeContacts, mergingContacts, openModal, t.GLOBAL, t.MERGE_FORM],
    )
}
