import React, {type FC} from 'react'

import {useActiveInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getMediaIcon} from '@bpinc/ad-interaction-state-methods'
import {DispositionButton} from '@bpinc/lib-interaction-buttons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {InteractionMediaIcon} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {interactionBarStyles} from '../styles'
import {ContactInfo} from './ContactInfo'


export const WrapUpContent: FC = () => {
    const t = useLocalizationState('t')
    const iconType = useActiveInteractionProp(getMediaIcon)
    return (
        <div className={tcn(interactionBarStyles, 'pl-4')}>
            <InteractionMediaIcon icon={iconType} />
            <p className="ml-1 font-semibold caption-s text-neutral-fade">{t.DISPOSITION.ended()}</p>
            <DispositionButton className="mr-4" />
            <ContactInfo />
        </div>
    )
}
