import React, {useMemo} from 'react'

import type {ActivityCase as ActivityCaseType} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {LegacyIconKey} from '@bpinc/lib-icons'
import {Avatar} from '@bpinc/lib-ui-elements'

import {useActivityCaseTitle} from './useActivityCaseTitle'


interface Props {
    activity: ActivityCaseType
    serviceId?: string
}

export const ActivityCase: React.FC<Props> = ({activity, serviceId}) => {
    const getAvatarUrl = useGetAvatarUrl()
    const user = activity.parties?.find(p => p.partyType === 'USER')
    const {userId, firstName = '', lastName = ''} = user || {}

    const title = useActivityCaseTitle({activity, serviceId})

    const avatarOptions = useMemo(
        () => ({userId, notFoundPlaceholder: true}),
        [userId],
    )

    return (
        <div className="relative flex p-2">
            <div className="mr-2 flex-none">
                <Avatar
                    size={40}
                    firstName={firstName}
                    lastName={lastName}
                    url={getAvatarUrl(avatarOptions)}
                />
            </div>
            <div className="flex-auto bg-[--c-activity-bg] p-2">
                <div className="flex">
                    <div className="mr-2 h-4 w-4 flex-none self-center [&_svg]:h-full [&_svg]:w-full">
                        <LegacyIconKey fill="var(--c-icon)" />
                    </div>
                    <div className="mr-2 text-gray-500">{title}</div>
                </div>
            </div>
        </div>
    )
}
