import type {ReactNode} from 'react'
import React, {useEffect} from 'react'

import {useCaseState, useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {ActiveDraft} from '@bpinc/ad-interaction-state-types'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Loader} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {EmailForm} from './EmailForm/EmailForm'
import {CaseContent} from './CaseContent'
import {CaseFooter} from './CaseFooter'
import {CaseHeader} from './CaseHeader'


export interface CaseProps {
    id: string | undefined
    FooterContent?: ReactNode
    className?: string
    loaderClassName?: string
    activeDrafts?: ActiveDraft[]
    interactionId?: string
    /**serviceId is passed from the queueItem in the Queues and from the interaction in the Email.
     * Check (find) CasePresenterGeneratorData.builder() code in the old AD for the refference */
    serviceId?: string
}

export const Case: React.FC<CaseProps> = React.memo(function Case({
    id,
    className,
    loaderClassName,
    FooterContent,
    activeDrafts,
    interactionId,
    serviceId,
}) {
    const {t} = useLocalizationState()
    const cases = useCaseState('cases')
    const {getCase} = useCaseStateMethods()

    const caseItem = id ? cases[id] : undefined

    useEffect(() => {
        if (id) {
            getCase(id)
        }
        // do not track item change
        // eslint-disable-next-line
    }, [id, getCase]);

    if (!caseItem?.data) {
        return (
            <Loader
                color="primary"
                size="big"
                className={loaderClassName}
                active
            />
        )
    }

    return (
        <div className={tcn('flex h-full flex-col', className)}>
            <CaseHeader
                data={caseItem.data}
                serviceId={serviceId}
                interactionMode={!!interactionId}
                assignments={caseItem.assignments}
            />
            <div className="grow overflow-auto">
                <CaseContent
                    serviceId={serviceId}
                    data={caseItem.data}
                    recordings={caseItem.recordings}
                />
                {interactionId &&
                    activeDrafts?.map(draft => (
                        <EmailForm
                            key={draft.emailId}
                            {...draft}
                            interactionId={interactionId}
                        />
                    ))}
            </div>
            <CaseFooter>{FooterContent}</CaseFooter>
        </div>
    )
})
