import React, {useCallback, useRef} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'
import {useTimezonesState} from '@bpinc/ad-timezones-state-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const OldEvaluationApp = () => {
    const {language} = useLocalizationState()
    const {sessionId, userId} = useSessionState()
    const {localTimezone} = useTimezonesState()
    const iframeRef = useRef<HTMLIFrameElement | null>(null)
    const loadIframe = useCallback(async () => {
        if (iframeRef.current) {
            // its very bad hack,
            // needs to be replaced with backend method or smth
            const resultHTML = await (await fetch(`/admin/EvaluationHome.jsp?token=${sessionId}`)).text()
            const regex = /initializer\.init\([^,]+,[^,]+,\s*(\{.*\})\s*,/

            const match = resultHTML.match(regex)
            let resultPermissions
            if (match && match[1]) {
                try {
                    resultPermissions = JSON.parse(match[1]) //
                } catch {
                    // nothing
                }
            }

            iframeRef.current.contentWindow?.postMessage({
                token: sessionId, type: 'init-evaluation', loginId: userId, timezone: localTimezone, locale: language, permissions: resultPermissions || {}},
            document.location.origin)
        }

    }, [iframeRef, language, sessionId, userId, localTimezone])

    return (
        <div style={{height: '100%'}}>
            <iframe
                id="containerIframe"
                ref={iframeRef}
                src="/agent/evaluation-page/main.html"
                onLoad={loadIframe}
                width={'100%'}
                height={'100%'}
            />
        </div>
    )
}
