import produce from 'immer'

import type {MediaType} from '@bpinc/ad-interaction-state-types'


export interface PageTitleState {
    pageIsVisible: boolean
    events: Event[]
}

interface Event {
    type: 'interaction' | 'chatMessage' | 'calendar'
    title?: string
    id: string
}

export type Action =
    | InteractionAppearedAction
    | InteractionDisappearedAction
    | CalendarEventAppearedAction
    | CalendarEventDisappearedAction
    | ChatMessageAppearedAction
    | PageVisibilityChangedAction
    | ResetStateAction

interface InteractionAppearedAction {
    type: 'interactionAppeared'
    mediaType: MediaType | undefined
    id: string
}

interface InteractionDisappearedAction {
    type: 'interactionDisappeared'
    id: string
}

interface CalendarEventDisappearedAction {
    type: 'calendarEventDisappeared'
    id: string
}

interface CalendarEventAppearedAction {
    type: 'calendarEventAppeared'
    id: string
}

interface ChatMessageAppearedAction {
    type: 'chatMessageAppeared'
    id: string
}

interface PageVisibilityChangedAction {
    type: 'pageVisibilityChanged'
    pageIsVisible: boolean
}

interface ResetStateAction {
    type: 'resetState'
}

export const initialState = {
    events: [],
    pageIsVisible: true,
}

export const reducer = produce((state: PageTitleState, action: Action) => {
    if (isPageVisibilityChanged(action)) {
        state.pageIsVisible = action.pageIsVisible
        state.events = initialState.events
    }

    if (!state.pageIsVisible) {
        if (isInteractionAppeared(action)) {
            state.events.push({
                type: 'interaction',
                title: getIncomingTitle(action.mediaType),
                id: action.id,
            })
        }
        if (isNewInteractionDisappeared(action)) {
            const eventIndex = state.events.findIndex(e => e.id === action.id)
            state.events.splice(eventIndex, 1)
        }

        if (isCalendarEventAppeared(action)) {
            state.events.push({
                type: 'calendar',
                title: getIncomingTitle('calendar'),
                id: action.id,
            })
        }

        if (isCalendarEventDisappeared(action)) {
            const eventIndex = state.events.findIndex(e => e.id === action.id)
            state.events.splice(eventIndex, 1)
        }

        if (isChatMessageAppeared(action)) {
            state.events.push({
                type: 'chatMessage',
                title: getIncomingTitle('chatMessage'),
                id: action.id,
            })
        }
    }
})

function isInteractionAppeared(action: Action): action is InteractionAppearedAction {
    return action.type === 'interactionAppeared'
}

function isNewInteractionDisappeared(action: Action): action is InteractionDisappearedAction {
    return action.type === 'interactionDisappeared'
}

function isCalendarEventAppeared(action: Action): action is CalendarEventAppearedAction {
    return action.type === 'calendarEventAppeared'
}

function isCalendarEventDisappeared(action: Action): action is CalendarEventDisappearedAction {
    return action.type === 'calendarEventDisappeared'
}

function isChatMessageAppeared(action: Action): action is ChatMessageAppearedAction {
    return action.type === 'chatMessageAppeared'
}

function isPageVisibilityChanged(action: Action): action is PageVisibilityChangedAction {
    return action.type === 'pageVisibilityChanged'
}

function isResetState(action: Action): action is ResetStateAction {
    return action.type === 'resetState'
}

function getIncomingTitle(type?: MediaType | 'calendar' | 'chatMessage') {
    switch (type) {
        case 'chatMessage':
            return 'GLOBAL.heading_incomingMessage'
        case 'workitem':
            return 'GLOBAL.heading_incomingPreview'
        case 'voice':
            return 'GLOBAL.heading_incomingCall'
        case 'forum':
            return 'GLOBAL.heading_incomingForum'
        case 'chat':
            return 'GLOBAL.heading_incomingChat'
        case 'email':
            return 'GLOBAL.heading_incomingEmail'
        case 'calendar':
            return 'GLOBAL.heading_calendarAlert'
    }
}
