import type {ActivityType, PartyContactShortType, PartyType, PartyUserShortType} from '@bpinc/ad-activities-state-types'


type PartyShortType = PartyUserShortType | PartyContactShortType
export const formatDate = (timestamp: string): string => {
    const date = new Date(timestamp)
    const options = {weekday: 'short', hour: 'numeric', minute: 'numeric'} as const

    return date.toLocaleDateString(undefined, options)
}
export function isPartyUserShortType(party: PartyShortType): party is PartyUserShortType {
    return party.partyType === 'USER' && 'firstName' in party
}

export const getActivityOpener = (activity: ActivityType) => {
    return activity.messages.find(message => message.firstName) as PartyType
}

export const getParties = (messages: PartyType[]) => {
    const map: { [key: string]: PartyType } = {}

    messages.forEach((message: PartyType) => {
        const {partyId} = message
        let key: keyof PartyType

        for (key in message) {
            if (Object.prototype.hasOwnProperty.call(message, key)) {
                if (key !== 'event' && typeof message[key] !== 'undefined') {
                    map[partyId] = {
                        ...(map[partyId] || {}),
                        [key]: message[key],
                    }
                }
            }
        }
    })

    return map
}

export const formatLongDate = (timestamp?: string | number): string => {
    if (!timestamp) return ''

    const date = new Date(timestamp)
    const options = {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
    } as const

    return date.toLocaleDateString(undefined, options)
}
export const getFullName = (party: Partial<PartyType>) =>
    [party.firstName, party.lastName].join(' ')

export const getTime = (party: PartyType) => {
    const date = new Date(party.timestamp)
    return date.toLocaleDateString(undefined, {
        hour: 'numeric',
        minute: 'numeric',
    })
}
