import type {ChangeEvent} from 'react'
import React, {useCallback, useState} from 'react'

import type {FilterType} from '@bpinc/ad-evaluation-state-types'
import {type FiltersGroupTypes, filtersGroups} from '@bpinc/ad-evaluation-state-types'
import {SearchInput} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {FiltersGroup} from './FiltersGroup'


const groupsList: (keyof FiltersGroupTypes)[] = (Object.keys(filtersGroups) as (keyof FiltersGroupTypes)[])

type Props = {
    filtersMode: FilterType
}

export const FilterSelectionModal: React.FC<Props> = ({filtersMode}) => {
    const {t} = useLocalizationState()
    const [filterValue, setFilterValue] = useState('')
    const onSearchChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
        setFilterValue(e.target.value)
    }, [setFilterValue])

    const [openedGroup, setOpenedGroup] = useState<(keyof FiltersGroupTypes) | ''>('')

    const openGroup = useCallback((newOpenedGroup: keyof FiltersGroupTypes) => {
        if (filterValue.length > 0) {
            return
        }
        setOpenedGroup(newOpenedGroup)
    }, [setOpenedGroup, filterValue])

    const closeGroup = useCallback(() => {
        if (filterValue.length > 0) {
            return
        }
        setOpenedGroup('')
    }, [setOpenedGroup, filterValue])

    return (
        <div
            className="max-h-[720px] w-[365px] overflow-auto rounded border border-solid
        border-[--evaluation-box-border] bg-white px-4 py-[13px] shadow-DEFAULT"
        >
            <div className="text-base font-bold text-eval-text-dark">
                {t.EVALUATION_PAGE.add_filter()}
            </div>
            <SearchInput className="mt-3" value={filterValue} onChange={onSearchChange} />
            {groupsList.map(groupName => (
                <FiltersGroup
                    key={groupName}
                    name={groupName}
                    filtersList={filtersGroups[groupName]}
                    onOpen={openGroup}
                    onClose={closeGroup}
                    opened={openedGroup === groupName}
                    filterValue={filterValue}
                    filtersMode={filtersMode}
                />
            ))}
        </div>
    )
}
