import React, {useCallback} from 'react'

import {useOpenSearchContactModal} from '@bpinc/lib-modals-ui'
import {LinkLikeButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useMergeContactsActions} from '../useMergeContactsActions'


export const AddContactForMere: React.FC = () => {
    const {t} = useLocalizationState()
    const openSearchContactModal = useOpenSearchContactModal()
    const {addContactForMerge} = useMergeContactsActions()

    const handleOpenSearchContactModal = useCallback(() => {
        openSearchContactModal({
            dataKey: 'calendar-event-form-add-contact-modal',
            onSelect: ({id}) => addContactForMerge?.(id),
            title: t.MERGE_FORM.add_contact(),
            selectButtonTitle: t.GLOBAL.add(),
        })
    }, [addContactForMerge, openSearchContactModal, t.MERGE_FORM])

    return (
        <LinkLikeButton
            onClick={handleOpenSearchContactModal}
        >
            <span>{`+ ${t.MERGE_FORM.add_contact()}`}</span>
        </LinkLikeButton>
    )
}
