import {useRef, useState} from 'react'
import {type XYCoord, useDrag, useDrop} from 'react-dnd'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {ResizeTableColumn} from '@bpinc/lib-ui-elements'

import {useSearchResultsTableState} from '../ui-context/SearchResultsUIContextProvider'


const dragStyles = tcn(
    'before:absolute before:inset-x-0 before:top-0 before:hidden before:h-1 before:bg-[--c-dropzone-line-bg]',
    'after:absolute after:inset-x-0 after:bottom-0 after:hidden after:h-1 after:bg-[--c-dropzone-line-bg]',
)

export const useMetricDndLogic = (metric: ResizeTableColumn) => {
    const {columns, setColumns} = useSearchResultsTableState()

    const dropRef = useRef<HTMLElement | null>(null)

    const [dropPosition, setDropPosition] = useState<'before' | 'after' | null>(null)
    const [{canDrop}, drop] = useDrop<ResizeTableColumn, unknown, {canDrop: boolean}>({
        collect: monitor => ({
            canDrop: monitor.canDrop(),
            isOver: monitor.isOver(),
        }),
        accept: ['metric'],
        hover(_, monitor) {
            const dropBoundingRect = dropRef.current?.getBoundingClientRect()

            if (!dropBoundingRect || !monitor.isOver()) {
                return
            }

            const hoveringOverFirstHalf = isHoveringOverFirstHalf(
                monitor.getClientOffset(),
                dropBoundingRect,
            )

            setDropPosition(hoveringOverFirstHalf ? 'before' : 'after')
        },

        canDrop: (draggedItem, monitor) => {
            if (!monitor.isOver()) {
                return false
            }

            return draggedItem.name !== metric.name
        },
        drop: draggedItem => {
            const dropIndex = getDropIndex()
            const newConfig = [...columns]
            const previousMetricIndex = columns.findIndex(
                m => m.name === draggedItem.name,
            )
            newConfig.splice(previousMetricIndex, 1)
            newConfig.splice(dropIndex, 0, {
                ...draggedItem,
            })

            setColumns(newConfig)
            function getDropIndex() {
                const draggedItemIdx = columns.findIndex(
                    m => m.name === draggedItem.name,
                )

                let result = columns.findIndex(
                    m => m.name === metric.name,
                )

                if (dropPosition === 'before' && result > draggedItemIdx && result !== 0) {
                    result--
                }

                if (dropPosition === 'after' && result < draggedItemIdx && result !== columns.length - 1) {
                    result++
                }
                return result
            }
        },
    })

    const [{isDragging}, dragRef, dragPreviewRef] = useDrag({
        type: 'metric',
        item: metric,
        collect(monitor) {
            return {
                isDragging: monitor.isDragging(),
            }
        },
    })

    drop(dropRef)

    const dndStyles = tcn(dragStyles, {
        'before:block': canDrop && dropPosition === 'before',
        'after:block': canDrop && dropPosition === 'after',
        'opacity-50': isDragging,
    })

    return {dropRef, dndStyles, dragRef, dragPreviewRef}
}


export function isHoveringOverFirstHalf(
    clientOffset: XYCoord | null,
    dropBoundingRect: DOMRect,
): boolean {
    const hoverMiddleY = (dropBoundingRect.bottom - dropBoundingRect.top) / 2

    if (!clientOffset) {
        return false
    }

    const hoverClientY = clientOffset.y - dropBoundingRect.top

    return hoverClientY < hoverMiddleY
}
