import React from 'react'

import {createComponentLoader} from '@bpinc/lib-ui-elements'
import {assertKBBridgeInterface, useKbBridgeConnector} from '@bpinc/state-kb-bridge-provider'
import type {CommonModalProps} from '@bpinc/state-modals-types'


const {KBSettingsLoader} = createComponentLoader('knowledgeBase', import('knowledgeBase'), 'KBSettings')

export const KBSettingsModal: React.FC<CommonModalProps> = props => {
    const kbBridge = useKbBridgeConnector()
    assertKBBridgeInterface(kbBridge)

    return (
        <KBSettingsLoader kbBridge={kbBridge} {...props} />
    )
}
