import React from 'react'

import {MainPageUIProvider} from '@bpinc/ad-main-page-ui-context'
import {NavigationStateProvider} from '@bpinc/ad-navigation-state-context'
import {createPlatformSpecific} from '@bpinc/ad-platform-specific-web-impl'
import {ScrollStateProvider} from '@bpinc/ad-scroll-state-context'
import {ServiceStateProvider} from '@bpinc/ad-service-context'
import {SupervisionUIContextProvider} from '@bpinc/ad-supervision-ui-context'
import {UIContextProvider} from '@bpinc/ad-ui-context'
import {ADShortcutsProvider as ShortcutsProvider} from '@bpinc/lib-accessible-shortcuts-context'
import {EmailAttachmentsUIStateProvider} from '@bpinc/lib-attachments-state'
import {AuthorizationModule} from '@bpinc/lib-authorization-module'
import {NavigationStateProvider as ADCNavigationStateProvider} from '@bpinc/lib-communicator-navigation-state'
import {CommunicatorUIProvider} from '@bpinc/lib-communicator-ui-context'
import {createProviderWrapper} from '@bpinc/lib-helpers'
import {ActiveInteractionContextProvider} from '@bpinc/lib-interaction-context'
import {InteractionUIStateProvider} from '@bpinc/state-interaction-ui'

import {App} from './App'
import {TanstackQueryClientProvider} from './tanstackQueryClientProvider'


const ADProviders = createProviderWrapper([
    ActiveInteractionContextProvider,
    TanstackQueryClientProvider,
    InteractionUIStateProvider,
    ServiceStateProvider,
    NavigationStateProvider,
    ADCNavigationStateProvider,
    MainPageUIProvider,
    ScrollStateProvider,
    UIContextProvider,
    ShortcutsProvider,
    SupervisionUIContextProvider,
    EmailAttachmentsUIStateProvider,
    CommunicatorUIProvider,
])

const Connector: React.FC = () => {
    return (
        <AuthorizationModule
            platformSpecific={createPlatformSpecific('ad')}
            app="ad"
            appRemoteName="agent"
        >
            {() => (
                <ADProviders>
                    <App />
                </ADProviders>
            )}
        </AuthorizationModule>
    )
}

export {Connector}
