import React, {useCallback} from 'react'
import * as HoverCard from '@radix-ui/react-hover-card'

import type {StatsItem} from '@bpinc/ad-stats-state-types'
import {ShowMore} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface Props {
    data: StatsItem[]
    selectedService: string
}

export const Stats: React.FC<Props> = ({data, selectedService}) => {
    const t = useLocalizationState('t')
    const getRank = useCallback((n?: string | number) => {
        if (!n) return n
        const mod = Number(n) % 100
        if (mod > 10 && mod < 14) return n + 'th'
        switch (mod % 10) {
            case 1:
                return n + 'st'
            case 2:
                return n + 'nd'
            case 3:
                return n + 'rd'
        }
        return n + 'th'
    }, [])

    function filterItems(item: StatsItem) {
        //We show  service select instead of this metric
        return item.metric !== 'SERVICE_NAME'
    }

    function renderValue(item: StatsItem) {
        //Service name isn't shown in old AD
        if (item.metric === 'SERVICE_NAME') {
            return
        }

        const parts = [item.value]

        // if ('total' in item) parts.push(`(${item.total})`)
        // if ('next' in item) parts.push(`/${item.next}`)
        if ('rank' in item) parts.push(`(${getRank(item.rank)})`)

        return parts.join(' ')
    }

    return (
        <>
            <ShowMore
                className="flex gap-4 overflow-hidden"
                trigger={
                    <HoverCard.Root openDelay={0}>
                        <HoverCard.Trigger asChild>
                            {/* TODO: Replace with Link Button once added to jskit-ui */}
                            <button className="whitespace-nowrap underline text-primary-link">
                                {t.GLOBAL.view_all()}
                            </button>
                        </HoverCard.Trigger>
                        <HoverCard.Portal>
                            <HoverCard.Content className="w-[410px] bg-neutral-0 p-4 shadow-3" align="end">
                                <p className="font-medium text-neutral-fade button-sm">
                                    {selectedService}
                                </p>
                                <div className="relative flex flex-wrap gap-y-8 overflow-hidden pt-2">
                                    {data.map((item, i) => {
                                        const value = renderValue(item)

                                        return (
                                            value && (
                                                <div
                                                    className={tcn(
                                                        'min-h-[35px] min-w-9.5 shrink-0 pr-6 border-action',
                                                        'after:absolute after:mt-4 after:h-px after:w-full after:bg-neutral-200',
                                                    )}
                                                    key={i}
                                                >
                                                    <div className="text-neutral-fade caption-s">
                                                        {item.display}
                                                    </div>
                                                    <div className="font-semibold text-primary-on-primary">
                                                        {value}
                                                    </div>
                                                </div>
                                            )
                                        )
                                    })}
                                </div>
                                <div className="absolute -mt-px h-px w-full bg-neutral-0" />
                            </HoverCard.Content>
                        </HoverCard.Portal>
                    </HoverCard.Root>
                }
            >
                {data.filter(filterItems).map((item, i) => {
                    const value = renderValue(item)
                    return value ? (
                        <div
                            key={i}
                            className="flex gap-1 whitespace-nowrap caption-s"
                        >
                            <span className="text-neutral-fade">
                                {item.display}
                            </span>
                            <span className="font-semibold text-primary-on-primary">
                                {value}
                            </span>
                        </div>
                    ) : null
                })}
            </ShowMore>
        </>
    )
}
