import type {Dispatch} from 'react'
import {useCallback} from 'react'

import {useOnLogout} from '@bpinc/ad-session-context'

import type {Action} from '../reducer'


export function useLogoutStateResetter(dispatch: Dispatch<Action>) {
    const resetState = useCallback(function resetStateCallback() {
        dispatch({type: 'resetState'})
    }, [])

    useOnLogout(resetState)
}
