import type {Dispatch} from 'react'
import {useEffect} from 'react'

import {useInteractionChatStateConnector} from '@bpinc/ad-interaction-chat-state-context'

import type {Action} from '../reducer'


export function useChatMessagesDispatcher(dispatch: Dispatch<Action>) {
    const interactionChatStateConnector = useInteractionChatStateConnector()

    useEffect(() => {
        const subscription = interactionChatStateConnector?.dispenser.subscribe({
            onNewMessage: newMessage => {
                dispatch({
                    type: 'chatMessageAppeared',
                    id: newMessage.msg_id,
                })

            },
        })
        return () => {
            subscription?.unsubscribe()
        }
    }, [dispatch, interactionChatStateConnector?.dispenser])
}
