
import {useGetUserPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import {useSessionState} from '@bpinc/ad-session-context'


export function useMyAvatar() {
    const userId = useSessionState('userId')
    const getUserPictureUrl = useGetUserPictureUrl()

    const avatarSrc = userId ? getUserPictureUrl(userId) : undefined

    return {
        avatarSrc,
    }
}
