import {useMemo} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'

import {routesConfig} from '../../constants/routes'


export function useModulesImportByPrivileges() {
    const privileges = useSessionState('privileges')

    useMemo(() => {
        Object.values(routesConfig).forEach(route => {
            if (route.checkPrivileges?.(privileges)) {
                route.load?.()
            }
        })
    }, [privileges])
}
