import {useCallback} from 'react'
import type {Table} from '@tanstack/react-table'
import produce from 'immer'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {QueueItem} from '@bpinc/ad-request-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'


export function useCloseAsSpamHandler(queueItems: QueueItem[], table?: Table<QueueItem>) {
    const {openModal} = useModalsStateMethods()
    const {closeCasesAsSpam} = useCaseStateMethods()
    const {t} = useLocalizationState()

    return useCallback(
        function handleCloseAsSpamCallback() {
            const selectedItemsCount = queueItems?.length ?? 0

            if (!selectedItemsCount) {
                return
            }

            openModal({
                title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning(),
                body:
                selectedItemsCount > 1
                    ? t.EMAIL.spam_confirm_message_multiple(selectedItemsCount.toString())
                    : t.EMAIL.spam_confirm_message_single(queueItems[0].caseNumber ?? ''),
                actions: {
                    ok: {
                        title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk(),
                        action: handleOk,
                    },
                    cancel: {
                        title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel(),
                    },
                },
                className: 'autoSizeModal',
            })

            function handleOk() {
                closeCasesAsSpam(queueItems.map(s => s.caseId), 'TEAM')
                unselectItems()

                function unselectItems() {
                    table?.setRowSelection(produce(draft => {
                        queueItems.forEach(q => {
                            delete draft[q.caseId]
                        })
                    }))
                }
            }
        },
        [closeCasesAsSpam, openModal, queueItems, t.EMAIL, t.WINDOW_POPUP_TITLES_AND_MESSAGES, table],
    )
}
