import {useCallback} from 'react'

import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'


type OpenEmailBySupervisorWarningModalParams = { okAction: () => void}

export function useOpenEmailBySupervisorWarningModal() {
    const {t} = useLocalizationState()
    const {openModal} = useModalsStateMethods()

    const openEmailBySupervisorWarningModal = useCallback(
        function openEmailBySupervisorWarningModalCallback({okAction}: OpenEmailBySupervisorWarningModalParams) {
            openModal({
                title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning(),
                body: t.WINDOW_POPUP_TITLES_AND_MESSAGES.openingEmailFromSupervisingStateConfirmation(),
                actions: {
                    ok: {
                        title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk(),
                        action: okAction,
                    },
                    cancel: {
                        title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel(),
                    },
                },
                className: 'autoSizeModal',
            })
        },
        [openModal, t.WINDOW_POPUP_TITLES_AND_MESSAGES],
    )

    return openEmailBySupervisorWarningModal
}
