import {useCallback, useEffect, useRef} from 'react'
import {get} from 'lodash-es'

import {useLocalizationState} from '@bpinc/state-localization-context'

import type {PageTitleState} from '../reducer'


export function useTitleBlinker(state: PageTitleState) {
    const {t} = useLocalizationState()

    const initialTitleRef = useRef(document.title)
    const blinkingInterval = useRef(0)

    const blinkTitle = useCallback(
        function blinkTitleCallback(newTitleTranslationKey: string | undefined) {
            const defaultString = 'Unknown / incoming'
            const title = newTitleTranslationKey ? get(t, newTitleTranslationKey)() : defaultString

            blinkingInterval.current = setInterval(() => {
                document.title = document.title === title ? initialTitleRef.current : title
            }, 700)
        }, [t],
    )

    const stopBlinking = useCallback(
        function stopBlinkingCallback() {
            clearInterval(blinkingInterval.current)
            document.title = initialTitleRef.current
        }, [],
    )


    useEffect(() => {
        if (state.events.length) {
            const lastEvent = state.events[state.events.length - 1]
            blinkTitle(lastEvent?.title)
        } else {
            stopBlinking()
        }

        return () => {
            clearInterval(blinkingInterval.current)
        }
    }, [blinkTitle, state.events, stopBlinking])
}
