import React from 'react'
import {DndProvider} from 'react-dnd'
import {HTML5Backend} from 'react-dnd-html5-backend'

import {useUIState} from '@bpinc/ad-ui-context'
import {ToastProvider} from '@bpinc/lib-jskit-ui'
import {Modals} from '@bpinc/lib-modals-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {AriaStatuses} from '@bpinc/lib-ui-elements'

import {MainPage} from '../mainpage'
import {MainPageResizeController} from '../mainpage/components/MainPageResizeController'
import {Navbar} from '../navbar'
import {useAppConfigCheckers} from './hooks/useAppConfigChecker'


export const App: React.FC = () => {
    const {isTabNavHighlighted} = useUIState()
    useAppConfigCheckers()

    return (
        <div
            className={tcn('h-full w-full', {
                '[&_*:focus-within]:!outline-none [&_*:focus]:!outline-none': !isTabNavHighlighted,
                '[&_*:focus-within]:!outline-0 [&_*:focus]:!outline-0': !isTabNavHighlighted,
            })}
        >
            <DndProvider backend={HTML5Backend}>
                <ToastProvider>
                    <Navbar>
                        <MainPageResizeController>
                            <MainPage />
                        </MainPageResizeController>
                    </Navbar>
                </ToastProvider>
            </DndProvider>
            <AriaStatuses />
            <Modals />
        </div>
    )
}
