import React, {useCallback} from 'react'

import {useCommunicatorUIState} from '@bpinc/ad-main-page-ui-context'
import {IconPanelHide24, IconPanelShow24} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'
import {Button} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {leftBarStyles} from '../LeftBar'


export const CommunicatorButton: FCWithChildren = ({children}) => {
    const {t} = useLocalizationState()

    const {floatingState, setFloatingState, openerRef} =
        useCommunicatorUIState()

    const handleCommunicatorButtonClick = useCallback(
        function handleCommunicatorButtonClickCallback() {
            setFloatingState(s => (s === 'closed' ? 'floating' : 'closed'))
        },
        [setFloatingState],
    )

    return (
        <Button
            ref={openerRef}
            tooltip={t.UNDEFINED.navigation_acl_tooltip()}
            data-testid="communication_panel_leftbar_btn"
            transparent
            className={tcn(leftBarStyles.button.base, 'mb-2 shrink-0')}
            onClick={handleCommunicatorButtonClick}
        >
            <div className={leftBarStyles.button.icon()}>
                {floatingState !== 'closed' ? (
                    <IconPanelShow24 />
                ) : (
                    <IconPanelHide24 />
                )}
            </div>
            {children}
        </Button>
    )
}
