import type {RefObject} from 'react'
import React, {useCallback} from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import type {ContactType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {getContactUrl} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {AvatarLegacy} from '@bpinc/lib-avatar-ui'
import {clipboard} from '@bpinc/lib-helpers'
import {SemanticButton, Svg} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import imgLink from '../../../images/link.svg'
import {ProfileHeaderColumn, ProfileHeaderWrapper} from './ProfileHeaderLayout'


interface Props {
    contact: ContactType
    actionsRef: RefObject<HTMLDivElement>
}

export const ProfileHeaderView: React.FC<Props> = ({contact, actionsRef}) => {
    const {formatFullName} = useFormatting()
    const {t} = useLocalizationState()
    const serverOrigin = useSessionState('serverOrigin')
    const getAvatarUrl = useGetAvatarUrl()

    const avatarUrl = getAvatarUrl({contactId: contact.Id.oid})

    const handleGetShareLinkClick = useCallback(() => {
        const contactId = contact.Id.oid
        const url = getContactUrl(serverOrigin, contactId)
        clipboard.copy(url)
    }, [contact.Id.oid, serverOrigin])

    return (
        <ProfileHeaderWrapper>
            <ProfileHeaderColumn type="avatar">
                <AvatarLegacy
                    firstName={contact.firstName}
                    lastName={contact.lastName}
                    url={avatarUrl}
                    type="contact"
                    size={74}
                />
            </ProfileHeaderColumn>

            <ProfileHeaderColumn>
                <div className="my-1 mb-0.5 w-3/4 py-1 text-left text-base text-black no-underline">
                    {formatFullName(contact.firstName, contact.lastName)}
                </div>

                <div className="flex flex-row">
                    <div className="flex-auto">
                        <div className="py-1 pr-2.5">
                            {contact.title}
                        </div>

                        <div className="py-1 pr-2.5">
                            {contact.companyName}
                        </div>
                    </div>

                    <div className="flex-auto">
                        <div className="py-1 pr-2.5">
                            {contact.primaryPhone && formatPhone(contact.primaryPhone.phone)}
                        </div>

                        <div className="py-1 pr-2.5">
                            {contact.primaryEmail && contact.primaryEmail.emailAddress}
                        </div>
                    </div>
                </div>
            </ProfileHeaderColumn>

            <ProfileHeaderColumn type="controls">
                <div className="absolute right-0 top-0 m-0">
                    <SemanticButton
                        basic
                        tooltip={t.GLOBAL.copy_shareable_link_button_tooltip()}
                        icon={<Svg src={imgLink} className="block h-3.5 w-3.5" />}
                        onClick={handleGetShareLinkClick}
                    />
                </div>
                <div ref={actionsRef} />
            </ProfileHeaderColumn>
        </ProfileHeaderWrapper>
    )
}
