import {useEffect} from 'react'
import type {Control} from 'react-hook-form'
import {useController} from 'react-hook-form'

import type {Email, EmailAttachment} from '@bpinc/ad-request-urls'
import {type Attachment, useEmailAttachmentsUIState} from '@bpinc/lib-attachments-state'
import {useBoolean} from '@bpinc/lib-helpers'
import {truthy} from '@bpinc/lib-types-helpers'

import type {EmailFormFields} from './types'


interface UseAttachmentsStateSyncParams {
    email: Email | undefined
    attachmentsList: Attachment<EmailAttachment>[]
    control: Control<EmailFormFields>
    emailLoaded: boolean
}

export function useAttachmentsStateSync({
    attachmentsList,
    control,
    email,
    emailLoaded,
}: UseAttachmentsStateSyncParams,
) {
    const stateInitialized = useBoolean()
    const {
        field: {onChange},
    } = useController({
        control,
        name: 'attachments',
    })

    const initializeAttachmentsList = useEmailAttachmentsUIState('initializeAttachmentsList')

    const initialAttachments = email?.attachments

    /** Email draft initial values contain only id and name of the attachment.
     *  Based of this information we initialize the attachments list
     *  and render appropriate UI for each attachment
     */
    useEffect(function initializeAttachments() {
        if (emailLoaded && !stateInitialized.value) {
            stateInitialized.setTrue()

            if (initialAttachments?.length && email?.id) {
                initializeAttachmentsList(email.id, initialAttachments)
            }
        }
    }, [email?.id, emailLoaded, initialAttachments, initializeAttachmentsList, stateInitialized])

    /** Each time attachment list changes, we update the form field value */
    useEffect(function syncFormFieldValue() {
        onChange(truthy(attachmentsList.map(({fileData}) => fileData?.id)))
    }, [attachmentsList, control, onChange])
}
