import type {FC} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'


interface SlaProps {
    value: number | undefined
}

export const Sla: FC<SlaProps> = ({value}) => {
    if (value === undefined || value < 0) {
        return null
    }

    return (
        <div className="flex gap-0.5">
            {Array.from({length: 5}).map((_, index) => {
                const isFilled = index * 20 < value
                return (
                    <span
                        key={index}
                        className={tcn('h-2 w-2 rounded-sm border border-icon', {
                            'bg-icon': isFilled,
                        })}
                    />
                )
            })}
        </div>
    )
}
