import type {FC} from 'react'
import {Button} from '@servicepattern/ui'

import {ButtonDropdown} from '@bpinc/lib-jskit-ui'


interface InteractionButtonProps {
    icon: JSX.Element
    text: string
    items: DropdownOption[]
    OptionComponent: FC<Omit<DropdownOption, 'key'>>
    align?: 'start' | 'center' | 'end'
    onClick: (value: string) => void
}

type DropdownOption = {
    key: string
    value: string
    type: string
}

export const InteractionButton: FC<InteractionButtonProps> = ({
    icon,
    text,
    items,
    onClick,
    OptionComponent,
    align = 'center',
}) => {
    return items.length > 1 ? (
        <ButtonDropdown
            align={align}
            modal={false}
            className="flex-auto [&>*:not(:last-child)]:border-b [&>*:not(:last-child)]:border-default"
            options={items.map(({key, ...props}) => (
                <button key={key} onClick={() => onClick(props.value)}>
                    <OptionComponent {...props} />
                </button>
            ))}
            button={{variant: 'neutral', iconLeft: icon}}
        >
            {text}
        </ButtonDropdown>
    ) : (
        <Button
            onClick={() => onClick(items[0]!.value)}
            className="flex-auto"
            state={items.length ? 'default' : 'disabled'}
            variant="neutral"
            iconLeft={icon}
        >
            {text}
        </Button>
    )
}
