import {useMemo} from 'react'

import type {DropdownItemProps} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export function useSortingOptions(): DropdownItemProps[] {
    const {t} = useLocalizationState()

    return useMemo(() => {
        return [
            {value: 'createTime', text: t.EMAIL.email_queue_field_createTime()},
            {value: 'updateTime', text: t.EMAIL.email_queue_field_updateTime()},
            {value: 'customerUpdate', text: t.EMAIL.email_queue_field_customerUpdate()},
            {value: 'sla', text: t.EMAIL.email_queue_field_serviceLevel()},
            {value: 'from', text: t.EMAIL.email_queue_field_from()},
            {value: 'subject', text: t.EMAIL.email_queue_field_subject()},
            {value: 'flag', text: t.EMAIL.email_queue_field_flag()},
        ]
    }, [t.EMAIL])
}
