import {useMemo} from 'react'
import type {Table} from '@tanstack/react-table'
import type {PrimitiveAtom} from 'jotai'
import {useAtomValue} from 'jotai'

import type {QueueItem} from '@bpinc/ad-request-urls'


export function useSelectedRows(activeQueueItemAtom: PrimitiveAtom<QueueItem | undefined>, table: Table<QueueItem>) {
    const rowSelectedByClick = useAtomValue(activeQueueItemAtom)
    const selectedRows = table.getSelectedRowModel().rows

    return useMemo(() => {
        const rowsSelectedByCheckbox = selectedRows.map(r => r.original)

        if (rowsSelectedByCheckbox.length) {
            return rowsSelectedByCheckbox
        }

        if (rowSelectedByClick) {
            return [rowSelectedByClick]
        }

        return []
    }, [rowSelectedByClick, selectedRows])

}
