import React, {type SyntheticEvent, useCallback, useMemo} from 'react'
import {useAtom} from 'jotai'

import {type DropdownProps, Dropdown} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {casesServiceAtom} from '../atoms'


export const CasesServiceSelect: React.FC = () => {
    const {t} = useLocalizationState()
    const [service, setService] = useAtom(casesServiceAtom)
    const handleSortingFieldChange = useCallback(
        (_evt: SyntheticEvent, data: DropdownProps) => {
            const selectedService = typeof data.value === 'string' && data.value === 'team' ? 'team' : undefined
            setService(selectedService)
        },
        [setService],
    )
    const options = useMemo(() => ([
        {text: t.EMAIL.search_queue_all_team_services(), value: 'all'},
        {text: t.EMAIL.search_queue_team_services(), value: 'team'},
    ]), [t.EMAIL])

    return (
        <Dropdown
            inline
            options={options}
            onChange={handleSortingFieldChange}
            value={service || 'all'}
            hideIcon
            trigger={
                <p className="cursor-pointer font-normal">
                    {t.EMAIL.email_teamQueue_service_label()}
                    {': '}
                    {service === 'team'
                        ? t.EMAIL.search_queue_team_services()
                        : t.EMAIL.search_queue_all_team_services()
                    }
                </p>
            }
        />
    )
}
