import type {ReactNode} from 'react'
import React, {useMemo} from 'react'
import {useMatch} from 'react-router-dom'

import {useInteractionWorkspaceState} from '@bpinc/ad-main-page-ui-context'
import {AppRoutes} from '@bpinc/ad-navigation-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useStatsState} from '@bpinc/ad-stats-state-context'
import {
    IconCalendar,
    IconCampaigns,
    IconCases,
    IconChat,
    IconContacts,
    IconContext,
    IconHomePage,
    IconKnowledgeBase,
    IconOmniQualityManagement,
    IconSupervisor,
} from '@bpinc/lib-jskit-ui'
import {UnreadBadge} from '@bpinc/lib-ui-elements'
import {useUnreadGroupChatsQuantity} from '@bpinc/state-group-chat-context'
import {useKbBridge} from '@bpinc/state-kb-bridge-provider'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {AppRoutesType} from '../../../constants/routes'
import {routesConfig} from '../../../constants/routes'
import {leftBarStyles} from '../LeftBar'


type MenuItemType = {
    route: AppRoutesType
    disabled?: boolean
    title: string
    titleFull?: string
    icon: ReactNode
    testId: string
    badge?: ReactNode
    hidden?: boolean
}

export function useMainMenuItems() {
    const t = useLocalizationState('t')
    const privileges = useSessionState('privileges')
    const {shownWorkspaceTabs} = useInteractionWorkspaceState()
    const {settings: kbSettings} = useKbBridge()
    const hasOpenedWorkspaceTabs = !!shownWorkspaceTabs?.length

    const unreadGroupChatsQuantity = useUnreadGroupChatsQuantity()
    const isSupervisorPage = Boolean(useMatch({path: AppRoutes.supervision, end: false}))
    const intentAlertsAmount = useStatsState(state => isSupervisorPage ? 0 : state.intentAlerts.filter(i => !i.closed).length)

    const menuItems = useMemo<MenuItemType[]>(() => ([
        {
            route: AppRoutes.interactionWorkspace,
            title: t.NAV_BAR.page_title_context(),
            icon: <IconContext />,
            disabled: !hasOpenedWorkspaceTabs,
            testId: 'context-link',
        },
        {
            route: AppRoutes.calendar,
            title: t.NAV_BAR.page_title_calendar(),
            icon: <IconCalendar />,
            testId: 'calendar-link',
        },
        {
            route: AppRoutes.cases,
            title: t.NAV_BAR.page_title_cases(),
            icon: <IconCases />,
            testId: 'cases-link',
        },
        {
            route: AppRoutes.search,
            title: t.NAV_BAR.page_title_contacts(),
            icon: <IconContacts />,
            testId: 'search-link',
        },
        {
            route: AppRoutes.campaigns,
            title: t.NAV_BAR.page_title_campaigns(),
            icon: <IconCampaigns />,
            testId: 'campaigns-link',
        },
        {
            route: AppRoutes.supervision,
            title: t.NAV_BAR.page_title_supervisor(),
            icon: <IconSupervisor />,
            badge:
              <UnreadBadge
                  unreadAmount={intentAlertsAmount}
                  className={leftBarStyles.unreadBadge}
              />,
            testId: 'supervision-link',
        },
        {
            route: AppRoutes.chat,
            title: t.NAV_BAR.page_title_internal_chat(),
            icon: <IconChat />,
            badge:
              <UnreadBadge
                  unreadAmount={unreadGroupChatsQuantity}
                  className={leftBarStyles.unreadBadge}
              />,
            testId: 'chat-center-link',
        },
        {
            route: AppRoutes.omniQm,
            title: t.NAV_BAR.page_title_omni_qm(),
            icon: <IconOmniQualityManagement />,
            testId: 'omni-qm-link',
        },
        {
            route: AppRoutes.knowledgeBase,
            title: t.NAV_BAR.page_title_kb(),
            titleFull: t.NAV_BAR.page_title_kb_full(),
            icon: <IconKnowledgeBase />,
            testId: 'knowledge-base-link',
            hidden: !kbSettings?.kbEnabled,
        },
        {
            route: AppRoutes.home,
            title: t.NAV_BAR.page_title_home_page(),
            icon: <IconHomePage />,
            testId: 'home-link',
        },
    ]).filter(item => {
        if (item.hidden) {
            return false
        }

        const {checkPrivileges} = routesConfig[item.route]
        if (!checkPrivileges) {
            return true
        }

        return checkPrivileges(privileges)
    }), [
        t.NAV_BAR,
        hasOpenedWorkspaceTabs,
        intentAlertsAmount,
        unreadGroupChatsQuantity,
        privileges,
        kbSettings?.kbEnabled,
    ])

    return menuItems
}
