import {useMutation} from '@tanstack/react-query'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {DefaultService} from '@bpinc/ad-request-urls'

import type {User} from '../atoms'


export interface CreateNoteMutationParams {
    user: User
    notes: string
    caseId: string
}

export function useCreateNoteMutation() {
    const {getNamesFromDisplayName} = useFormatting()

    return useMutation({
        mutationFn: ({user, notes, caseId}: CreateNoteMutationParams) => {
            const [firstName, lastName] = getNamesFromDisplayName(user.name)

            return DefaultService.createCaseActivity({
                requestBody: {
                    media_type: 'NOTES',
                    parties: [
                        {
                            party_type: 'USER',
                            user_id: user.id,
                            first_name: firstName,
                            last_name: lastName,
                            notes: notes,
                            start_time: {
                                date: new Date().toISOString(),
                            },
                        },
                    ],
                    case_ids: [
                        {
                            oid: caseId,
                        },
                    ],
                    has_voice_recording: false,
                    is_agent_assignment: false,
                    is_flagged: false,
                    is_auto_acknowledgement: false,
                },
            })
        },
    })
}
