import {useMemo} from 'react'

import {useChatUnread} from '@bpinc/ad-interaction-chat-state-context'
import {useInteractions} from '@bpinc/ad-interaction-state-context'
import {isInternalChat} from '@bpinc/ad-interaction-state-methods'
import {useGroupChatTotalUnread} from '@bpinc/state-group-chat-context'


export function useInteractionChatsUnread() {
    const unreadExternal = useChatUnread()

    const interactions = useInteractions()
    const internalChatIds = useMemo(() => interactions
        .filter(isInternalChat)
        .map(i => i.id),
    [interactions])

    const unreadGroup = useGroupChatTotalUnread(item => internalChatIds.includes(item.chatId))

    return unreadExternal + unreadGroup

}
