import type {FC} from 'react'
import React, {useMemo} from 'react'
import {useAtom} from 'jotai'

import {Dropdown} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useQueueCountByServiceQuery} from '../../../queries'
import {useDefaultQueueServiceOptionsByPrivileges} from '../../useDefaultQueueServiceOptionsByPrivileges'
import {useEmailServicesOptions} from '../../useEmailServicesOptions'
import {serviceFilterAtom, teamPaginationAtom, teamSortingAtom} from '../atoms'
import {Pagination} from '../Pagination'
import {SortingControls} from '../SortingControls'
import {useSortingOptions} from './useSortingOptions'


interface TableHeaderContentProps {
    actualQueueCount: number
    totalQueueCount: number | undefined
    noData: boolean
}

export const TableHeaderContent: FC<TableHeaderContentProps> = ({
    actualQueueCount,
    totalQueueCount,
    noData,
}) => {
    const optionsByPrivileges = useDefaultQueueServiceOptionsByPrivileges()
    const serviceOptions = useEmailServicesOptions()

    const {data: queueCountByService, refetch} = useQueueCountByServiceQuery({
        serviceIds: serviceOptions.map(o => o.value),
    })

    const finalServiceOptions = useMemo(() => {
        return [...optionsByPrivileges, ...serviceOptions]
            .filter(
                option =>
                    option.value === 'all' ||
                    queueCountByService?.[option.value],
            )
            .map(option => {
                const count = queueCountByService?.[option.value]
                return {
                    ...option,
                    text: count ? `${option.text} (${count})` : option.text,
                }
            })
    }, [optionsByPrivileges, queueCountByService, serviceOptions])

    const [serviceFilter, setServiceFilter] = useAtom(serviceFilterAtom)

    const {t} = useLocalizationState()

    const sortingOptions = useSortingOptions()

    return (
        <div className="flex items-center justify-between">
            <div className="ml-2 flex items-center">
                <p className="mr-1">
                    {t.EMAIL.email_teamQueue_service_label()}
                </p>
                <Dropdown
                    onOpen={refetch}
                    options={finalServiceOptions}
                    inline
                    value={serviceFilter}
                    onChange={(_event, data) => {
                        if (typeof data.value === 'string') {
                            setServiceFilter(data.value)
                        }
                    }}
                />
            </div>
            {!noData && (
                <SortingControls
                    sortingAtom={teamSortingAtom}
                    options={sortingOptions}
                />
            )}
            {!noData && (
                <Pagination
                    paginationAtom={teamPaginationAtom}
                    actualQueueCount={actualQueueCount}
                    totalQueueCount={totalQueueCount}
                />
            )}
        </div>
    )
}
