import React, {type FC, useMemo} from 'react'
import {useAtom} from 'jotai'

import type {DirectoryObject} from '@bpinc/ad-directory-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import {Dropdown} from '@bpinc/lib-ui-elements'

import {teamIdAtom} from '../atoms'


interface TeamSelectorProps {
    teams: DirectoryObject[]
}

export const TeamSelector: FC<TeamSelectorProps> = ({teams}) => {
    const selfTeamId = useSessionState('teamId')
    const [teamId, setTeamId] = useAtom(teamIdAtom)

    const options = useMemo(() => {
        return teams.filter(team => team.supervised || team.id === selfTeamId).map(team => ({
            key: team.id,
            text: team.name,
            value: team.id,
        }))
    }, [selfTeamId, teams])

    if (options.length < 2) {
        return null
    }

    return (
        <Dropdown
            className="ml-2"
            options={options}
            onChange={(_evt, data) => {
                if (typeof data.value === 'string') {
                    setTeamId(data.value)
                }
            }}
            value={teamId || selfTeamId}
            inline
        />
    )
}
