import {useMemo} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'


export function useDefaultServicesByPrivileges() {
    const privileges = useSessionState('privileges')

    return useMemo(() => {
        if (privileges.CAN_SEE_CONTENTS_OF_EMAIL_PUSH_QUEUES) {
            return ['router', 'team'] as const
        }
        return []
    }, [privileges.CAN_SEE_CONTENTS_OF_EMAIL_PUSH_QUEUES])
}
