import React, {useRef} from 'react'
import * as HoverCard from '@radix-ui/react-hover-card'
import {Button} from '@servicepattern/ui'

import {AddToExistingModal, QuickAddModal, useAddNewContact} from '@bpinc/ad-contact-modals'
import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {EmailTypes, PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import {
    useActiveChatInteractionId,
    useActiveInteraction,
    useActiveInteractionProp,
} from '@bpinc/ad-interaction-state-context'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import {canHaveDisposition, getItemDisposition, isWorkItem} from '@bpinc/ad-interaction-state-methods'
import {AppRoutes, useNavigationState} from '@bpinc/ad-navigation-state-context'
import {useContactsActions} from '@bpinc/ad-searchpage-ui'
import {useServiceState} from '@bpinc/ad-service-context'
import {useServicesDispositionsStateServices} from '@bpinc/ad-services-dispositions-context'
import {useHandleUseContact} from '@bpinc/lib-contact-details'
import {InteractionFavoriteButton} from '@bpinc/lib-favorites-button'
import {useBoolean} from '@bpinc/lib-helpers'
import {useOpenDispositionPanel} from '@bpinc/lib-interaction-ui-hooks'
import {IconEmailEnvelope24, IconMessage, IconPhone} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {EmailOption, PhoneOption} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {InteractionButton} from './ContactControls/InteractionButton'
import {InteractionBarAvatar} from './InteractionBarAvatar'
import {InteractionBarDisplayName} from './InteractionBarDisplayName'


interface ContactInfoProps {
    className?: string
}

const VERTICAL_PADDING = 9

type DropdownOption = {
    key: string
    value: string
    type: string
}

export const ContactInfo: React.FC<ContactInfoProps> = ({className}) => {
    const hovercardOpened = useBoolean()
    const selectedDisposition = useActiveInteractionProp(getItemDisposition)
    const services = useServicesDispositionsStateServices()
    const canHaveDispositions = useActiveInteractionProp(i =>
        canHaveDisposition(i, services),
    )
    const {t} = useLocalizationState()

    const dispositionText = selectedDisposition?.id
        ? selectedDisposition.name
        : t.DISPOSITION.set_disposition()

    const primaryContentRef = useRef<HTMLDivElement>(null)
    const primaryContentHeight = primaryContentRef.current?.offsetHeight ?? 0
    const goTo = useNavigationState('goTo')

    const activeItem = useActiveInteraction()
    const openQuickAddModal = useModalComponentOpener(QuickAddModal)
    const openAddToExistingModal = useModalComponentOpener(AddToExistingModal)

    const phoneOptions: DropdownOption[] =
        activeItem?.displayData?.phones?.map(phone => ({
            key: phone.id,
            value: phone.phone,
            type: phone.type,
        })) ?? []
    const emailOptions: DropdownOption[] =
        activeItem?.displayData?.emails?.map(email => ({
            key: email.id,
            value: email.emailAddress,
            type: email.type,
        })) ?? []

    const {handleContactOpen} = useContactsActions()
    const handleUse = useHandleUseContact()

    const activeChatId = useActiveChatInteractionId()
    assertDefined(activeChatId)
    const openDispositionPanel = useOpenDispositionPanel(activeChatId)

    function handleOpenDispositionPanel() {
        openDispositionPanel()
        hovercardOpened.setFalse()
    }

    function handleOpenContactPage() {
        if (!activeItem?.contactId) {
            console.error('No contactId found in active interaction')
            return
        }

        goTo(AppRoutes.search)
        handleContactOpen({
            id: activeItem.contactId,
            firstName: activeItem.displayData.firstName,
            lastName: activeItem.displayData.lastName,
        })
        hovercardOpened.setFalse()
    }

    assertDefined(activeItem)

    const onCall = useCallHandler()

    const openActivityHistory = useActivityHistoryOpener()

    const {selectedService} = useServiceState()
    const {selectedDIDNumber} = useDIDNumberState()

    assertDefined(activeItem)

    const {
        contactId,
        userId,
        groupChatId,
        displayData: {firstName, lastName, emiPhotoUrl, emiUserType},
    } = activeItem

    const isUndefinedContact = !contactId && !userId

    function handleCall(phone: string) {
        onCall({
            phone,
            contactId,
            userId,
            serviceId: selectedService,
            firstName,
            lastName,
            accessNumberId: selectedDIDNumber,
            origination: 'topbar',
        })
        hovercardOpened.setFalse()
    }

    function handleChat(phone: string) {
        openActivityHistory({
            contactId: activeItem?.contactId,
            userId: activeItem?.userId,
            phone,
            firstName: firstName,
            lastName: lastName,
            groupChatId,
            emiPhotoUrl,
            emiUserType,
            origination: 'topbar',
        })
        hovercardOpened.setFalse()
    }

    function handleEmail(_email: string) {
        hovercardOpened.setFalse()
        alert('TODO: Implement email')
    }

    function onContactCreated(createdContactId: string) {
        handleUse({
            id: createdContactId,
            firstName: firstName ?? '',
            lastName: lastName ?? '',
            phone: '',
            email: '',
        })
    }

    const addNewContact = useAddNewContact(activeItem, onContactCreated)

    function handleAddNewContact() {
        addNewContact()
        hovercardOpened.setFalse()
        return Promise.resolve()
    }

    const {createContact} = useContactsStateMethods()

    function handleQuickAdd() {

        function getPrimaryPhoneNumber(): string | undefined {
            if (isWorkItem(activeItem)) {
                return activeItem?.workitemData?.suggestedPhone
            }
            return activeItem?.displayData?.number
        }

        openQuickAddModal({
            defaultValues: {
                firstName,
                lastName,
                email: activeItem?.displayData?.email,
                phone: getPrimaryPhoneNumber(),
            },
            onSubmit: contact => {
                createContact(
                    {
                        contact: {
                            first_name: contact.firstName,
                            last_name: contact.lastName,
                            emails: contact.email
                                ? [
                                    {
                                        type: EmailTypes.PRIMARY,
                                        email_address: contact.email,
                                    },
                                ]
                                : [],
                            phones: contact.phone
                                ? [
                                    {
                                        type: PhoneTypes.BUSINESS,
                                        phone: contact.phone,
                                    },
                                ]
                                : [],
                        },
                    },
                    {
                        onSuccess(id: string) {
                            goTo(AppRoutes.search)
                            handleContactOpen({
                                id,
                                firstName: contact.firstName,
                                lastName: contact.lastName,
                            })
                            handleUse({
                                id,
                                firstName: contact.firstName,
                                lastName: contact.lastName,
                                email: contact.email,
                                phone: contact.phone,
                            })
                        },
                    },
                )
            },
        })
        hovercardOpened.setFalse()
    }

    function handleAddToExisting() {
        openAddToExistingModal({
            handleAddNewContact,
            handleAddToExisting: handleUse,
        })
        hovercardOpened.setFalse()
    }

    return (
        <HoverCard.Root
            open={hovercardOpened.value}
            onOpenChange={hovercardOpened.toggle}
        >
            <HoverCard.Trigger asChild>
                <div
                    ref={primaryContentRef}
                    className={tcn(
                        'ml-2 flex items-center gap-2 overflow-hidden',
                        className,
                    )}
                >
                    <InteractionBarAvatar />
                    <div className="truncate">
                        <div className="flex">
                            <InteractionBarDisplayName />
                        </div>
                        {canHaveDispositions && dispositionText && (
                            <p className="-mt-0.5 truncate caption-s text-neutral-fade">
                                {dispositionText}
                            </p>
                        )}
                    </div>
                </div>
            </HoverCard.Trigger>
            <HoverCard.Portal forceMount>
                <HoverCard.Content
                    forceMount
                    align="start"
                    style={{
                        transform: `translate(-9px, -${
                            primaryContentHeight + VERTICAL_PADDING
                        }px)`,
                    }}
                    className={tcn(
                        'z-[1] flex w-[464px] flex-col border bg-neutral-0  pt-2 shadow-4 border-default',
                        'pointer-events-none invisible size-0 opacity-0 transition-opacity',
                        {
                            'pointer-events-auto visible size-auto opacity-100':
                                hovercardOpened.value,
                        },
                    )}
                >
                    <div
                        className={tcn(
                            'ml-2 flex items-start gap-2 pb-2 pr-2',
                            className,
                        )}
                    >
                        <InteractionBarAvatar className="mt-[2.5px]" />
                        <div className="flex-auto truncate">
                            <div className="flex justify-between">
                                {activeItem?.contactId ? (
                                    <InteractionBarDisplayName
                                        onClick={handleOpenContactPage}
                                        as="button"
                                        className="inline hover:underline"
                                    />
                                ) : (
                                    <InteractionBarDisplayName />
                                )}
                                <InteractionFavoriteButton
                                    interaction={activeItem}
                                />
                            </div>
                            {canHaveDispositions && dispositionText && (
                                <button
                                    onClick={handleOpenDispositionPanel}
                                    className="-mt-0.5 block whitespace-normal p-0 caption-s text-neutral-fade hover:underline"
                                    title={dispositionText}
                                >
                                    {dispositionText}
                                </button>
                            )}
                        </div>
                    </div>
                    <div
                        className={tcn(
                            'flex flex-auto gap-2 border-t p-4 border-default',
                            {'pb-2': isUndefinedContact},
                        )}
                    >
                        <InteractionButton
                            onClick={handleCall}
                            OptionComponent={PhoneOption}
                            icon={<IconPhone className="svg-color-current" />}
                            items={phoneOptions}
                            align="start"
                            text={t.INTERACTIONS.call()}
                        />
                        <InteractionButton
                            onClick={handleChat}
                            OptionComponent={PhoneOption}
                            icon={<IconMessage className="svg-color-current" />}
                            text={t.INTERACTIONS.message()}
                            items={phoneOptions}
                            align="center"
                        />
                        <InteractionButton
                            onClick={handleEmail}
                            OptionComponent={EmailOption}
                            text={t.INTERACTIONS.email()}
                            icon={<IconEmailEnvelope24 className="svg-color-current" />}
                            items={emailOptions}
                            align="end"
                        />
                    </div>
                    {isUndefinedContact && (
                        <div className="flex gap-1 px-4 pb-2">
                            <Button
                                size="xs"
                                variant="ghost"
                                onClick={handleQuickAdd}
                            >
                                {t.INTERACTIONS.quick_add()}
                            </Button>
                            <Button
                                size="xs"
                                variant="ghost"
                                onClick={handleAddNewContact}
                            >
                                {t.INTERACTIONS.new_contact()}
                            </Button>
                            <Button
                                size="xs"
                                variant="ghost"
                                onClick={handleAddToExisting}
                            >
                                {t.INTERACTIONS.add_to_existing()}
                            </Button>
                        </div>
                    )}
                </HoverCard.Content>
            </HoverCard.Portal>
        </HoverCard.Root>
    )
}
