import React from 'react'

import {SmModal} from '@bpinc/lib-external-ui-elements'
import {Help} from '@bpinc/lib-help-ui'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


export const HelpModal: React.FC<CommonModalProps> = ({onClose, ...restProps}) => {
    const {t} = useLocalizationState()

    return (
        <Modal
            size="small"
            onClose={onClose}
            aria-labelledby="helpModal"
            data-testid="help-modal"
            {...restProps}
        >
            <SmModal.Header id="helpModal" role="heading">
                {t.GLOBAL.help()}
            </SmModal.Header>
            <SmModal.Content>
                <Help />
            </SmModal.Content>
            <SmModal.Actions>
                <SemanticButton
                    enabledInOffline
                    onClick={() => onClose()}
                    data-testid="cancel-button"
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </SmModal.Actions>
        </Modal>
    )
}
