import type {ReactNode} from 'react'
import React from 'react'


interface CaseFooterProps {
    children: ReactNode
}

export const CaseFooter: React.FC<CaseFooterProps> = props => {

    return (
        <footer className="mx-2 flex rounded-t border border-main-border p-2">
            {props.children}
        </footer>
    )
}
