import type {FC} from 'react'
import {Button} from '@servicepattern/ui'

import {MAIN_CONTENT_MIN_WIDTH, MIN_KB_WIDTH, useMainPageUIState} from '@bpinc/ad-main-page-ui-context'
import type {KBFeatures} from '@bpinc/kb-types'
import {IconChevronLeft16, IconChevronRight16, IconPin20, IconPinFilled20} from '@bpinc/lib-jskit-ui'
import type {OnDragEndParams} from '@bpinc/lib-ui-elements'
import {createComponentLoader, Floating, useCanPin} from '@bpinc/lib-ui-elements'
import {assertKBBridgeInterface, useKbBridge, useKbBridgeConnector} from '@bpinc/state-kb-bridge-provider'
import {useLocalizationState} from '@bpinc/state-localization-context'


const {KBPageLoader} = createComponentLoader('knowledgeBase', import('knowledgeBase'), 'KBPage')

const features: Partial<KBFeatures> = {
    autoOpenInteractionTab: true,
    dualView: false,
    multiTabs: true,
    topHeader: true,
}

export const RightBarKB: FC = () => {
    const {t} = useLocalizationState()
    const {
        kbFloatingState,
        kbWidth,
        mainContentNode,
        setKBFloatingState,
        setKBWidth,
    } = useMainPageUIState(['kbFloatingState', 'kbWidth', 'setKBFloatingState', 'setKBWidth', 'mainContentNode'])

    const canPin = useCanPin({
        mainContentNode,
        floatingState: kbFloatingState,
        floatingSize: kbWidth,
        contentMinWidth: MAIN_CONTENT_MIN_WIDTH,
    })

    const onDragEnd = ({size}: OnDragEndParams) => {
        setKBWidth(size)
    }

    const toggleFloatingState = () => {
        setKBFloatingState(kbFloatingState === 'closed' ? 'floating' : 'closed')
    }
    const togglePinState = () => {
        setKBFloatingState(kbFloatingState === 'floating' ? 'pinned' : 'floating')
    }

    const pinButton = (
        <Button
            flat
            icon
            onClick={togglePinState}
            className="ml-2"
            tooltip={kbFloatingState === 'floating'
                ? t.KNOWLEDGE_BASE.pin_tooltip()
                : t.KNOWLEDGE_BASE.unpin_tooltip()
            }
            tooltipPlacement="bottom-end"
            variant="ghost"
        >
            {kbFloatingState === 'pinned' ? <IconPinFilled20 /> : <IconPin20 />}
        </Button>
    )

    const EarIcon = kbFloatingState === 'closed'
        ? IconChevronLeft16
        : IconChevronRight16

    const kbSettings = useKbBridge('settings')
    const kbBridge = useKbBridgeConnector()
    assertKBBridgeInterface(kbBridge)

    if (!kbSettings?.kbEnabled) {
        return null
    }

    return (
        <Floating
            canPin={canPin}
            className="h-full"
            closeByEscape={kbFloatingState === 'floating'}
            data-testid="kb-panel"
            exceptionSelectors=".tox"
            earIcon={EarIcon}
            earClassName="top-6 h-10 w-8"
            initialSize={kbWidth}
            minSize={MIN_KB_WIDTH}
            onDragEnd={onDragEnd}
            resizeable
            setState={toggleFloatingState}
            side="right"
            state={kbFloatingState}
        >
            <KBPageLoader
                features={features}
                kbBridge={kbBridge}
                pinButton={pinButton}
            />
        </Floating>
    )
}
