import type {MouseEvent} from 'react'
import {useCallback, useMemo} from 'react'

import {useSessionState, useSessionStateMethods} from '@bpinc/ad-session-context'
import type {Toggle} from '@bpinc/lib-helpers'
import {
    useOpenAboutModal,
    useOpenAudioDevicesModal,
    useOpenCalendarSettingsModal,
    useOpenChangePasswordModal,
    useOpenEMIAccountsModal,
    useOpenKeyboardShortcutsModal,
    useOpenLogoutModal,
    useOpenPredefinedMessagesModal,
    useOpenPreferencesModal,
    useOpenPrerecordedMessagesModal,
    useOpenUserProfileModal,
    useOpenVolumeModal,
} from '@bpinc/lib-modals-ui'
import type {PopupMenuOption} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useOpenKBSettingsModal} from './useOpenKBSettingsModal'


export function useSettingsItems(toggleShow: Toggle): PopupMenuOption[] {
    const {t} = useLocalizationState()
    const {
        privileges,
        phoneType,
        tenantFeatures,
    } = useSessionState(['privileges', 'phoneType', 'tenantFeatures'])
    const {startPhoneTypeSelection} = useSessionStateMethods()
    const openChangePasswordModal = useOpenChangePasswordModal()
    const openEMIAccountsModal = useOpenEMIAccountsModal()
    const openUserProfileModal = useOpenUserProfileModal()
    const openKBSettingsModal = useOpenKBSettingsModal()
    const openKeyboardShortcutsModal = useOpenKeyboardShortcutsModal()
    const openPredefinedMessagesModal = useOpenPredefinedMessagesModal()
    const openVolumeModal = useOpenVolumeModal()
    const openPreferencesModal = useOpenPreferencesModal()
    const openAboutModal = useOpenAboutModal()
    const openAudioDevicesModal = useOpenAudioDevicesModal()
    const openCalendarSettingsModal = useOpenCalendarSettingsModal()
    const openPrerecordedMessagesModal = useOpenPrerecordedMessagesModal()
    const openLogoutModal = useOpenLogoutModal()

    const canManageKBSettings = !!privileges.MANAGE_KNOWLEDGE_BASE_SETTINGS

    const isAudioDevicesLinkVisible =
        !!phoneType && ['phone_type_browser', 'phone_type_soft_secure', 'phone_type_soft_unsecure'].includes(phoneType)

    const clickHandler = useCallback((handler: () => void) => (evt: MouseEvent) => {
        evt.stopPropagation()
        handler()
        toggleShow()
    }, [toggleShow])

    return useMemo(() => {
        const options: PopupMenuOption[] = [
            {
                children: t.SETTINGS.settings_menu_userProfile(),
                onClick: clickHandler(openUserProfileModal),
            },
            {
                children: t.DEVICE_SETUP_WINDOW.deviceSetup_heading(),
                onClick: clickHandler(startPhoneTypeSelection),
            },
            {
                children: t.SETTINGS.settings_menu_audioDevices(),
                onClick: clickHandler(openAudioDevicesModal),
            },
            {
                children: t.CALENDAR_SETTINGS.heading(),
                onClick: clickHandler(openCalendarSettingsModal),
            },
            {
                children: t.SETTINGS.settings_menu_changePassword(),
                onClick: clickHandler(() => openChangePasswordModal({isForced: true})),
            },
            {
                children: t.SETTINGS.settings_menu_chatCannedResponses(),
                onClick: clickHandler(openPredefinedMessagesModal),
            },
            {
                children: t.SETTINGS.settings_menu_preferences(),
                onClick: clickHandler(openPreferencesModal),
            },
            {
                children:
                    t.UNDEFINED.tab_enterprise_messaging_accounts_heading(),
                onClick: clickHandler(openEMIAccountsModal),
            },
            {
                children: t.KNOWLEDGE_BASE.kb_settings(),
                onClick: clickHandler(openKBSettingsModal),
            },
            {
                children: t.SETTINGS.settings_menu_editVoicePrompts(),
                onClick: clickHandler(openPrerecordedMessagesModal),
            },
            {
                children: t.SETTINGS.settings_menu_shortcuts(),
                onClick: clickHandler(openKeyboardShortcutsModal),
            },
            {
                children: t.SETTINGS.settings_menu_ringtone_volume(),
                onClick: clickHandler(openVolumeModal),
            },
            {
                children: t.SETTINGS.settings_menu_about(),
                onClick: clickHandler(openAboutModal),
            },
            {
                children: t.GLOBAL.logout(),
                onClick: clickHandler(openLogoutModal),
            },
        ]
        const checkKBSettingsPrivilege = !canManageKBSettings ? options.filter(({children}: PopupMenuOption) => {
            return children !== t.KNOWLEDGE_BASE.kb_settings()
        }) : options
        const checkAudioDevices = !isAudioDevicesLinkVisible ? checkKBSettingsPrivilege.filter(({children}: PopupMenuOption) => {
            return children !== t.SETTINGS.settings_menu_audioDevices()
        }) : checkKBSettingsPrivilege
        return !tenantFeatures?.msTeamsIntegrationEnabled ? checkAudioDevices.filter(({children}: PopupMenuOption) => {
            return children !== t.UNDEFINED.tab_enterprise_messaging_accounts_heading()
        }) : checkAudioDevices
    },
    [
        t,
        clickHandler,
        openUserProfileModal,
        startPhoneTypeSelection,
        openAudioDevicesModal,
        openCalendarSettingsModal,
        openChangePasswordModal,
        openPredefinedMessagesModal,
        openPreferencesModal,
        openEMIAccountsModal,
        openPrerecordedMessagesModal,
        openKeyboardShortcutsModal,
        openVolumeModal,
        openAboutModal,
        openLogoutModal,
        openKBSettingsModal,
        isAudioDevicesLinkVisible,
        tenantFeatures?.msTeamsIntegrationEnabled,
        canManageKBSettings,
    ])
}
