import React, {useEffect} from 'react'
import {ReactQueryDevtools} from '@tanstack/react-query-devtools'
import {useAtom} from 'jotai'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useLocalStorageForState} from '@bpinc/ad-local-settings-context'

import {queueTableWidthAtom} from './atoms'
import {CasePageContent} from './CasePageContent'
import {JotaiProvider} from './JotaiProvider'


export const CasePage: React.FC = () => {
    const [queueTableWidth, setQueueTableWidth] = useAtom(queueTableWidthAtom)
    useLocalStorageForState(
        queueTableWidth,
        'adQueueTableWidth',
        setQueueTableWidth,
        queueTableWidth,
    )

    const {getCasePendingReasons} = useCaseStateMethods()

    useEffect(function onDidMount() {
        getCasePendingReasons()
    }, [getCasePendingReasons])

    return (
        <JotaiProvider>
            <CasePageContent />
            <ReactQueryDevtools initialIsOpen={false} buttonPosition="bottom-left" />
        </JotaiProvider>
    )
}
