import React, {type FC, useCallback} from 'react'
import {useAtomValue} from 'jotai'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {personalActiveQueueItemAtom} from '../atoms'
import {ViewButton} from '../ViewButton'


interface CaseFooterContentProps {
    canViewCase?: boolean
}

export const PersonalCaseFooterContent: FC<CaseFooterContentProps> = ({
    canViewCase,
}) => {
    const queueItem = useAtomValue(personalActiveQueueItemAtom)
    const {caseId, queueId, queueType} = queueItem ?? {}
    const {t} = useLocalizationState()
    const {openCase} = useCaseStateMethods()

    const handleOpen = useCallback(
        function handleOpenCallback() {
            assertDefined(queueId)
            assertDefined(caseId)

            openCase({
                caseId,
                queueId,
                queueType,
            })
        },
        [queueId, caseId, openCase, queueType],
    )


    return (
        <>
            {queueItem && canViewCase && <ViewButton queueItem={queueItem} />}
            <SemanticButton basic primary onClick={handleOpen}>
                {t.EMAIL.email_open_button()}
            </SemanticButton>
        </>
    )
}
