import {useEffect, useRef} from 'react'

import {useEvaluationStateMethods} from '@bpinc/ad-evaluation-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {MS_IN_MINUTE} from '@bpinc/lib-helpers'


const REFRESH_STATISTICS_INTERVAL = 30 * MS_IN_MINUTE

export function useRefreshEvalStatistics() {
    const {sessionId} = useSessionState()
    const {getEvalsStatistics} = useEvaluationStateMethods()
    const intervalId = useRef<number>()

    useEffect(() => {
        if (!sessionId) {
            return
        }

        getEvalsStatistics(sessionId)
        intervalId.current = setInterval(() => {
            getEvalsStatistics(sessionId)
        }, REFRESH_STATISTICS_INTERVAL)
        return () => clearInterval(intervalId.current)
    }, [sessionId, getEvalsStatistics])
}
