import type {ChangeEvent, FC} from 'react'
import React, {useCallback, useState} from 'react'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {SimpleInput} from '@bpinc/lib-ui-elements'


interface CaseTitleInputProps {
    initialTitle: string
    caseId: string
}

export const CaseTitleInput: FC<CaseTitleInputProps> = ({initialTitle, caseId}) => {
    const [title, setTitle] = useState(initialTitle)
    const {updateCaseTitle} = useCaseStateMethods()

    const handleChange = useCallback(function handleChangeCallback(e: ChangeEvent<HTMLInputElement>) {
        setTitle(e.target.value)
    }, [])

    const handleBlur = useCallback(
        function handleBlurCallback() {
            if (title !== initialTitle) {
                updateCaseTitle({caseId, caseTitle: title})
            }
        },
        [caseId, initialTitle, title, updateCaseTitle],
    )

    return (
        <SimpleInput
            value={title}
            onChange={handleChange}
            onBlur={handleBlur}
            className="-ml-px -mt-px [&:not(:focus):not(:hover)]:!ml-0 [&:not(:focus):not(:hover)]:!mt-0 [&:not(:focus):not(:hover)]:[&_input]:!border-none"
        />
    )
}
