import type {FC} from 'react'
import React from 'react'

import {useCaseState, useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {SetCaseStatusButton} from '@bpinc/ad-case-ui/src/components/CasePage/SetCaseStatusButton'
import {useActiveInteractionProp, useActiveInteractionStepId} from '@bpinc/ad-interaction-state-context'
import {getServiceId} from '@bpinc/ad-interaction-state-methods'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface ActiveInteractionCaseFooterContentrops {
    caseId: string
}

export const ActiveInteractionCaseFooterContent: FC<
    ActiveInteractionCaseFooterContentrops
> = ({caseId}) => {
    const caseItem = useCaseState(s =>
        caseId ? s.cases[caseId]?.data : undefined,
    )

    const {t} = useLocalizationState()

    const pendingReasonsId = caseItem?.pendingReasonId
    const caseStatus = caseItem?.status

    const activeInteractionStepId = useActiveInteractionStepId()
    const serviceId = useActiveInteractionProp(getServiceId)
    const {createEmailDraft} = useCaseStateMethods()

    if (!serviceId || !caseStatus) {
        return null
    }

    return (
        <div className="flex w-full flex-wrap justify-between gap-y-2">
            <div className="flex gap-2">
                <SemanticButton
                    icon="sticky note"
                    basic
                    tooltip={t.CASE_SEARCH.new_case_notes_button_tooltip()}
                />
                <SemanticButton
                    icon="envelope"
                    basic
                    tooltip={t.EMAIL.email_new_email_draft()}
                    onClick={() => {
                        createEmailDraft({interactionStepId: activeInteractionStepId})
                    }}
                />
            </div>
            <SetCaseStatusButton
                key={caseId}
                caseId={caseId}
                initialPendingReasonId={pendingReasonsId}
                service={serviceId}
                initialStatus={caseStatus}
            />
        </div>
    )
}
