import type {FC} from 'react'
import React from 'react'
import {getCoreRowModel, useReactTable} from '@tanstack/react-table'
import {useAtomValue} from 'jotai'

import type {QueueItem} from '@bpinc/ad-request-urls'

import {usePinnedCasesQuery} from '../../../queries'
import type {SpecificTabProps} from '../../types'
import {PAGINATION_CHUNK_SIZE, pinnedActiveQueueItemAtom, pinnedSortingAtom} from '../atoms'
import {CaseTab} from '../CaseTab'
import {useBasicColumns} from '../useBasicColumns'
import {PinnedCaseFooterContent} from './PinnedCaseFooterContent'
import {TableHeaderContent} from './TableHeaderContent'


const initialData: QueueItem[] = []

export const PinnedTab: FC<SpecificTabProps> = props => {
    const sorting = useAtomValue(pinnedSortingAtom)

    const {data, isFetched} = usePinnedCasesQuery({
        limit: PAGINATION_CHUNK_SIZE,
        sortBy: sorting.field,
        asc: sorting.direction === 'asc',
    })

    const noData = isFetched && !data?.length

    const table = useReactTable({
        columns: useBasicColumns(),
        data: data || initialData,
        getCoreRowModel: getCoreRowModel(),
        getRowId: row => row.caseId,
    })

    return (
        <CaseTab
            noSelection
            noData={noData}
            table={table}
            CaseFooterContent={<PinnedCaseFooterContent canViewCase />}
            TableHeaderContent={<TableHeaderContent noData={noData} />}
            activeQueueItemAtom={pinnedActiveQueueItemAtom}
            queueItems={data}
            {...props}
        />
    )
}
