import type {FC} from 'react'
import React, {useCallback, useEffect, useMemo, useState} from 'react'
import {flow} from 'lodash-es'

import {useSessionState} from '@bpinc/ad-session-context'
import {removeHttp, removeLastSlash} from '@bpinc/lib-helpers'
import type {TabI} from '@bpinc/lib-ui-elements'
import {Iframe, Tabs} from '@bpinc/lib-ui-elements'

import styles from '../styles/NonCallCases.module.less'


export const NonCallCases: FC = () => {
    const {nonCallCasesUrls} = useSessionState()

    const tabs = useMemo(
        function memoizeTabs() {
            return nonCallCasesUrls?.map(url => ({id: url, title: flow(removeLastSlash, removeHttp)(url)})) ?? []
        },
        [nonCallCasesUrls],
    )

    const [activeTabId, setActiveTabId] = useState<string>()

    const renderTabContent = useCallback(function renderTabCallback({id}: TabI<string>) {
        return <Iframe src={id} className={styles.iFrame} />
    }, [])

    useEffect(function initializeActiveTab() {
        if (tabs.length && !activeTabId) setActiveTabId(tabs[0].id)
    }, [activeTabId, tabs])

    return activeTabId ? (
        <Tabs
            data-testid="non-call-cases-page"
            tabs={tabs}
            activeTabId={activeTabId}
            onChange={setActiveTabId}
            renderTabContent={renderTabContent}
            className={styles.tabs}
            tabsContentClassName={styles.tabsContent}
        />
    ) : <div data-testid="non-call-cases-page" />
}
