import {useBPClientStateSubscription} from './useBPClientStateSubscription'
import {useCaseStateSubscription} from './useCaseStateSubscription'
import {useInteractionChatStateSubscription} from './useInteractionChatStateSubscription'
import {useInteractionStateSubscription} from './useInteractionStateSubscription'
import {useRemoteAssistStateSubscription} from './useRemoteAssistStateSubscription'


export function useStateSubscriptions() {
    useBPClientStateSubscription()
    useCaseStateSubscription()
    useInteractionChatStateSubscription()
    useInteractionStateSubscription()
    useRemoteAssistStateSubscription()
}
