import {type Dispatch, useEffect, useMemo} from 'react'

import {useInteractions, useModalInteractionIds} from '@bpinc/ad-interaction-state-context'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {usePrevious} from '@bpinc/lib-helpers'

import type {Action} from '../reducer'


export function useInteractionDispatchers(dispatch: Dispatch<Action>) {
    const items = useInteractions()
    const modalItemIds = useModalInteractionIds()

    const modalItems = useMemo(function getModalItems() {
        return modalItemIds.map(id => items.find(item => item.id === id)).filter(Boolean) as Item[]
    }, [items, modalItemIds])

    const prevModalItems = usePrevious(modalItems)
    const prevModalItemsLength = prevModalItems?.length ?? 0

    useEffect(() => {
        if (modalItems.length > prevModalItemsLength) {
            const lastModalItem = modalItems[modalItems.length - 1]
            dispatch({type: 'interactionAppeared', id:  lastModalItem.id, mediaType: lastModalItem.mediaType})
        } else if (modalItems.length < prevModalItemsLength) {
            const lastModalItem = prevModalItems?.[prevModalItems.length - 1]
            lastModalItem && dispatch({type: 'interactionDisappeared', id:  lastModalItem.id})
        }
    }, [dispatch, modalItems, modalItems.length, prevModalItems, prevModalItemsLength])
}
