import React, {useEffect, useMemo, useRef} from 'react'
import {useAtom, useAtomValue} from 'jotai'

import {useContactsState, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {useEvent, useEventListener} from '@bpinc/lib-helpers'
import {Loader} from '@bpinc/lib-ui-elements'

import {searchQueryAtom, selectedContactIdAtom} from '../atoms'
import {ContactActions} from './ContactActions'
import {ContactsTable} from './ContactsTable'


type ContactsProps = {
    searchId?: string
}

export const Contacts: React.FC<ContactsProps> = ({
    searchId,
}) => {
    const ref = useRef<HTMLDivElement>(null)
    const query = useAtomValue(searchQueryAtom)
    const [selectedContactId, setSelectedContactId] = useAtom(selectedContactIdAtom)
    const {search, contactList, contactListLoading} = useContactsState([
        'search',
        'contactList',
        'contactListLoading',
    ])
    const contactsStateMethods = useContactsStateMethods()

    const [list, isLoading] = useMemo(() => {
        if (searchId) {
            return [
                search[searchId]?.list ?? [],
                search[searchId]?.isLoading ?? true,
            ]
        }
        return [contactList, contactListLoading]
    }, [contactList, contactListLoading, search, searchId])

    const selectedContact = useMemo(
        () => list.find(it => it.id === selectedContactId),
        [list, selectedContactId],
    )

    useEffect(function subscribeToContacts() {
        const timeout = setTimeout(() => {
            contactsStateMethods.getContactList({query, searchId})
        }, 1000)

        return () => {
            timeout && clearTimeout(timeout)
        }
    }, [query, contactsStateMethods, searchId])

    useEffect(function resetSelectedContact() {
        // update selectedId when list changes
        if (selectedContactId) {
            const selectedIdIsStillInList = list.some(
                ({id}) => id === selectedContactId,
            )
            if (!selectedIdIsStillInList) {
                setSelectedContactId(list[0]?.id)
            }
        } else if (list.length) {
            setSelectedContactId(list[0]?.id)
        } else {
            setSelectedContactId(undefined)
        }
    }, [list, selectedContactId, setSelectedContactId])

    const handleKeyDown = useEvent((event: KeyboardEvent) => {
        const currentIndex = list.findIndex(it => it.id === selectedContactId)

        let selectedId = ''
        switch (event.key) {
            case 'ArrowUp':
                selectedId = (currentIndex === 0 || currentIndex === -1)
                    ? list[list.length - 1].id
                    : list[currentIndex - 1].id
                break
            case 'ArrowDown':
                selectedId = (currentIndex === list.length - 1 || currentIndex === -1)
                    ? list[0]?.id
                    : list[currentIndex + 1]?.id
                break
        }

        if (selectedId) {
            event.stopPropagation()
            event.preventDefault()

            setSelectedContactId(selectedId)

            const selectedRow = ref.current?.querySelector<HTMLTableRowElement>(`tr[data-id='${selectedId}']`)
            selectedRow?.scrollIntoView({block: 'center', behavior: 'smooth'})
            selectedRow?.focus()
        }
    })

    useEventListener('keydown', handleKeyDown)

    return (
        <div className="flex flex-1 flex-col overflow-hidden">
            <div className="flex flex-1 flex-col overflow-hidden border-b border-main-border" ref={ref}>
                <ContactsTable
                    data={list}
                    selectedContactId={selectedContactId}
                />
            </div>
            <Loader active={isLoading} color="primary" size="huge" />
            <ContactActions
                selectedContact={selectedContact}
            />
        </div>
    )
}
