import React, {useMemo, useState} from 'react'

import {useServicesDispositions, useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import {truthy} from '@bpinc/lib-types-helpers'
import {Dropdown, Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


type SetDispositionModalProps = CommonModalProps & {
    service: string
    onSave: (disposition: { id: string; name: string } | undefined) => void
}

export const SetDispositionModal: React.FC<SetDispositionModalProps> = ({
    onClose,
    service,
    onSave,
    ...restProps
}) => {
    const {t} = useLocalizationState()
    const dispositions = useServicesDispositions()[service]
    const dispositionIsRequired = useServicesDispositionsState(
        s => s.services[service].requireDispositions,
    )

    const dipsositionOptions = useMemo(() => {
        return truthy([
            dispositionIsRequired && {
                text: t.DISPOSITION.disposition_not_selected(),
                value: '',
            },
            ...Object.values(dispositions ?? {})
                .filter(d => d.type === 'SUCCESS' && d.group === 'Final')
                .map(d => ({
                    text: d.name,
                    value: d.id,
                })),
        ])
    }, [dispositionIsRequired, dispositions, t.DISPOSITION])

    const [selectedDisposition, setSelectedDisposition] = useState(dipsositionOptions[0]?.value)

    const handleSubmit = () => {
        const disposition = selectedDisposition
            ? dispositions[selectedDisposition]
            : undefined

        onSave(
            disposition && {
                id: disposition.id,
                name: disposition.name,
            },
        )
        onClose()
    }

    return (
        <Modal
            onClose={onClose}
            className="relative max-w-90 overflow-hidden"
            aria-labelledby="set_disposition_modal"
            data-testid="SetDispositionModal"
            {...restProps}
        >
            <Modal.Header role="heading" id="set_disposition_modal">
                {t.GLOBAL.select_disposition_dialog_header()}
            </Modal.Header>
            <Modal.Content>
                <Dropdown
                    options={dipsositionOptions}
                    value={selectedDisposition}
                    onChange={(_evt, data) =>
                        typeof data.value === 'string' &&
                        setSelectedDisposition(data.value)
                    }
                    placeholder={t.DISPOSITION.disposition_selectDisposition()}
                />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={() => onClose()}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    primary
                    onClick={handleSubmit}
                    disabled={dispositionIsRequired && !selectedDisposition}
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
