import React, {useEffect, useRef, useState} from 'react'
import type {UseFormReturn} from 'react-hook-form'

import {useContactFormDefinition, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactDetailsType} from '@bpinc/ad-contacts-state-types'
import {FormBuilder} from '@bpinc/ad-form-builder'
import {createFormValues} from '@bpinc/lib-form-builder'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {CreateActions} from './Details/CreateActions'
import {ProfileHeaderForm} from './ProfileHeader/ProfileHeaderForm'


interface Props {
    className?: string
    onCancel: () => void
    initialData?: ContactDetailsType
    onContactCreated?: (contactId: string) => void
}

export const ProfileCreate: React.FC<Props> = ({className, onCancel, initialData, onContactCreated}) => {
    const {
        getContactFormsDefinitions,
    } = useContactsStateMethods()

    useEffect(() => {
        getContactFormsDefinitions({})
    }, [getContactFormsDefinitions])

    const actionsRef = useRef<HTMLDivElement>(null)
    const [form, setForm] = useState<UseFormReturn | null>(null)

    const formDefinition = useContactFormDefinition('CONTACT_DETAIL')
    const defaultValues = formDefinition && initialData && createFormValues(formDefinition, initialData)

    if (!formDefinition) {
        return null
    }

    return (
        <div className={tcn('flex h-full flex-auto flex-col', className)}>
            <ProfileHeaderForm actionsRef={actionsRef} />

            <div className="relative flex flex-auto flex-col overflow-auto">
                <CreateActions
                    actionsRef={actionsRef}
                    closeCreateTab={onCancel}
                    form={form}
                    onContactCreated={onContactCreated}
                />
                <FormBuilder
                    formDefinition={formDefinition}
                    onFormAvailable={setForm}
                    defaultValues={defaultValues}
                />
            </div>
        </div>
    )
}
