import type {FC} from 'react'
import React, {useEffect} from 'react'

import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import type {DropdownProps} from '@bpinc/lib-ui-elements'
import {Dropdown} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useEmailServicesOptions} from '../useEmailServicesOptions'


interface ServiceSelectorProps {
    serviceId: string
    itemId: string
    emailId: string
}

export const ServiceSelector: FC<ServiceSelectorProps> = ({itemId, emailId, serviceId}) => {
    const {t} = useLocalizationState()

    const [localServiceId, setLocalServiceId] = React.useState(serviceId)

    useEffect(() => {
        setLocalServiceId(serviceId)
    }, [serviceId])

    const options = useEmailServicesOptions()

    const {changeEmailService} = useInteractionStateMethods()

    const handleChange = (_e: React.SyntheticEvent, {value}: DropdownProps) => {
        if (typeof value !== 'string') {
            throw Error('Invalid type in email service selector')
        }
        setLocalServiceId(value)
        changeEmailService({serviceId: value, emailId, itemId})
    }

    return (
        <Dropdown
            options={options}
            value={localServiceId}
            onChange={handleChange}
            placeholder={t.GLOBAL.service_selector_no_value()}
            selectOnBlur={false}
            data-testid="service-selector"
            selection
        />
    )
}
