import {useMemo} from 'react'

import {useContactFormDefinition} from '@bpinc/ad-contacts-state-context'
import {checkIsLayout, createFormElement} from '@bpinc/lib-form-builder'


export function useMergeFormElements() {
    const formDefinition = useContactFormDefinition('CONTACT_DETAIL')

    return useMemo(() => {
        if (!formDefinition) {
            return []
        }

        const formElements = formDefinition.layout.pages.map(page => page.fields).flat()
        const formFields = formElements.filter(field => !checkIsLayout(field.field))
        return formFields?.map(createFormElement) ?? []
    }, [formDefinition])
}
