import type {EmailAddress} from '@bpinc/ad-request-urls'
import {sanitizeHtml} from '@bpinc/lib-html-utils'

import {EmailAddressValidator} from './email-address-validator'

/** This is a transformed to TS copy of one we have in the old AD */
export class EmailAddressTransformers {
    static LABEL(emailAddress: EmailAddress): string {
        let result = ''
        if (emailAddress) {
            const {address, name} = emailAddress
            if (name && name !== address) {
                result += name
                if (address) {
                    result += ` <${address}>`
                }
            } else if (address) {
                result += address
            }
        }
        return result
    }

    static LABEL2(emailAddress: EmailAddress): string {
        if (!emailAddress) {
            return ''
        }

        const {name, address} = emailAddress
        const displayString = !name
            ? address
            : name.toLowerCase() === address.toLowerCase()
                ? address
                : `${name} (${address})`

        if (EmailAddressValidator.validate(address)) {
            return sanitizeHtml(
                `<span data-email='${address}'>${displayString}</span>`,
            )
        } else {
            return sanitizeHtml(
                `<span style='border-bottom: 1px dotted red; border-top: 1px solid transparent;' title='${displayString}'>${displayString}</span>`,
            )
        }
    }
}
