import type {FC, SyntheticEvent} from 'react'
import React, {useCallback} from 'react'
import {useAtom} from 'jotai'

import type {CasesSortBy} from '@bpinc/ad-request-urls'
import {type DropdownProps, Dropdown, SemanticButton} from '@bpinc/lib-ui-elements'

import {casesSortingAtom} from '../atoms'
import {useCaseOrderOptions} from './useCaseOrderOptions'


export const CasesSorting: FC = () => {
    const options = useCaseOrderOptions()
    const [sorting, setSorting] = useAtom(casesSortingAtom)

    const handleSortingDirectionChange = useCallback(
        function handleSortingDirectionChangeCallback() {
            setSorting(s => ({
                ...s,
                asc: !s.asc,
            }))
        },
        [setSorting],
    )

    const handleSortingFieldChange = useCallback(
        (_evt: SyntheticEvent, data: DropdownProps) => {
            setSorting(s => {
                if (typeof data.value !== 'string') {
                    return s
                }

                return {
                    ...s,
                    asc: data.value === 'score' ? false : s.asc,
                    sortBy: data.value as CasesSortBy,
                }
            })
        },
        [setSorting],
    )

    return (
        <div className="flex items-center gap-0.5">
            <Dropdown
                inline
                options={options}
                onChange={handleSortingFieldChange}
                value={sorting.sortBy}
                hideIcon
            />
            <SemanticButton
                transparent
                icon={sorting.asc ? 'caret up' : 'caret down'}
                className="[&>i]:flex [&>i]:items-center [&>i]:justify-center [&>i]:text-[18px]"
                onClick={handleSortingDirectionChange}
                disabled={sorting.sortBy === 'score'}
                basic
                size="mini"
            />
        </div>
    )
}
