import type {FC} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {TableItem} from '@bpinc/lib-ui-elements'

import {IconChat, IconLaptop, IconTranscript, IconVoiceRecording} from '../../../icons'


enum RecordingValue {
    VOICE_RECORDING = 'hasVoiceRecording',
    SCREEN_RECORDING = 'hasScreenRecording',
    TRANSCRIPT = 'hasTranscript',
    CHAT_TRANSCRIPT = 'hasChatTranscript',
    ERASED_VOICE = 'erasedVoiceRecording',
    ERASED_CHAT = 'erasedChatTranscript',
    ERASED_SCREEN = 'erasedScreenRecording',
}

interface RecordingCellProps {
    item: TableItem
}

export const RecordingCell: FC<RecordingCellProps> = React.memo(({item}) => {
    const iconClassName = tcn('flex h-6 w-6 items-center justify-center')

    return (
        <div className="flex h-14 items-center gap-x-1.5">
            {!!item?.[RecordingValue.VOICE_RECORDING] && (
                <div className={iconClassName}>
                    <IconVoiceRecording
                        className={tcn({'[&_path]:fill-primary-legacy': !item?.[RecordingValue.ERASED_VOICE]})}
                    />
                </div>
            )}
            {!!item?.[RecordingValue.SCREEN_RECORDING] && (
                <div className={iconClassName}>
                    <IconLaptop
                        className={tcn({'[&_path]:fill-primary-legacy': !item?.[RecordingValue.ERASED_SCREEN]})}
                    />
                </div>
            )}
            {!!item?.[RecordingValue.CHAT_TRANSCRIPT] && (
                <div className={iconClassName}>
                    <IconChat className={tcn({'[&_path]:fill-primary-legacy': !item?.[RecordingValue.ERASED_CHAT]})} />
                </div>
            )}
            {!!item?.[RecordingValue.TRANSCRIPT] && (
                <div className={iconClassName}>
                    <IconTranscript className="[&_path]:fill-primary-legacy" />
                </div>
            )}
        </div>
    )
})
