import type {FC} from 'react'
import React from 'react'

import {Activities} from '@bpinc/ad-activities-ui'
import type {TabI} from '@bpinc/lib-ui-elements'

import {Details} from './Details'


interface Props {
    contactId: string
    tab: TabI<string>
    editMode?: boolean
    actionsRef: React.RefObject<HTMLDivElement>
    onMerge?: () => void
    onEditMode?: (edit: boolean) => void
}

export const ProfileTabContent: FC<Props> = ({
    contactId,
    tab,
    editMode,
    actionsRef,
    onMerge,
    onEditMode,
}) => {
    if (tab.id === 'details') {
        return (
            <Details
                contactId={contactId}
                editMode={editMode}
                actionsRef={actionsRef}
                onMerge={onMerge}
                onEditMode={onEditMode}
            />
        )
    }

    if (tab.id === 'activities') {
        return <Activities contactId={contactId} />
    }

    return <div>{`Content of "${tab.title}" tab`}</div>
}
