import type {MouseEvent} from 'react'
import React, {useCallback, useEffect, useMemo, useState} from 'react'

import {useEvaluationState, useEvaluationStateMethods} from '@bpinc/ad-evaluation-state-context'
import type {
    NodeFolder,
    NodeLeaf,
    TreeElem,
    TreeElemsMap,
    TreeProps,
} from '@bpinc/ad-evaluation-state-types'
import {Tree} from '@bpinc/lib-ui-elements'

import {useAllSearchesStorageControls} from '../../hooks/useAllSearchesStorageControls'
import {useTreeModalsController} from './hooks/useTreeModalsController'
import {Folder} from './Folder'
import {RootFolder} from './RootFolder'
import {Search} from './Search'


export const SavedSearchesTree = ({
    elements,
    personalRootId,
    publicRootId,
    onDoubleClickSearch,
}: TreeProps) => {
    const {
        getStorageOpenNodesPersonal,
        getStorageOpenNodesPublic,
        setStorageOpenNodesPersonal,
        setStorageOpenNodesPublic,
    } = useAllSearchesStorageControls()

    const selectedSearchId = useEvaluationState('selectedSearchId')
    const {setSelectedSearch} = useEvaluationStateMethods()

    const [openedPersonal, setOpenedPersonal] = useState<string[]>(() => getStorageOpenNodesPersonal())
    const [openedPublic, setOpenedPublic] = useState<string[]>(() => getStorageOpenNodesPublic())
    const [convertedElements, setConvertedElements] = useState<TreeElemsMap>({})

    const {
        removeElem,
        addFolder,
        addSearch,
    } = useTreeModalsController(convertedElements)

    const toggleNodePersonal = useCallback((nodeId: string) => {
        let newOpened
        if (openedPersonal.includes(nodeId)) {
            newOpened = openedPersonal.filter(id => id !== nodeId)
        } else {
            newOpened = [...openedPersonal, nodeId]
        }
        setOpenedPersonal(newOpened)
        setStorageOpenNodesPersonal(newOpened)
    }, [openedPersonal, setOpenedPersonal, setStorageOpenNodesPersonal])

    const toggleNodePublic = useCallback((nodeId: string) => {
        let newOpened
        if (openedPublic.includes(nodeId)) {
            newOpened = openedPublic.filter(id => id !== nodeId)
        } else {
            newOpened = [...openedPublic, nodeId]
        }
        setOpenedPublic(newOpened)
        setStorageOpenNodesPublic(newOpened)
    }, [openedPublic, setOpenedPublic, setStorageOpenNodesPublic])

    useEffect(() => {
        const convertedElems = elements.reduce<TreeElemsMap>((acc, element) => {
            acc[element.id] = {
                id: element.id,
                name: element.name,
                isLeaf: element.type === 'QUERY',
                parentId: element.parentId,
                privacy: element.privacy,
            }
            return acc
        }, {})

        setConvertedElements(convertedElems)
    }, [elements])

    const personalRoot = useMemo(() => {
        return convertedElements[personalRootId] || null
    }, [personalRootId, convertedElements])

    const publicRoot = useMemo(() => {
        return convertedElements[publicRootId] || null
    }, [publicRootId, convertedElements])

    const openTreeChildren = useCallback((nodeId: string) => {
        return Object.keys(convertedElements).reduce<TreeElem[]>((acc, elemId) => {
            const elem = convertedElements[elemId]
            if (elem?.parentId === nodeId) {
                return [...acc, elem]
            }
            return acc
        }, []).sort((a, b) => {
            return Number(a.isLeaf) - Number(b.isLeaf)
        })

    }, [convertedElements])

    //@todo: speak about renaming items with project managers
    // there are no backend, and probably its not necessary
    const renameElem = (elemId: string) => {}

    const isChildOf = useCallback((possibleParentId: string, elementId: string) => {
        let elemParentId = convertedElements[elementId].parentId
        while (elemParentId) {
            if (elemParentId === possibleParentId) {
                return true
            }
            elemParentId = convertedElements[elemParentId]?.parentId || ''
        }
        return false
    }, [convertedElements])

    const renderNonLeafNode = useCallback((nodeData: NodeFolder, handleNodeIconClick: (event: MouseEvent<HTMLDivElement>) => void) => {
        const privacy = convertedElements[nodeData.id].privacy || 'PERSONAL'
        if (nodeData.id === personalRootId || nodeData.id === publicRootId) {
            return (
                <RootFolder
                    nodeData={nodeData}
                    handleNodeIconClick={handleNodeIconClick}
                    addFolder={addFolder}
                    addSearch={addSearch}
                    privacy={privacy}
                    isChildOf={isChildOf}
                />
            )
        }
        return (
            <Folder
                nodeData={nodeData}
                handleNodeIconClick={handleNodeIconClick}
                addFolder={addFolder}
                addSearch={addSearch}
                removeElem={removeElem}
                renameElem={renameElem}
                privacy={privacy}
                isChildOf={isChildOf}
            />
        )
    }, [addFolder, addSearch, removeElem, renameElem, personalRootId, publicRootId, convertedElements, isChildOf])

    const renderLeafNode = useCallback((nodeData: NodeLeaf) => {
        const privacy = convertedElements[nodeData.id].privacy || 'PERSONAL'
        return (
            <Search
                nodeData={nodeData}
                selected={nodeData.id === selectedSearchId}
                onSelect={setSelectedSearch}
                removeElem={removeElem}
                renameElem={renameElem}
                onDoubleClick={onDoubleClickSearch}
                isChildOf={isChildOf}
                privacy={privacy}
            />
        )
    }, [
        removeElem,
        renameElem,
        onDoubleClickSearch,
        convertedElements,
        isChildOf,
        selectedSearchId,
        setSelectedSearch,
    ])

    return (
        <div className="px-1.5">
            <div className="mb-[30px]">
                <Tree
                    onNodeIconClick={toggleNodePersonal}
                    rootNode={personalRoot}
                    getChildren={openTreeChildren}
                    expandedNodes={openedPersonal}
                    initialDepth={0}
                    renderNonLeafNode={renderNonLeafNode}
                    renderLeafNode={renderLeafNode}
                    depthWidth={0}
                />
            </div>
            <Tree
                onNodeIconClick={toggleNodePublic}
                rootNode={publicRoot}
                getChildren={openTreeChildren}
                expandedNodes={openedPublic}
                renderNonLeafNode={renderNonLeafNode}
                renderLeafNode={renderLeafNode}
                initialDepth={0}
                depthWidth={0}
            />
        </div>
    )
}
