import React from 'react'

import type {Activity, Case, Recordings} from '@bpinc/ad-case-state-types'

import {ActivityCase} from './ActivityCase'
import {ActivityChat} from './ActivityChat'
import {ActivityEmail} from './ActivityEmail'
import {ActivityNotes} from './ActivityNotes'
import {ActivityVoice} from './ActivityVoice'


interface Props {
    data: Case
    recordings?: Recordings
    serviceId?: string
}

export const CaseContent: React.FC<Props> = ({data, recordings = [], serviceId}) => {

    return (
        <>
            {data.activities.map((activity: Activity) => {
                switch (activity.mediaType) {
                    case 'NOTES': {
                        return <ActivityNotes key={activity.id} activity={activity} />
                    }
                    case 'CASE': {
                        return <ActivityCase serviceId={serviceId} key={activity.id} activity={activity} />
                    }
                    case 'EMAIL': {
                        return <ActivityEmail caseId={data.id} key={activity.id} activity={activity} />
                    }
                    case 'VOICE': {
                        return <ActivityVoice key={activity.id} activity={activity} recordings={recordings} />
                    }
                    case 'CHAT': {
                        return <ActivityChat key={activity.id} activity={activity} />
                    }
                    default: return null
                }
            })}
        </>
    )
}
