import React, {useCallback, useMemo} from 'react'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {Assignments, Case} from '@bpinc/ad-case-state-types'
import {useGetContactPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useActiveInteractionId} from '@bpinc/ad-interaction-state-context'
import {LegacyIconCaseFlagOn, LegacyIconPinOn} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Avatar, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {AssignEmailModal} from './CasePage/AssignEmailModal'
import {useCreateNoteMutation} from './CasePage/mutations'
import {CaseHeaderInfo} from './CaseHeaderInfo'
import {CaseReporter} from './CaseReporter'
import {CaseTitle} from './CaseTitle'
import {CopyCaseLinkButton} from './CopyCaseLinkButton'
import {DeleteCaseButton} from './DeleteCaseButton'
import {SaveDraftButton} from './SaveDraftButton'


interface CaseHeaderProps {
    data: Case
    serviceId?: string
    assignments?: Assignments
    interactionMode?: boolean
}

export const CaseHeader: React.FC<CaseHeaderProps> = ({data, interactionMode, serviceId, assignments}) => {

    const {pinCase, unpinCase, flagCase, unflagCase} = useCaseStateMethods()
    const getContactPictureUrl = useGetContactPictureUrl()
    const activeItemId = useActiveInteractionId()
    const {formatFullName} = useFormatting()

    const reporter = {
        id: data.reporterId,
        firstName: data.reporterFirstName || 'Unidentified',
        lastName: data.reporterLastName,
        avatarUrl: getContactPictureUrl(data.reporterId, true),
    }

    const assignment = assignments?.personal[0]
    const assignedToString =
        assignment && formatFullName(assignment.firstName, assignment.lastName)

    const pinnedBy = assignments?.followUp.map(pin =>
        formatFullName(pin.firstName, pin.lastName),
    )

    const isPinned = !!pinnedBy?.length

    const pinnedByString = pinnedBy?.join(', ')

    const status = data.status
    const isFlagged = data.isFlagged

    const handlePin = useCallback(() => pinCase(data.id), [data.id, pinCase])
    const handleUnpin = useCallback(
        () => unpinCase(data.id),
        [data.id, unpinCase],
    )
    const handleFlag = useCallback(
        () => flagCase(data.id),
        [data.id, flagCase],
    )
    const handleUnflag = useCallback(
        () => unflagCase(data.id),
        [data.id, unflagCase],
    )
    const headerInfoColumnClassName = tcn('ml-4 mr-2 last:!mr-0')
    const headerInfoIconClassName = tcn(
        'mx-1 inline-block h-4 w-4 [&_svg]:h-full [&_svg]:w-full [&_svg]:fill-[--c-activity-border]',
    )
    const iconButtonClassName = tcn('[&_svg]:h-full [&_svg]:w-full')

    const {t} = useLocalizationState()

    const pinnedByTooltip = useMemo(() => {
        if (!pinnedBy?.length) return null

        return (
            <>
                {pinnedBy?.map((name, index) => (
                    <CaseHeaderInfo key={index} info={name} title={t.EMAIL.case_view_followups_label()} />
                ))}
            </>
        )
    }, [pinnedBy, t.EMAIL])

    const openAssignEmailModal = useModalComponentOpener(AssignEmailModal)
    const {mutate: createNote} = useCreateNoteMutation()

    const assignedToTooltip = useMemo(() => {
        if (!assignedToString || !assignment.date) return null

        const date = new Date(assignment.date).toLocaleDateString('en-US', {
            month: 'short',
            day: '2-digit',
            year: 'numeric',
        })
        const time = new Date(assignment.date).toLocaleTimeString('en-US', {
            hour: 'numeric',
            minute: '2-digit',
            hour12: true,
        })

        return (
            <div className="flex flex-col gap-1 p-2 ">
                {t.EMAIL.case_view_assignment_personal_label(
                    assignedToString,
                    date,
                    time,
                )}
                <SemanticButton
                    basic
                    className="self-end"
                    primary
                    size="small"
                    onClick={() =>
                        openAssignEmailModal({
                            createNote,
                            queueItemsToMove: [
                                {
                                    caseId: data.id,
                                    queueId: assignments.queueItemId,
                                    service: serviceId,
                                },
                            ],
                        })
                    }
                >
                    {t.EMAIL.email_assign_button()}
                </SemanticButton>
            </div>
        )
    }, [serviceId, assignedToString, assignment, assignments?.queueItemId, createNote, data.id, openAssignEmailModal, t.EMAIL])

    return (
        <div className="flex flex-row flex-nowrap border-b border-[--c-activity-border] p-2">
            <div className="flex-1">
                <div className="relative mb-2 block text-lg font-bold leading-[22px] before:inline-block before:content-[attr(before)]">
                    <div className="absolute inset-x-0 top-0 flex truncate pl-[0.1em] pt-px">
                        <div className="inline-block pr-[0.5em]">
                            {data.number}
                        </div>
                        {data.title && (
                            <CaseTitle
                                editable={interactionMode}
                                text={data.title}
                                caseId={data.id}
                            />
                        )}
                    </div>
                </div>

                <div className="mt-1 flex flex-row flex-nowrap">
                    <div className="w-10 flex-none">
                        <Avatar
                            firstName={reporter.firstName}
                            lastName={reporter.lastName}
                            url={reporter.avatarUrl}
                            size={40}
                        />
                    </div>

                    <div className="flex flex-1 flex-row flex-nowrap">
                        <div className={headerInfoColumnClassName}>
                            <CaseReporter
                                {...reporter}
                                interactionMode={interactionMode}
                                caseId={data.id}
                            />

                            {data.categoryName && (
                                <div className="mt-1 text-gray-400">
                                    {data.categoryName}
                                </div>
                            )}
                        </div>

                        <div className={headerInfoColumnClassName}>
                            <CaseHeaderInfo title={t.EMAIL.case_view_status_label()} info={status} />
                            <CaseHeaderInfo title={t.EMAIL.case_view_assignments_label()} info={assignedToString} tooltip={assignedToTooltip} />
                            <CaseHeaderInfo title={t.EMAIL.case_view_followups_label()} info={pinnedByString} tooltip={pinnedByTooltip} />
                        </div>

                        {(isPinned || isFlagged) && (
                            <div className={headerInfoColumnClassName}>
                                {isPinned && (
                                    <div className={headerInfoIconClassName}>
                                        <LegacyIconPinOn />
                                    </div>
                                )}
                                {isFlagged && (
                                    <div className={headerInfoIconClassName}>
                                        <LegacyIconCaseFlagOn />
                                    </div>
                                )}
                            </div>
                        )}
                    </div>
                </div>
            </div>

            <div className="ml-2 flex flex-none items-start">
                <SemanticButton
                    basic
                    active={isPinned}
                    className={iconButtonClassName}
                    icon="pin"
                    onClick={isPinned ? handleUnpin : handlePin}
                />
                <SemanticButton
                    basic
                    icon="flag"
                    active={data.isFlagged}
                    className={iconButtonClassName}
                    onClick={data.isFlagged ? handleUnflag : handleFlag}
                />
                <CopyCaseLinkButton caseId={data.id} />
                {interactionMode && activeItemId && (
                    <>
                        <SaveDraftButton itemId={activeItemId} />
                        <DeleteCaseButton
                            caseId={data.id}
                            itemId={activeItemId}
                            caseNumber={data.number ?? ''}
                        />
                    </>
                )}
            </div>
        </div>
    )
}
