import React from 'react'
import type {Table} from '@tanstack/react-table'

import type {CasesListItem} from '@bpinc/ad-case-state-types'
import {Button, Checkbox, PopupButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {DEFAULT_VISIBLE_COLUMNS} from './constants'


type CasesColumnsSelectProps = {
    table: Table<CasesListItem>
}

export const CasesColumnsSelect: React.FC<CasesColumnsSelectProps> = ({table}) => {
    const {t} = useLocalizationState()

    return (
        <PopupButton
            transparent
            className="text-[14px] !text-main"
            popupContent={
                <div className="flex flex-col gap-1.5 p-2">
                    <Checkbox
                        checked={table.getIsAllColumnsVisible()}
                        onChange={value => {
                            table.toggleAllColumnsVisible(value)
                        }}
                        label={t.EMAIL.search_queue_case_content_all()}
                    />
                    {table
                        .getAllLeafColumns()
                        .filter(c => c.getCanHide())
                        .map(column => {
                            const header = column.columnDef.header
                            return (
                                <Checkbox
                                    key={column.id}
                                    onChange={value => {
                                        column.toggleVisibility(value)
                                    }}
                                    checked={column.getIsVisible()}
                                    label={typeof header === 'string' ? header : null }
                                />
                            )
                        })}
                    <Button
                        transparent
                        className="pl-[26px]"
                        onClick={() => {
                            table.setColumnVisibility(DEFAULT_VISIBLE_COLUMNS)
                        }}
                    >
                        {t.EMAIL.search_queue_case_restore_default()}
                    </Button>
                </div>
            }
        >
            {t.EMAIL.search_queue_case_content_label()}
        </PopupButton>
    )
}
