import React from 'react'

import type {PartyType} from '@bpinc/ad-activities-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {AvatarLegacy} from '@bpinc/lib-avatar-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {getFullName, getTime} from '../utils/helpers'


interface Props {
    className?: string
    message?: PartyType
    parties?: {
        [key: string]: PartyType
    }
    agent?: {
        firstName: string
        lastName: string
    }
    text?: string
}

export const ActivityMessage: React.FC<Props> = props => {
    const {className, parties, message, agent, text} = props
    let party
    const getAvatarUrl = useGetAvatarUrl()
    const {t} = useLocalizationState()
    const activityMessageClassName = tcn('flex flex-row px-2 py-[5px] [&_b]:mr-1')
    const activityMessageContentClassName = tcn('relative flex-auto pr-5')
    const activityMessageIconClassName = tcn(
        'absolute right-0 top-0 h-3 w-5 rounded bg-[--c-activity-message-icon]',
        'before:absolute before:left-[3px] before:top-full before:border-[3px]',
        'before:border-b-transparent before:border-l-[--c-activity-message-icon] before:border-r-transparent before:border-t-[--c-activity-message-icon]',
        'after:absolute after:inset-x-0 after:top-0 after:text-center after:text-[8px] after:uppercase after:leading-[12px]',
    )
    const activityMessageIconSmsClassName = tcn('after:content-["sms"]')

    const renderAvatar = (avatar: Partial<PartyType>) => {
        const url = getAvatarUrl({
            userId: avatar.userId,
            contactId: avatar.contactId,
        })

        return (
            <AvatarLegacy
                size={40}
                url={url}
                firstName={avatar.firstName}
                lastName={avatar.lastName}
                type={avatar.userId ? 'user' : 'contact'}
            />
        )
    }

    if (agent) {
        return (
            <div className={tcn(className, activityMessageClassName)}>
                <div className="mr-2.5">
                    {renderAvatar(agent)}
                </div>
                <div className={activityMessageContentClassName}>
                    <div>
                        <b>{getFullName(agent)}</b>
                    </div>
                    <div>{text}</div>
                </div>
            </div>
        )
    }

    if (message && parties) {
        const {partyId, event} = message

        switch (event) {
            case 'party_joined': {
                party = parties[partyId]
                if (
                    party && party.type &&
                    ['internal', 'external'].includes(party.type)
                ) {
                    return (
                        <div
                            className={tcn(
                                className,
                                activityMessageClassName,
                                'bg-[--c-activity-message-bg]',
                            )}
                        >
                            <div className="mr-2.5">
                                {renderAvatar(party)}
                            </div>
                            <div className={activityMessageContentClassName}>
                                <div>
                                    <b>{getFullName(party)}</b>
                                    {getTime(party)}
                                </div>
                                <div>
                                    {' ' + t.EXTERNAL_CHAT.party_joined_chat_2() + ' '}
                                </div>
                            </div>
                        </div>
                    )
                }
                break
            }
            case 'party_left': {
                party = parties[partyId]
                if (
                    party &&
                    party.type &&
                    ['internal', 'external'].includes(party.type)
                ) {
                    return (
                        <div
                            className={tcn(className, activityMessageClassName, 'bg-[--c-activity-message-bg]')}
                        >
                            <div className="mr-2.5">
                                {renderAvatar(party)}
                            </div>
                            <div className={activityMessageContentClassName}>
                                <div>
                                    <b>{getFullName(party)}</b>
                                    {getTime(party)}
                                </div>
                                <div>
                                    {' ' + t.EXTERNAL_CHAT.party_left_chat_2() + ' '}
                                </div>
                            </div>
                        </div>
                    )
                }
                break
            }
            case 'party_message': {
                party = parties[partyId]
                if (
                    party &&
          party.type &&
          ['internal', 'external'].includes(party.type)
                ) {
                    return (
                        <div className={tcn(className, activityMessageClassName)}>
                            <div className="mr-2.5">
                                {renderAvatar(party)}
                            </div>
                            <div className={activityMessageContentClassName}>
                                <div>
                                    <b>{getFullName(party)}</b>
                                    {getTime(party)}
                                </div>
                                <div>{party.msg}</div>
                                {party.channel === 'sms' && (
                                    <div
                                        className={tcn(
                                            activityMessageIconClassName,
                                            activityMessageIconSmsClassName,
                                        )}
                                    />
                                )}
                            </div>
                        </div>
                    )
                }
                break
            }
            default: {
                return null
            }
        }
    }

    return null
}
