import type {FC} from 'react'
import React, {useCallback} from 'react'
import {AnimatePresence, motion} from 'framer-motion'

import {useActiveInteractionProp} from '@bpinc/ad-interaction-state-context'
import {useCallButtonsHandlers} from '@bpinc/ad-interaction-state-hooks'
import {getMediaIcon} from '@bpinc/ad-interaction-state-methods'
import {useCommunicatorUIState} from '@bpinc/ad-main-page-ui-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {
    EndCallButton,
    HoldButton,
    MuteButton,
    RecordButton,
    TransferButton,
} from '@bpinc/lib-interaction-buttons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {InteractionMediaIcon, InteractionTimer} from '@bpinc/lib-ui-elements'

import {interactionBarStyles} from '../styles'
import {ContactInfo} from './ContactInfo'


interface OngoingCallContentProps {
    onDisconnect: () => void
}

export const OngoingCallContent: FC<OngoingCallContentProps> = ({
    onDisconnect,
}) => {
    const iconType = useActiveInteractionProp(getMediaIcon)
    const deliveredStartTime = useActiveInteractionProp(
        i => i?.displayData.deliveredStartTime,
    )
    const holdStartTime = useActiveInteractionProp(
        i => i?.displayData.holdStartTime,
    )

    const {
        showRecordButton,
        isCallEstablished,
        isMuted,
        isOnHold,
        isRecording,
        isLoading,
        handleHoldClick,
        handleMuteClick,
        handleRecordClick,
    } = useCallButtonsHandlers()

    const {handleToggleTransferModal} = useCommunicatorUIState()

    const handleClickToggleButton = useCallback(() => {
        return handleToggleTransferModal?.(true)
    }, [handleToggleTransferModal])

    const {phoneDeviceCapabilities} = useSessionState()

    const holdButtonDisabled =
        !isCallEstablished ||
        (isOnHold
            ? !phoneDeviceCapabilities.canRetrieve
            : !phoneDeviceCapabilities.canHold)


    return (
        <div className={tcn(interactionBarStyles, 'pl-4')}>
            <div className="flex items-center">
                <InteractionMediaIcon
                    icon={iconType}
                    className={tcn(
                        'icon-default',
                        {
                            'animate-pulse-light':
                            !isCallEstablished && iconType === 'call_outbound',
                        })}
                />
                {deliveredStartTime && (
                    <InteractionTimer
                        startTime={deliveredStartTime}
                        data-testid="duration-timer"
                        className="ml-1"
                    />
                )}
            </div>
            <motion.div
                whileHover="hover"
                className={tcn(
                    'group/circle-btn-parent ml-2 flex shrink-0 items-center rounded p-2.5',
                    'transition-colors state-layer-bg-[--c-main-bars-bg] state-layer-color-black state-layer-opacity-0 hover:state-layer-opacity-10',
                )}
            >
                <section className="flex gap-2.5">
                    <MuteButton
                        onClick={handleMuteClick}
                        disabled={!isCallEstablished}
                        isLoading={isLoading.muted}
                        isActive={isMuted}
                    />
                    <HoldButton
                        isActive={isOnHold}
                        isLoading={isLoading.onHold}
                        onClick={handleHoldClick}
                        holdStartTime={holdStartTime}
                        disabled={holdButtonDisabled}
                    />
                    {showRecordButton && (
                        <RecordButton
                            className={tcn('hidden group-hover:grid', {
                                grid: isRecording,
                            })}
                            isActive={isRecording}
                            isLoading={isLoading.recording}
                            onClick={handleRecordClick}
                            disabled={!isCallEstablished || isOnHold}
                        />
                    )}
                </section>
                <AnimatePresence>
                    <motion.section
                        initial={{opacity: 0, width: 0, marginLeft: 0, pointerEvents: 'none'}}
                        variants={{hover: {opacity: 1, width: 'auto', marginLeft: 10, pointerEvents: 'auto'}}}
                        className="flex items-center gap-2.5"
                    >
                        <TransferButton
                            disabled={!isCallEstablished}
                            onClick={handleClickToggleButton}
                        />
                        <EndCallButton onClick={onDisconnect} />
                    </motion.section>
                </AnimatePresence>
            </motion.div>
            <ContactInfo />
        </div>
    )
}
