import type {IAllProps} from '@tinymce/tinymce-react'

import {TINYMCE_FONT_SIZES, TINYMCE_FONTS} from './constants'


export type TinyMCEConfig = Exclude<IAllProps['init'], undefined>

export type TinyMCEFont = {
    fontFamily: string
    fontSize: string
    color: string
    defaultFontStyle: string
}

/** Self-hosted tinymce script */
export const getTinymceScriptSrc = (publicUrl: string) => `${publicUrl}/static/js/tinymce/tinymce.js`

export const DEFAULT_TINYMCE_FONT: TinyMCEFont = {
    fontFamily: 'Arial',
    fontSize: '14px',
    color: '#000',
    defaultFontStyle: '14px Arial',
}

export const getTinymceFont = (font: string, size: number, color: string): TinyMCEFont => ({
    fontFamily: font,
    fontSize: size + 'px',
    color,
    defaultFontStyle: `${size}px ${font}`,
})

