import React, {
    useCallback,
    useLayoutEffect,
    useMemo,
    useRef,
    useState,
} from 'react'

import {useEvaluationStateMethods} from '@bpinc/ad-evaluation-state-context'
import {
    type InteractionData,
    type InteractionFormsState,
    type InteractionMetadata,
    type InteractionParticipant,
    type InteractionScreenRecording,
    type InteractionSearchItem,
    type InteractionSegments,
    type Transcript as TranscriptType,
    EvaluationPageName,
} from '@bpinc/ad-evaluation-state-types'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {TabI} from '@bpinc/lib-ui-elements'
import {ResizePanel, Tabs} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {buildInteractionTitle, buildTabId} from '../../utils/tabs'
import {EvaluationForm} from './EvaluationForm'
import {Header} from './Header'
import {ScreenRecording} from './ScreenRecording'
import {Summary} from './Summary'
import {Timeline} from './Timeline'
import {Transcript} from './Transcript'


enum LeftPanelTab {
    SUMMARY = 'summary',
    TRANSCRIPT = 'transcript',
    SCREEN_RECORDING = 'screen-recording',
}

enum RightPanelTab {
    EVALUATION = 'evaluation',
    ALL_EVALS = 'all-evals',
}

type InteractionProps = {
    interaction: InteractionData
    interactionStartTime: number
    meta: InteractionMetadata
    segments: InteractionSegments
    participants: InteractionParticipant[]
    transcripts: TranscriptType[]
    searchItem: InteractionSearchItem
    forms: InteractionFormsState
    screenRecording?: InteractionScreenRecording
}

export const Interaction: React.FC<InteractionProps> = ({
    interaction,
    interactionStartTime,
    meta,
    segments,
    participants,
    transcripts,
    searchItem,
    forms,
    screenRecording,
}) => {
    const {
        dateTime,
        duration,
        service,
    } = interaction
    const activeForm = forms.activeFormId ?
        forms.loadedItems[forms.activeFormId] : undefined

    const {addTab} = useEvaluationStateMethods()
    const contentRef = useRef<HTMLDivElement>(null)
    const [panelSize, setPanelSize] = useState(0)
    const [currentTimeline, setCurrentTimeline] = useState(0)
    const {t: {EVALUATION_PAGE: messages}} = useLocalizationState()
    const [activeLeftTab, setActiveLeftTab]
        = useState<LeftPanelTab>(LeftPanelTab.SUMMARY)
    const [activeRightTab, setActiveRightTab]
        = useState<RightPanelTab>(RightPanelTab.EVALUATION)

    const openInteraction = useCallback((cdrId?: string) => {
        const giid = interaction.globalInteractionId
        const search = searchItem.search
        const title =
            buildInteractionTitle(
                search.title,
                searchItem.relatedIndex,
                search.totalLength,
                searchItem.item.agentName,
            )
        addTab({
            page: EvaluationPageName.INTERACTION,
            tabId: buildTabId(EvaluationPageName.INTERACTION, giid, cdrId),
            searchType: search.type,
            title,
            searchId: search.id,
            giid,
            cdrId,
        })
    }, [interaction, searchItem, addTab])

    const showTimeline = useMemo(() => {
        const party = meta.selectedParty
        return party.hasVoiceRecording || party.hasScreenRecording ||
            meta.mediaType !== 'EMAIL' && party.transcriptsBatch && !party.transcriptsBatch.empty
    }, [meta])

    useLayoutEffect(() => {
        if (contentRef.current) {
            const panelWidth =
                Math.round(contentRef.current.getBoundingClientRect().width / 2)
            setPanelSize(panelWidth)
        }
    }, [])

    const leftTabs = useMemo<TabI<LeftPanelTab>[]>(() => {
        const tabs: TabI<LeftPanelTab>[] = [
            {id: LeftPanelTab.SUMMARY, title: messages.summary()},
            {id: LeftPanelTab.TRANSCRIPT, title: messages.transcript()},
        ]

        if (meta.selectedParty.hasScreenRecording) {
            tabs.push({
                id: LeftPanelTab.SCREEN_RECORDING,
                title: messages.screen_recording(),
            })
        }

        return tabs
    }, [messages, meta.selectedParty.hasScreenRecording])

    const rightTabs = useMemo<TabI<RightPanelTab>[]>(() => [
        {id: RightPanelTab.EVALUATION, title: messages.evaluation()},
        {id: RightPanelTab.ALL_EVALS, title: messages.all_evals()},
    ], [messages])

    const onLeftTabChange = useCallback((id: LeftPanelTab) => {
        setActiveLeftTab(id)
    }, [])

    const onRightTabChange = useCallback((id: RightPanelTab) => {
        setActiveRightTab(id)
    }, [])

    const renderLeftTabContent = useCallback(() => {
        if (activeLeftTab === LeftPanelTab.SUMMARY) {
            return (
                <Summary
                    interaction={interaction}
                    participants={participants}
                    meta={meta}
                    openInteraction={openInteraction}
                />
            )
        }
        if (activeLeftTab === LeftPanelTab.TRANSCRIPT) {
            return (
                <Transcript
                    meta={meta}
                    transcripts={transcripts}
                    currentTimeline={currentTimeline}
                    duration={duration}
                />
            )
        }
        if (activeLeftTab === LeftPanelTab.SCREEN_RECORDING) {
            return (
                <ScreenRecording
                    meta={meta}
                    screenRecording={screenRecording}
                />
            )
        }
        return null
    }, [
        activeLeftTab,
        interaction,
        meta,
        participants,
        transcripts,
        screenRecording,
        duration,
        currentTimeline,
        openInteraction,
    ])

    const renderRightTabContent = useCallback(() => {
        if (activeRightTab === RightPanelTab.EVALUATION) {
            return (
                <EvaluationForm
                    forms={forms}
                    interaction={interaction}
                    meta={meta}
                    searchItem={searchItem}
                />
            )
        }
        return null
    }, [activeRightTab, interaction, meta, forms, searchItem])

    const tabsClassName = 'flex flex-col flex-1'
    const tabsContentClassName = 'flex overflow-hidden'
    const tabsHeaderClassName = 'bg-transparent h-[42px] mb-0.5'
    const tabClassName = tcn(
        'text-eval-text-dark shadow-none hover:shadow-none hover:after:bg-eval-text-dark focus:shadow-none',
        'whitespace-nowrap after:absolute after:-bottom-0.5 after:left-0 after:h-px after:w-full',
    )
    const activeTabClassName =
        'shadow-none hover:shadow-none active:shadow-none focus:shadow-none after:h-0.5 after:bg-eval-text-dark'

    return (
        <div className="flex h-full w-full flex-col">
            <div className="px-8">
                <Header
                    metadata={meta}
                    searchItem={searchItem}
                    form={activeForm}
                    agent={meta.calleeParty}
                    client={meta.callerParty}
                    startTime={dateTime}
                    duration={duration}
                    service={service}
                />
            </div>
            <div
                ref={contentRef}
                className="flex flex-1 gap-4 overflow-hidden px-8"
            >
                {panelSize && (
                    <>
                        <ResizePanel
                            className="relative"
                            direction="right"
                            containerClass="h-full overflow-x-auto"
                            borderClass={tcn(
                                'mx-1 w-2 rounded bg-[--c-table-resizer]',
                                'opacity-0 transition-opacity duration-300 hover:opacity-100',
                                'after:absolute after:inset-y-[calc(50%-10px)]',
                                'after:inset-x-px after:border-x-2 after:border-[--c-legacy-icon-default]',
                            )}
                            initialSize={panelSize}
                        >
                            <Tabs
                                className={tabsClassName}
                                headerClassName={tabsHeaderClassName}
                                tabClassName={tabClassName}
                                tabsContentClassName={tabsContentClassName}
                                activeTabClassName={activeTabClassName}
                                tabs={leftTabs}
                                renderTabContent={renderLeftTabContent}
                                activeTabId={activeLeftTab}
                                onChange={onLeftTabChange}
                            />
                        </ResizePanel>
                        <Tabs
                            className={tabsClassName}
                            headerClassName={tabsHeaderClassName}
                            tabClassName={tabClassName}
                            tabsContentClassName={tabsContentClassName}
                            activeTabClassName={activeTabClassName}
                            tabs={rightTabs}
                            renderTabContent={renderRightTabContent}
                            activeTabId={activeRightTab}
                            onChange={onRightTabChange}
                        />
                    </>
                )}
            </div>
            {showTimeline && (
                <Timeline
                    meta={meta}
                    segments={segments}
                    participants={participants}
                    transcripts={transcripts}
                    interactionStartTime={interactionStartTime}
                    duration={duration}
                    currentTimeline={currentTimeline}
                    setCurrentTimeline={setCurrentTimeline}
                />
            )}
        </div>
    )
}
