import {useEffect} from 'react'

import {useBpClientStateConnector} from '@bpinc/ad-bpclient-state-context'
import {useCommunicatorFloatingStateSetter} from '@bpinc/ad-main-page-ui-context'
import {AppRoutes, useNavigationStateRef} from '@bpinc/lib-communicator-navigation-state'


export function useBPClientStateSubscription() {
    const connector = useBpClientStateConnector()
    const navigationStateRef = useNavigationStateRef()
    const setCommunicatorFloatingState = useCommunicatorFloatingStateSetter()

    useEffect(
        function bpClientSubscription() {
            const {setActivePage, activePage} = navigationStateRef.current
            const subscription = connector?.dispenser.subscribe({
                onNavigateToInteraction: () => {
                    setCommunicatorFloatingState(state => (state === 'closed' ? 'floating' : state))
                    if (activePage.name !== AppRoutes.Interactions) {
                        setActivePage({name: AppRoutes.Interactions})
                    }
                },
            })
            return () => subscription?.unsubscribe()
        },
        [setCommunicatorFloatingState, connector, navigationStateRef],
    )
}
