import type {FC} from 'react'
import React from 'react'

import {CaseTitleInput} from './CaseTitleInput'


interface CaseTitleProps {
    editable?: boolean
    text: string
    caseId: string
}

export const CaseTitle: FC<CaseTitleProps> = ({text, editable, caseId}) => {
    return <>{editable ? <CaseTitleInput initialTitle={text} caseId={caseId} /> : text}</>
}
