import type {FC} from 'react'
import React from 'react'
import type {Row} from '@tanstack/react-table'
import {flexRender} from '@tanstack/react-table'

import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import {SmTable} from '@bpinc/lib-external-ui-elements'

import {useContactsActions} from '../useContactsActions'


type ContactsTableRowProps = {
    row: Row<ContactShortType>
}

export const ContactsTableRow: FC<ContactsTableRowProps> = ({
    row,
}) => {
    const {handleContactOpen} = useContactsActions()

    return (
        <SmTable.Row
            key={row.id}
            tabIndex={0}
            data-id={row.id}
            onClick={row.getToggleSelectedHandler()}
            onKeyDown={(e: KeyboardEvent) => {
                if (!['Enter', ' '].includes(e.key)) {
                    return
                }
                if (!row.getIsSelected()) {
                    row.toggleSelected()
                    return
                }
                handleContactOpen(row.original)
            }}
            onDoubleClick={() => {
                row.toggleSelected(true)
                handleContactOpen(row.original)
            }}
            active={row.getIsSelected()}
            className="cursor-pointer"
        >
            {row.getVisibleCells().map(cell => (
                <SmTable.Cell key={cell.id} >
                    {flexRender(
                        cell.column.columnDef.cell,
                        cell.getContext(),
                    )}
                </SmTable.Cell>
            ))}
        </SmTable.Row>
    )
}
