import React from 'react'
import type {Table} from '@tanstack/react-table'
import {flexRender} from '@tanstack/react-table'

import type {CasesListItem} from '@bpinc/ad-case-state-types'
import {SmTable} from '@bpinc/lib-external-ui-elements'

import {CasesColumnsSelect} from './CasesColumnsSelect'
import {CasesServiceSelect} from './CasesServiceSelect'
import {CasesSorting} from './CasesSorting'
import {CasesStatusesSelect} from './CasesStatusesSelect'


type CasesTableHeaderProps = {
    table: Table<CasesListItem>
}

export const CasesTableHeader: React.FC<CasesTableHeaderProps> = ({table}) => {
    const [checkboxColumn, ...restHeaders] = table.getLeafHeaders()

    return (
        <>
            <SmTable.HeaderCell>
                {flexRender(
                    checkboxColumn.column.columnDef.header,
                    checkboxColumn.getContext(),
                )}
            </SmTable.HeaderCell>
            <SmTable.HeaderCell colSpan={restHeaders.length}>
                <div className="flex items-center justify-between gap-2">
                    <div className="flex items-center gap-2">
                        <CasesStatusesSelect />
                        <CasesServiceSelect />
                    </div>
                    <div className="flex items-center gap-2">
                        <CasesSorting />
                        <CasesColumnsSelect table={table} />
                    </div>
                </div>
            </SmTable.HeaderCell>
        </>
    )
}
