import React, {useMemo} from 'react'

import type {ActivityNotes as ActivityNotesType} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {LegacyIconKey} from '@bpinc/lib-icons'
import {Avatar} from '@bpinc/lib-ui-elements'

import {getAction} from './utils/helpers'


interface Props {
    activity: ActivityNotesType
}

export const ActivityNotes: React.FC<Props> = ({activity}) => {
    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()
    const {createdTime, parties} = activity
    const user = parties.find(p => p.partyType === 'USER')
    const {userId, firstName = '', lastName = '', notes = '', disposition} = user || {}

    const fullName = formatFullName(firstName, lastName)
    const action = getAction(disposition)
    const date = formatDate(createdTime)

    const title = [fullName, action, 'on', date].join(' ')
    const text = disposition || notes
    const avatarOptions = useMemo(() => ({userId, notFoundPlaceholder: true}), [userId])

    return (
        <div className="relative flex p-2">
            <div className="mr-2 flex-none">
                <Avatar
                    size={40}
                    firstName={firstName}
                    lastName={lastName}
                    url={getAvatarUrl(avatarOptions)}
                />
            </div>
            <div className="flex-auto bg-[--c-activity-bg] p-2">
                <div className="flex">
                    <div className="mr-2 h-4 w-4 flex-none self-center [&_svg]:h-full [&_svg]:w-full">
                        <LegacyIconKey fill="var(--c-icon)" />
                    </div>
                    <div className="mr-2 text-gray-500">
                        {title}
                    </div>
                </div>

                <div className="mt-2">
                    {text}
                </div>
            </div>
        </div>
    )
}
