import type {FC} from 'react'
import React, {useCallback} from 'react'

import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {LegacyIconSaveClose} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SemanticButton} from '@bpinc/lib-ui-elements'


interface SaveDraftButtonProps {
    itemId: string
}

export const SaveDraftButton: FC<SaveDraftButtonProps> = ({itemId}) => {
    const {saveEmailDraft} = useInteractionStateMethods()

    const handleClick = useCallback(
        function handleClickCallback() {
            if (itemId) {
                saveEmailDraft({itemId})
            }
        },
        [itemId, saveEmailDraft],
    )

    return (
        <SemanticButton
            icon
            basic
            className={tcn('h-9 w-15 [&_svg]:h-full [&_svg]:w-9.5')}
            onClick={handleClick}
        >
            <LegacyIconSaveClose fill="var(--c-icon)" />
        </SemanticButton>
    )
}
