import type {FC} from 'react'
import React from 'react'

import {pinnedSortingAtom} from '../atoms'
import {SortingControls} from '../SortingControls'
import {useSortingOptions} from './useSortingOptions'


interface TableHeaderContentProps {
    noData: boolean
}
export const TableHeaderContent: FC<TableHeaderContentProps> = ({noData}) => {
    const sortingOptions = useSortingOptions()

    if (!noData) {
        return null
    }

    return (
        <SortingControls
            className="justify-self-end"
            sortingAtom={pinnedSortingAtom}
            options={sortingOptions}
        />
    )
}
