import type {FC} from 'react'
import React, {useCallback} from 'react'
import type {UseFormReturn} from 'react-hook-form'
import {partial} from 'lodash-es'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useInteractionProp, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId, getServiceId} from '@bpinc/ad-interaction-state-methods'
import {useEmailAttachmentsUIState} from '@bpinc/lib-attachments-state'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {AttachButton, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useSaveDraftMutation} from '../../mutations'
import type {EmailFormProps} from './EmailForm'
import {ServiceSelector} from './ServiceSelector'
import type {EmailFormFields} from './types'


interface EmailControlsProps extends EmailFormProps {
    className?: string
    form: UseFormReturn<EmailFormFields>
}

const buttonStyles = 'w-6 p-2.5 shrink-0'

export const EmailControls: FC<EmailControlsProps> = ({
    interactionId,
    serviceId,
    emailId,
    form,
    className,
}) => {
    const {
        t: {EMAIL: i18n},
    } = useLocalizationState()

    const {sendEmailDraft, saveEmailDraft} = useInteractionStateMethods()
    const {mutateAsync: saveDraft} = useSaveDraftMutation(emailId)

    const selectedService = useInteractionProp(interactionId, getServiceId)
    const interactionStepId = useInteractionProp(interactionId, getInteractionStepId)
    const isLastEmailDraft = useInteractionProp(interactionId, i => i?.emailData?.activeDrafts?.length === 1)
    const addAttachment = useEmailAttachmentsUIState('addAttachment')
    const {discardEmailDraft} = useCaseStateMethods()

    const handleUploadFiles = useCallback(
        (event: React.ChangeEvent<HTMLInputElement>): void => {
            addFilesToAttachmentsList()

            function addFilesToAttachmentsList() {
                const files = getFilesFromEvent()
                if (!files) {
                    return
                }

                files.forEach(partial(addAttachment, emailId))
            }

            function getFilesFromEvent() {
                const input = event.target
                return input.files ? Array.from(input.files) : []
            }
        },
        [addAttachment, emailId],
    )
    async function handleSendEmail() {
        // REST API Request for save the draft before sending the email
        await saveDraft(form.getValues())
        //WS API Request for sending the email
        sendEmailDraft({itemId: interactionId, emailId})
    }

    async function handleDeleteDraft() {
        if (isLastEmailDraft) {
            //Reset form values to default values
            form.reset()
            //Save the draft with empty values
            saveDraft(form.getValues())
            //WS API Request for completing the interaction
            saveEmailDraft({itemId: interactionId, emailId})
        } else {
            //Simply delete the draft
            //TODO: Add optimistic update?
            discardEmailDraft({interactionStepId, emailId})
        }
    }

    return (
        <footer
            className={tcn(
                'flex w-full items-end gap-3 rounded p-2 pr-4 shadow transition-all',
                className,
            )}
        >
            <SemanticButton
                icon="paper plane"
                transparent
                disabled={!selectedService}
                className={buttonStyles}
                onClick={handleSendEmail}
                tooltip={i18n.email_send_button_tooltip()}
            />
            <ServiceSelector
                itemId={interactionId}
                serviceId={serviceId}
                emailId={emailId}
            />
            <div className="flex-auto" id={`email-editor-toolbar-${emailId}`} />
            <div className="flex gap-1">
                <AttachButton
                    className={buttonStyles}
                    handleUploadFiles={handleUploadFiles}
                    tooltip={i18n.email_addAttachment_button_tooltip()}
                />
                <SemanticButton
                    icon="trash"
                    className={buttonStyles}
                    onClick={handleDeleteDraft}
                    tooltip={i18n.email_deleteDraft_button_tooltip()}
                    transparent
                />
            </div>
        </footer>
    )
}
