import {useEffect} from 'react'
import {useLocation} from 'react-router-dom'

import {useInteractionWorkspaceState} from '@bpinc/ad-main-page-ui-context'
import {useNavigationState as useADNavigationState} from '@bpinc/ad-navigation-state-context'


export const useAutoRedirectToActivityFormTab = () => {
    const {
        shownWorkspaceTabs,
        shownWorkspaceItem,
        activateItemTab,
    } = useInteractionWorkspaceState()
    const location = useLocation()

    const activeItemId = shownWorkspaceItem?.id

    const {getParams, setParams} = useADNavigationState()

    useEffect(() => {
        const routeParams = getParams()

        if (!activeItemId) {
            return
        }

        const tabs = shownWorkspaceTabs ?? []

        const activityFormTabId = tabs?.find(tab => tab.type === 'activityForm')?.id

        if (routeParams.tab === 'activityForm' && activityFormTabId) {
            activateItemTab(activityFormTabId)
            // if we don't clear the params,
            // user will not be redirected next time he attempts to complete interaction
            setParams({})
        }
    }, [
        location,
        activeItemId,
        getParams,
        setParams,
        shownWorkspaceTabs,
        activateItemTab,
    ])
}
