import {useEffect} from 'react'
import {useAtomValue} from 'jotai'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'

import {mergingContactsAtom} from '../atoms'


export function useLoadMergeContactsData() {
    const mergingContacts = useAtomValue(mergingContactsAtom)

    const {
        getContact,
        getContactDetails,
        getContactFormsDefinitions,
    } = useContactsStateMethods()

    useEffect(function loadContacts() {
        mergingContacts.forEach(contactId => {
            getContact({id: contactId, force: true})
            getContactDetails({id: contactId, force: true})
        })
    }, [getContact, getContactDetails, mergingContacts])

    useEffect(function loadContactFromDefinition() {
        getContactFormsDefinitions({})
    }, [getContactFormsDefinitions])
}
