import {useEffect, useMemo} from 'react'
import type {PrimitiveAtom} from 'jotai'
import {useAtom} from 'jotai'

import type {QueueItem} from '@bpinc/ad-request-urls'
import {usePrevious} from '@bpinc/lib-helpers'


export function useActivateQueueWhenItemMoved(
    queueItems: QueueItem[] = [],
    activeQueueItemAtom: PrimitiveAtom<QueueItem | undefined>,
) {

    const [activeQueueItem, setActiveQueueItem] = useAtom(activeQueueItemAtom)
    const activeQueueId = activeQueueItem?.queueId

    const activeQueueItemIndex = useMemo(
        function memoizeActiveQueueItemIndex() {
            return queueItems.findIndex(q => q.queueId === activeQueueId)
        },
        [activeQueueId, queueItems],
    )

    const previousQueueItemIndex = usePrevious(activeQueueItemIndex)
    const prevQueueDataLength = usePrevious(queueItems?.length)

    const amountOfDeletedQueueItems = prevQueueDataLength
        ? prevQueueDataLength - queueItems.length
        : 0

    const activeQueueItemIsDeleted = useMemo(() => {
        const queueReduced = (prevQueueDataLength ?? 0) > queueItems?.length
        const activeQueueItemIsAbsent = activeQueueItemIndex === -1

        return queueReduced && activeQueueItemIsAbsent
    }, [activeQueueItemIndex, prevQueueDataLength, queueItems?.length])

    useEffect(
        function activateQueueWhenItemMoved() {
            if (activeQueueItemIsDeleted && activeQueueItemIndex === -1) {
                setActiveQueueItem(queueItems[findClosestIndex()])
            }

            function findClosestIndex() {
                const newIndex =
                    (previousQueueItemIndex ?? 0) -
                    amountOfDeletedQueueItems +
                    1

                const toBigIndex = newIndex >= queueItems.length

                if (toBigIndex) {
                    return queueItems.length - 1
                }

                const toSmallIndex = newIndex < 0

                if (toSmallIndex) {
                    return 0
                }

                return newIndex
            }
        },
        [
            queueItems.length,
            activeQueueId,
            activeQueueItemIndex,
            activeQueueItemIsDeleted,
            amountOfDeletedQueueItems,
            previousQueueItemIndex,
            setActiveQueueItem,
            queueItems,
        ],
    )
}
