import {useCallback} from 'react'

import {truthy} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'

import type {MergeFormArrayFields, MergeFormFieldValidator} from './types'


const MAX_ARRAY_ELEMENTS_AMOUNT: Record<MergeFormArrayFields, number> = {
    phones: 5,
    emails: 3,
    addresses: 3,
    messengers: 1,
}

export function useGetMergeFormFieldValidator(): (type: MergeFormArrayFields) => MergeFormFieldValidator {
    const {t} = useLocalizationState()
    const {openModal} = useModalsStateMethods()

    return useCallback((type: MergeFormArrayFields) => {
        const alertTexts = {
            addresses: t.MERGE_FORM.max_addresses(MAX_ARRAY_ELEMENTS_AMOUNT[type]),
            phones: t.MERGE_FORM.max_phones(MAX_ARRAY_ELEMENTS_AMOUNT[type]),
            emails: t.MERGE_FORM.max_emails(MAX_ARRAY_ELEMENTS_AMOUNT[type]),
            messengers: t.MERGE_FORM.max_messengers(MAX_ARRAY_ELEMENTS_AMOUNT[type]),
        }

        return values => {
            if (truthy(values.Contact?.[type] || []).length >= MAX_ARRAY_ELEMENTS_AMOUNT[type]) {
                openModal({
                    title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning(),
                    body: alertTexts[type],
                    actions: {
                        ok: {
                            title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk(),
                        },
                    },
                })
                return false
            }
            return true
        }
    }, [openModal, t.MERGE_FORM, t.WINDOW_POPUP_TITLES_AND_MESSAGES])
}

