import React from 'react'
import {useAtomValue} from 'jotai'

import {Case} from '@bpinc/ad-case-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {selectedCaseIdAtom} from '../atoms'
import {CaseFooterContent} from './CaseFooterContent'


export const CaseView = () => {
    const {t} = useLocalizationState()
    const selectedCaseId = useAtomValue(selectedCaseIdAtom)

    if (!selectedCaseId) {
        return (
            <div className="m-auto text-[28px] text-[--c-no-data-text]">
                {t.GLOBAL.no_data()}
            </div>
        )
    }

    return (
        <Case
            id={selectedCaseId}
            FooterContent={<CaseFooterContent caseId={selectedCaseId} />}
        />
    )
}
