import type {FC} from 'react'
import React, {useMemo, useState} from 'react'
import type {UseFormReturn} from 'react-hook-form'

import {useContact, useContactFormDefinition} from '@bpinc/ad-contacts-state-context'
import {contactToShortContact} from '@bpinc/ad-contacts-state-methods'
import {FormBuilder} from '@bpinc/ad-form-builder'
import {useSessionState} from '@bpinc/ad-session-context'
import {useHandleUseContact, useIsUseContactButtonVisible} from '@bpinc/lib-contact-details'
import {createFormValues} from '@bpinc/lib-form-builder'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {EditActions} from './EditActions'
import {ProfileActions} from './ProfileActions'


export const Details: FC<{
    contactId: string
    editMode?: boolean
    actionsRef: React.RefObject<HTMLDivElement>
    onMerge?: () => void
    onEditMode?: (edit: boolean) => void
}> = ({
    contactId,
    actionsRef,
    editMode,
    onMerge,
    onEditMode,
}) => {
    const {t} = useLocalizationState()
    const {privileges} = useSessionState()

    const [form, setForm] = useState<UseFormReturn | null>(null)

    const formDefinition = useContactFormDefinition('CONTACT_DETAIL')
    const contact = useContact(contactId)
    const contactVariables = contact?.details || {}

    const contactData = useMemo(() => contact?.data
        ? {
            contactId,
            firstName: contact.data.firstName,
            lastName: contact.data.lastName,
        }
        : undefined,
    [contactId, contact?.data])

    const iseUseButtonVisible = useIsUseContactButtonVisible()
    const handleUse = useHandleUseContact()

    if (!formDefinition) {
        return null
    }


    const defaultValues = createFormValues(formDefinition, contactVariables)

    return (
        <>
            {editMode ? (
                <EditActions
                    actionsRef={actionsRef}
                    onEditMode={onEditMode}
                    contactId={contactId}
                    form={form}
                />
            ) : (
                <ProfileActions profileActionsRef={actionsRef}>
                    {privileges.EDIT_CONTACTS && (
                        <SemanticButton onClick={() => onEditMode?.(true)} basic primary>
                            {t.UNDEFINED.button_edit_contact_text()}
                        </SemanticButton>
                    )}
                    {onMerge && (
                        <SemanticButton onClick={onMerge} basic primary>
                            {t.GLOBAL.merge()}
                        </SemanticButton>
                    )}
                    {iseUseButtonVisible && (
                        <SemanticButton
                            basic
                            primary
                            onClick={() => {
                                assertDefined(contact?.data)
                                handleUse(contactToShortContact(contact.data))
                            }}
                        >
                            {t.UNDEFINED.button_use_contact_text()}
                        </SemanticButton>
                    )}
                </ProfileActions>
            )}

            <FormBuilder
                formDefinition={formDefinition}
                onFormAvailable={setForm}
                variables={contactVariables}
                defaultValues={defaultValues}
                view={!editMode}
                contactData={contactData}
            />
        </>
    )
}
