import type {FC} from 'react'
import React, {useCallback} from 'react'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useActiveInteractionStepId} from '@bpinc/ad-interaction-state-context'
import {useOpenSearchContactModal} from '@bpinc/lib-modals-ui'
import {LinkLikeButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface CaseReporterProps {
    firstName: string | undefined
    lastName: string | undefined
    caseId: string
    interactionMode?: boolean
}

export const CaseReporter: FC<CaseReporterProps> = ({caseId, firstName, lastName, interactionMode}) => {
    const {t} = useLocalizationState()
    const interactionStepId = useActiveInteractionStepId()
    const {changeReporter} = useCaseStateMethods()
    const {formatFullName} = useFormatting()
    const reportedByName = firstName || lastName ? formatFullName(firstName, lastName) : t.GLOBAL.unidentified()
    const openSearchContactModal = useOpenSearchContactModal()

    const handleReporterChange = useCallback(
        function handleReporterChangeCallback(contact: ContactShortType) {
            if (interactionStepId) {
                changeReporter(interactionStepId, caseId, contact)
            }
        },
        [caseId, changeReporter, interactionStepId],
    )
    const openChangeCaseReporterModal = useCallback(
        function handleChangeReporterCallback() {
            openSearchContactModal({
                dataKey: 'change_reporter_modal',
                onSelect: handleReporterChange,
                title: t.CALENDAR_EVENT_FORM.contact_search_dialog_heading(),
            })
        },
        [handleReporterChange, openSearchContactModal, t.CALENDAR_EVENT_FORM],
    )
    return (
        <div>
            {reportedByName}
,
            {' '}
            {interactionMode ? (
                <LinkLikeButton onClick={openChangeCaseReporterModal}>
                    {t.EMAIL.case_view_reporter_label()}
                </LinkLikeButton>
            ) : (
                <u>{t.EMAIL.case_view_reporter_label()}</u>
            )}
        </div>
    )
}
