import React, {useCallback, useEffect, useState} from 'react'

import {useBoolean} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {StateSetter} from '@bpinc/lib-types-helpers'
import type {SearchResultItem} from '@bpinc/lib-user-search-components'
import {MultipleUserSearch} from '@bpinc/lib-user-search-components'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {User} from './atoms'


type UserSelectProps = {
    className?: string
    user: User | undefined
    placeholder?: string
    setUser: StateSetter<User | undefined>
}

const empty: SearchResultItem[] = []

export const UserSelect: React.FC<UserSelectProps> = ({
    className,
    user,
    placeholder,
    setUser,
}) => {
    const {t} = useLocalizationState()

    const open = useBoolean()
    const handleClose = useCallback(() => {
        open.setFalse()
        setSearchValue('')
        setSelectedItems([])
    }, [open])

    const [searchValue, setSearchValue] = useState('')
    const [selectedItems, setSelectedItems] = useState<SearchResultItem[]>([])

    useEffect(() => {
        const selectedItem = selectedItems[0]

        if (selectedItem) {
            handleClose()
            setSelectedItems([])

            if (selectedItem.type === 'user') {
                setUser({
                    id: selectedItem.id,
                    name: selectedItem.displayText,
                })
            }
        }

    }, [selectedItems, handleClose, setUser])

    return (
        <div className="relative flex items-center">
            {open.value ? (
                <MultipleUserSearch
                    searchForUsers
                    withSessionUser
                    className={className}
                    autoFocus
                    uniqueSearchId="calendar-owner"
                    searchValue={searchValue}
                    onClickOutside={handleClose}
                    setSearchValue={setSearchValue}
                    selectedItems={empty}
                    setSelectedItems={setSelectedItems}
                    inputPlaceholder={t.EMAIL.find_agents()}
                    loadingLabel={t.GLOBAL.loading()}
                />
            ) : (
                <button
                    className={tcn(
                        'after:ml-[0.3em] after:inline-block after:h-0 after:w-0 after:border-solid after:content-["_"]',
                        'after:border-x-[0.3em] after:border-b-[0.1em] after:border-t-[0.3em] after:border-[var(--c-icon)_transparent_transparent]',
                    )}
                    onClick={open.toggle}
                >
                    {user?.name || placeholder}
                </button>
            )}
        </div>
    )
}
