import type {RefObject} from 'react'
import React, {useCallback} from 'react'
import type {FieldValues, UseFormReturn} from 'react-hook-form'

import {useContactFormDefinition, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactDetailsUnflatten} from '@bpinc/ad-contacts-state-types'
import {unflatten} from '@bpinc/lib-flat'
import {createLeadMappingStringToFormValueMap} from '@bpinc/lib-form-builder'
import {dirtyValues as getDirtyValues} from '@bpinc/lib-forms-base'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SemanticButton as ButtonUI} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ProfileActions} from './ProfileActions'


interface EditActionsProps {
    contactId: string
    actionsRef: RefObject<HTMLDivElement>
    onEditMode?: (edit: boolean) => void
    form: UseFormReturn | null
}

export const EditActions: React.FC<EditActionsProps> = ({
    contactId,
    actionsRef,
    onEditMode,
    form,
}) => {
    const {t} = useLocalizationState()

    const {showNotification} = useNotificationsState('methods')

    const {saveContactDetails} = useContactsStateMethods()

    const contactDetailsFormDefinition = useContactFormDefinition('CONTACT_DETAIL')

    const handleSave = useCallback(() => {
        const dirtyValues = getDirtyValues(
            form?.formState.dirtyFields || {},
            form?.getValues() || {},
        )
        const onSuccess = () => onEditMode?.(false)
        const onError = () => {
            onEditMode?.(false)
            showNotification({
                type: 'error',
                id: Notifications.ContactSaveError,
                tags: ['current-view'],
                message: t.CONTACT.save_error(),
            })
        }

        const onValid = () => {
            assertDefined(contactDetailsFormDefinition)

            if (!Object.keys(dirtyValues).length) {
                onEditMode?.(false)
                return
            }

            const flatContactDetails = createLeadMappingStringToFormValueMap(dirtyValues, contactDetailsFormDefinition)
            const newDetails = unflatten<FieldValues, ContactDetailsUnflatten>(flatContactDetails)

            saveContactDetails(contactId, newDetails, {onSuccess, onError})
        }

        form?.handleSubmit(onValid)()
    }, [
        form,
        onEditMode,
        showNotification,
        t.CONTACT,
        contactDetailsFormDefinition,
        saveContactDetails,
        contactId,
    ])


    return (
        <ProfileActions profileActionsRef={actionsRef}>
            <ButtonUI onClick={handleSave} basic primary>
                {t.UNDEFINED.button_save_contact_text()}
            </ButtonUI>

            <ButtonUI onClick={() => onEditMode?.(false)} basic color="orange">
                {t.UNDEFINED.button_cancel_contact_text()}
            </ButtonUI>
        </ProfileActions>
    )
}
