import {useEffect} from 'react'
import {useIsFetching, useQueryClient} from '@tanstack/react-query'

import {queryKeys} from './query-keys'


export function useQueueCountInvalidator() {
    const queryClient = useQueryClient()
    const isFetchingQueue = !!useIsFetching({queryKey: queryKeys.queue()})

    /** Invalidate (refetch) queue count, when queue request is refetched */
    useEffect(() => {
        if (isFetchingQueue) {
            queryClient.invalidateQueries({queryKey: queryKeys.queueCount()})
        }
    }, [isFetchingQueue, queryClient])
}
