import type {FC} from 'react'
import React from 'react'
import produce from 'immer'
import {type PrimitiveAtom, useAtom} from 'jotai'

import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {Pagination as PaginationType} from './atoms'


interface PaginationProps {
    paginationAtom: PrimitiveAtom<PaginationType>
    actualQueueCount: number
    totalQueueCount: number | undefined
}

export const Pagination: FC<PaginationProps> = ({
    actualQueueCount,
    paginationAtom,
    totalQueueCount = actualQueueCount,
}) => {
    const {t} = useLocalizationState()
    const [pagination, setPagination] = useAtom(paginationAtom)

    const noMoreItems = actualQueueCount + pagination.skip * pagination.size >= totalQueueCount
    const noLessItems = pagination.skip === 0
    const paginationFrom = pagination.skip * pagination.size + 1
    const paginationTo = noMoreItems ? totalQueueCount : actualQueueCount + pagination.skip * pagination.size

    return (
        <div className="flex items-center">
            <p className="mr-2 text-sm text-gray-500">
                {t.GLOBAL.pagination(
                    paginationFrom,
                    paginationTo,
                    totalQueueCount,
                )}
            </p>
            <SemanticButton
                transparent
                icon="chevron left"
                className="mr-2"
                onClick={() =>
                    setPagination(
                        produce(s => {
                            s.skip--
                        }),
                    )
                }
                disabled={noLessItems}
            />
            <SemanticButton
                transparent
                icon="chevron right"
                onClick={() =>
                    setPagination(
                        produce(s => {
                            s.skip++
                        }),
                    )
                }
                disabled={noMoreItems}
            />
        </div>
    )
}
