import {useEffect, useMemo} from 'react'
import type {UseFormReturn} from 'react-hook-form'
import {debounce} from 'lodash-es'

import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useInteractionWorkspaceState} from '@bpinc/ad-main-page-ui-context'
import type {FormDefinition} from '@bpinc/ad-request-urls'
import {useAsRef, useEvent} from '@bpinc/lib-helpers'


export const useActiveItemFormEffects = ({
    pageIndex,
    formDefinition,
    form,
}: {
    form: UseFormReturn | null
    pageIndex: number
    formDefinition?: FormDefinition
}) => {
    const {shownWorkspaceItem} = useInteractionWorkspaceState()
    const activeItemRef = useAsRef(shownWorkspaceItem)
    const {setFormValues, setNotes} = useInteractionStateMethods()

    const pageFields = useMemo(() => {
        return formDefinition?.layout?.pages?.[pageIndex]?.fields || []
    }, [formDefinition?.layout?.pages, pageIndex])

    useEffect(() => {
        if (shownWorkspaceItem?.state === 'wrap_up') {
            form?.trigger()
        }
    }, [shownWorkspaceItem?.state, form])

    const resetForm = useEvent(() => {
        form?.reset(shownWorkspaceItem?.details?.formData?.values)
    })

    useEffect(() => {
        resetForm()
    }, [pageIndex, resetForm])

    const debouncedSetFormValues = useMemo(() =>
        debounce((formData: Record<string, unknown>) => {
            if (!activeItemRef.current?.id) {
                return
            }
            setFormValues(activeItemRef.current?.id, formData)
        }, 300), [activeItemRef, setFormValues])

    const debouncedSetNotes = useMemo(() =>
        debounce((notes: string[]) => {
            if (!activeItemRef.current?.id) {
                return
            }
            setNotes({
                itemId: activeItemRef.current?.id,
                notes,
            })
        }, 300), [activeItemRef, setNotes])

    const onFormChange = useEvent((fields: Record<string, any>) => {
        if (!activeItemRef.current) {
            return
        }

        const mergedValues = {
            ...activeItemRef.current?.details?.formData?.values,
            ...fields,
        }

        /**
         * structuredClone is needed to remove frozen objects created by immer
         */
        const newFormValues = structuredClone(mergedValues)

        debouncedSetFormValues(newFormValues)

        const notes = pageFields
            .filter(fieldItem =>
                fieldItem.field?.type === 'CUSTOM' &&
                fieldItem.field?.customType === 'NOTES',
            )
            .map(fieldItem => fieldItem.field?.name || '')
            .map(fieldName => String(newFormValues[fieldName])) || []

        if (notes.length) {
            debouncedSetNotes(notes)
        }
    })

    useEffect(() => {
        const subscription = form?.watch(onFormChange)
        return () => subscription?.unsubscribe()
    }, [form, onFormChange])
}
