import React from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {getInitials} from '@bpinc/ad-formatting-helpers'
import {useActiveInteraction} from '@bpinc/ad-interaction-state-context'
import {
    getFirstAndLastName,
    isCallConference,
    isChatConference,
    isEmail,
    isGroupChat,
} from '@bpinc/ad-interaction-state-methods'
import {useUnread} from '@bpinc/lib-helpers-ui'
import {Avatar} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {UnreadBadge} from '@bpinc/lib-ui-elements'


interface InteractionBarAvatarProps {
    className?: string
}

export const InteractionBarAvatar: React.FC<InteractionBarAvatarProps> = ({className}) => {
    const activeItem = useActiveInteraction()

    assertDefined(activeItem)

    const getAvatarUrl = useGetAvatarUrl()
    const {userId, contactId} = activeItem
    const {firstName, lastName} = getFirstAndLastName(activeItem)

    function getAvatarType() {
        if (isGroupChat(activeItem) || isCallConference(activeItem) || isChatConference(activeItem)) {
            return 'conference'
        }

        if (contactId) {
            return 'contact'
        }

        if (userId) {
            if (activeItem?.displayData.emiUserType === 'msteams_channel') {
                return 'msteams-channel'
            }
            if (activeItem?.displayData.emiUserType === 'msteams') {
                return 'msteams-user'
            }
            return 'user'
        }

        if (isEmail(activeItem)) {
            return 'case'
        }

        return 'contact'
    }


    const avatarPhotoUrl = isGroupChat(activeItem)
        ? undefined
        : getAvatarUrl({
            userId,
            contactId,
            userType: activeItem?.displayData?.emiUserType,
            photoUrl: activeItem?.displayData?.emiPhotoUrl,
        })

    const unread = useUnread(activeItem)

    return (
        <div className="relative">
            <Avatar
                className={className}
                type={getAvatarType()}
                fallback={getInitials(firstName, lastName)}
                src={avatarPhotoUrl}
                size="32"
            />
            <UnreadBadge unreadAmount={unread} className="absolute bottom-0 right-0 translate-x-1/2" />
        </div>
    )
}
