import React from 'react'
import {get} from 'lodash-es'

import type {ActivityEmail as ActivityEmailType} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {useInteractionStateRef} from '@bpinc/ad-interaction-state-context'
import {getDownloadEmailUrl} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {downloadURL} from '@bpinc/lib-helpers'
import {
    LegacyIconEmail,
    LegacyIconOriginalEmail,
    LegacyIconReply,
    LegacyIconSingleStepTransfer,
} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {Avatar, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useShowOriginalEmailContent} from '../email-utils'
import {EmailContent} from './EmailContent'


interface Props {
    activity: ActivityEmailType
    caseId: string
}

export const ActivityEmail: React.FC<Props> = ({activity, caseId}) => {
    const {t} = useLocalizationState()
    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()
    const {serverOrigin, sessionId} = useSessionState([
        'serverOrigin',
        'sessionId',
    ])
    const {createdTime, emailData, direction, parties} = activity
    const partyInternal = parties.find(p => p.partyType === 'USER')
    const partyExternal = parties.find(p => p.partyType !== 'USER')
    const isOutbound = direction !== 'INBOUND'

    const sender = (emailData.fromAddresses || [])[0]
    // const receivers = (emailData.toAddresses || []).map(a => a.name).join(', ')

    const fullName = formatFullName(
        ...((sender && sender.name) || 'Unidentified').split(' '),
    )
    const date = formatDate(createdTime)

    const title = [fullName, 'sent on', date].join(' ')

    const avatar = {
        firstName: get(isOutbound ? partyInternal : partyExternal, 'firstName'),
        lastName: get(isOutbound ? partyInternal : partyExternal, 'lastName'),
        size: 40,
        url: getAvatarUrl({
            userId: isOutbound ? partyInternal?.userId : undefined,
            contactId: isOutbound ? undefined : partyExternal?.contactId,
            notFoundPlaceholder: true,
        }),
    }
    const iconButtonClassName = tcn('!h-5 !w-5 [&_svg]:h-full [&_svg]:w-full')
    const showOriginalEmail = useShowOriginalEmailContent(activity)
    function handleDownloadEmail() {
        assertDefined(sessionId)
        return downloadURL(
            getDownloadEmailUrl(serverOrigin, sessionId, activity.emailId),
            'originalMessage',
        )
    }

    const interactionsStateRef = useInteractionStateRef()
    function handleReplyEmail() {
        const itemByCaseId = interactionsStateRef.current?.items.find(i => {
            return i.emailData?.caseId === caseId
        })

        if (itemByCaseId) {
            /** In the old AD we have these lines of code:
             *
             * eventBus.selectInteraction(interactionModel.getLinker().getHost(any.get()));
             * eventBus.navigateTo(DeskNavigationPresenter.Item.CONTEXT);
             *
             * However, it seems, that we dont need this, as we are already in the context of the case.
             * */

            const emailDraftInput = findEmailDraftInput(activity.emailId)

            if (emailDraftInput instanceof HTMLElement) {
                emailDraftInput.scrollIntoView()
                emailDraftInput.focus()
            } else {

                //TODO: create email draft
            }

        }

        //TODO: Reply email
        //TODO: Make logic as here: https://github.com/ServicePattern/ServicePattern/blob/6afd9f598a1d237b6feea92b815e3341cd18b084/Web/agentdesktop/src/com/brightpattern/agentdesktop/client/cases/presenter/CaseEventHandler.java#L91
        function findEmailDraftInput(emailId: string) {
            return document.querySelector(`[data-email-editor-reference-id="${emailId}"]>div`)
        }

    }

    const controls = (
        <>
            <SemanticButton
                transparent
                icon
                className={iconButtonClassName}
                tooltip={t.EMAIL.show_original_email_body_button_tooltip()}
                onClick={showOriginalEmail}
            >
                <LegacyIconOriginalEmail fill="var(--c-icon)" />
            </SemanticButton>
            <SemanticButton
                transparent
                icon
                className={iconButtonClassName}
                onClick={handleReplyEmail}
            >
                <LegacyIconReply fill="var(--c-icon)" />
            </SemanticButton>
            <SemanticButton transparent icon className={iconButtonClassName}>
                <LegacyIconSingleStepTransfer fill="var(--c-icon)" />
            </SemanticButton>
            <SemanticButton
                size="big"
                transparent
                icon="download"
                tooltip={t.EMAIL.download_original()}
                onClick={handleDownloadEmail}
            />
        </>
    )

    return (
        <div className="group relative flex p-2">
            <div className="mr-2 flex-none">
                <Avatar
                    size={avatar.size}
                    firstName={avatar.firstName}
                    lastName={avatar.lastName}
                    url={avatar.url}
                />
            </div>
            <div className="flex-auto bg-[--c-activity-bg] p-2">
                <div className="flex">
                    <div className="mr-2 mt-1 h-4 w-4 flex-none self-start [&_svg]:h-full [&_svg]:w-full">
                        <LegacyIconEmail />
                    </div>
                    <div className="mr-2 text-gray-500">{title}</div>

                    <div className="hidden flex-none text-[--c-controls-text] group-hover:!flex">
                        {controls}
                    </div>
                </div>

                <div className="mt-2">
                    <EmailContent
                        title={emailData.subject}
                        html={emailData.content}
                    />
                </div>

                <div className="mt-2 text-right text-[--c-controls-text]">
                    {controls}
                </div>
            </div>
        </div>
    )
}
