import type {RefObject} from 'react'
import React, {useCallback} from 'react'
import type {UseFormReturn} from 'react-hook-form'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {Toggle} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SemanticButton as ButtonUI} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ProfileActions} from './ProfileActions'


interface CreateActionsProps {
    actionsRef: RefObject<HTMLDivElement>
    closeCreateTab: Toggle
    form: UseFormReturn | null
    onContactCreated?: (contactId: string) => void
}

export const CreateActions: React.FC<CreateActionsProps> = ({
    actionsRef,
    closeCreateTab,
    form,
    onContactCreated,
}) => {
    const {t} = useLocalizationState()

    const {showNotification} = useNotificationsState('methods')

    const {createContact} = useContactsStateMethods()

    const handleSave = useCallback(() => {
        assertDefined(form)

        const onSuccess = (contactId: string) => {
            onContactCreated?.(contactId)
            closeCreateTab()
        }
        const onError = () => {
            showNotification({
                type: 'error',
                id: Notifications.ContactSaveError,
                tags: ['current-view'],
                message: t.CONTACT.save_error(),
            })
        }

        const onValid = () => {
            createContact({details: form.getValues()}, {onSuccess, onError})
        }

        form?.handleSubmit(onValid)()

    }, [form, onContactCreated, closeCreateTab, showNotification, t.CONTACT, createContact])

    return (
        <ProfileActions profileActionsRef={actionsRef}>
            <ButtonUI onClick={handleSave} basic primary>
                {t.UNDEFINED.button_save_contact_text()}
            </ButtonUI>
            <ButtonUI onClick={closeCreateTab} basic color="orange">
                {t.UNDEFINED.button_cancel_contact_text()}
            </ButtonUI>
        </ProfileActions>
    )
}
