import React, {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react'

import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import {useStatsState} from '@bpinc/ad-stats-state-context'
import {IconArrowDown24, TriggerDropdown} from '@bpinc/lib-jskit-ui'
import type {Option} from '@bpinc/lib-ui-elements'

import {Stats} from './Stats'


interface Props {
    rotatePeriod: number
}

export const Metrics: React.FC<Props> = ({rotatePeriod}) => {
    const rotateTimeoutRef = useRef<number | null>(null)
    const {agentStats} = useStatsState()
    const {services} = useServicesDispositionsState()

    const servicesOptions = useMemo(() => {
        return Object.values(services)
            .filter(s => s.id in agentStats)
            .map(s => ({value: s.id, label: s.name}))
    }, [services, agentStats])

    const [selectedService, setSelectedService] = useState<
        Option | undefined
    >()

    const statsData = useMemo(() => {
        const serviceId = selectedService?.value
        return (serviceId && agentStats[serviceId]) || undefined
    }, [selectedService, agentStats])

    const selectNextService = useCallback(() => {
        if (servicesOptions.length > 1) {
            setSelectedService(currentSelectedService => {
                if (currentSelectedService) {
                    const currentIndex = servicesOptions.findIndex(s => {
                        return currentSelectedService.value === s.value
                    })
                    if (currentIndex > -1) {
                        const nextIndex =
                            (currentIndex + 1) % servicesOptions.length
                        return servicesOptions[nextIndex]
                    }
                }
                return servicesOptions[0]
            })
        }
    }, [servicesOptions])

    const stopServiceRotation = useCallback(() => {
        if (rotateTimeoutRef.current) {
            clearInterval(rotateTimeoutRef.current)
        }
    }, [])

    const startServiceRotation = useCallback(() => {
        if (rotatePeriod) {
            rotateTimeoutRef.current = setInterval(
                selectNextService,
                rotatePeriod * 1000,
            )
        }
    }, [rotatePeriod, selectNextService])

    const handleServiceChange = useCallback(
        (option: Option) => {
            setSelectedService(option)
            stopServiceRotation()
            startServiceRotation()
        },
        [startServiceRotation, stopServiceRotation],
    )

    useEffect(() => {
        if (!selectedService && servicesOptions.length) {
            setSelectedService(servicesOptions[0])
        }
    }, [selectedService, servicesOptions, startServiceRotation])

    useEffect(() => {
        if (!rotateTimeoutRef.current && statsData) {
            startServiceRotation()
        }
        // start rotation after at least some stats are loaded
        // eslint-disable-next-line
    }, [statsData]);

    useEffect(() => {
        return stopServiceRotation
        // onComponentDidMount
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    if (!selectedService || !statsData) return null

    return (
        <div className="flex flex-col items-start overflow-hidden px-4 has-[[data-show-more=true]]:flex-auto">
            {/* TODO: Replace with the select component once added to jskit-ui */}
            <TriggerDropdown
                align="end"
                options={servicesOptions.map(service => (
                    <button key={service.value} onClick={() => handleServiceChange(service)}>{service.label}</button>
                ))}
            >
                <button className="group flex items-center gap-0.5 font-semibold button text-neutral-on-secondary">
                    {selectedService.label}
                    <IconArrowDown24 className="icon-default group-data-[state=open]:rotate-180" />
                </button>
            </TriggerDropdown>
            <Stats data={statsData} selectedService={selectedService.label} />
        </div>
    )
}
