import React, {useCallback, useMemo} from 'react'
import type {Table} from '@tanstack/react-table'
import {useAtom} from 'jotai'

import {useAgentState, useAgentStateMethods} from '@bpinc/ad-agent-state-context'
import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useOpenEmailBySupervisorWarningModal} from '@bpinc/ad-modals-ui'
import type {QueueItem} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {IconForward} from '@bpinc/lib-icons'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {useSelectedRows} from '../../useSelectedRow'
import {AssignEmailModal} from '../AssignEmailModal'
import {personalActiveQueueItemAtom, personalTabUserAtom} from '../atoms'
import {useCreateNoteMutation} from '../mutations'
import {UserSelect} from '../UserSelect'


export function usePersonalControls(table: Table<QueueItem>) {
    const {t} = useLocalizationState()
    const isSupervisor = useSessionState('isSupervisor')
    const selectedRows = useSelectedRows(personalActiveQueueItemAtom, table)
    const {moveQueueItems, createCaseEmail} = useCaseStateMethods()
    const {notReadyReasons, selectedAgentStateOption} = useAgentState()
    const {setState} = useAgentStateMethods()
    const openEmailBySupervisorWarningModal =
        useOpenEmailBySupervisorWarningModal()
    const {userId: selfUserId} = useSessionState()

    const moveQItemToQ = useCallback(
        function handleGrabCallback() {
            assertDefined(selfUserId)
            moveQueueItems({
                itemsToMove: selectedRows,
                directions: {
                    toQueueType: 'TEAM',
                    fromQueueType: 'PERSONAL',
                    fromUserId: selfUserId,
                },
            })
            table.resetRowSelection()
        },
        [moveQueueItems, selectedRows, selfUserId, table],
    )

    const handleComposeEmail = useCallback(
        function handleComposeEmailCallback() {
            const isSupervisionState = selectedAgentStateOption?.state === ''
            if (isSupervisionState) {
                openEmailBySupervisorWarningModal({
                    okAction: changeAgentStateAndCreateEmail,
                })
            } else {
                createCaseEmail()
            }

            function changeAgentStateAndCreateEmail() {
                setState({state: 'not_ready', reason: notReadyReasons?.[0]})
                createCaseEmail()
            }
        },
        [
            selectedAgentStateOption?.state,
            openEmailBySupervisorWarningModal,
            createCaseEmail,
            setState,
            notReadyReasons,
        ],
    )

    const [user, setUser] = useAtom(personalTabUserAtom)
    const openAssignEmailModal = useModalComponentOpener(AssignEmailModal)
    const {mutate: createNote} = useCreateNoteMutation()
    const handleAssignEmail = useCallback(() => {
        openAssignEmailModal({
            queueItemsToMove: selectedRows,
            fromUserId: user.id,
            createNote,
        })
    }, [createNote, openAssignEmailModal, selectedRows, user.id])

    return useMemo(
        function memoizeControls() {
            return (
                <div className="flex justify-between px-2 pb-2 pt-3">
                    <div className="flex">
                        {isSupervisor && (
                            <SemanticButton
                                icon
                                basic
                                onClick={handleAssignEmail}
                                tooltip={t.EMAIL.email_assign_button_tooltip()}
                            >
                                <IconForward />
                            </SemanticButton>
                        )}
                        <SemanticButton onClick={moveQItemToQ} primary basic>
                            {t.EMAIL.email_move_to_team_queue_button_tooltip()}
                        </SemanticButton>
                    </div>
                    <UserSelect
                        user={user}
                        setUser={setUser}
                    />
                    <SemanticButton
                        basic
                        onClick={handleComposeEmail}
                        tooltip={t.ADDRESS_BOOK_AREA.addressBook_button_email_tooltip()}
                        icon="envelope"
                    />
                </div>
            )
        },
        [handleAssignEmail, handleComposeEmail, isSupervisor, moveQItemToQ, setUser, t.ADDRESS_BOOK_AREA, t.EMAIL, user],
    )
}
