import type {FC} from 'react'
import React, {useMemo, useState} from 'react'

import {useCaseState, useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {CaseStatus} from '@bpinc/ad-request-urls'
import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import {SmIcon, SmList} from '@bpinc/lib-external-ui-elements'
import {useBoolean} from '@bpinc/lib-helpers'
import {Button, Dropdown, Popup, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {SetDispositionModal} from './SetDispositionModal'


interface SetCaseStatusButtonProps {
    caseId: string
    service: string
    initialPendingReasonId?: string
    initialStatus: CaseStatus
}

type Option = {
    text: string
    value: CaseStatus
}

export const SetCaseStatusButton: FC<SetCaseStatusButtonProps> = ({
    initialStatus,
    caseId,
    initialPendingReasonId,
    service,
}) => {
    const {t} = useLocalizationState()
    const showPopup = useBoolean()
    const [status, setStatus] = useState<CaseStatus>(initialStatus)

    const stateOptions: Option[] = useMemo(() => {
        return [
            {
                text: t.GLOBAL.case_status_open(),
                value: 'Open',
            },
            {
                text: t.GLOBAL.case_status_pending(),
                value: 'Pending',
            },
            {
                text: t.GLOBAL.case_status_resolved(),
                value: 'Resolved',
            },
        ]
    }, [t])

    const {setCaseStatus} = useCaseStateMethods()
    const useDisposition = useServicesDispositionsState(
        s => s.services[service].useDispositions,
    )
    const openSetDipositionModal = useModalComponentOpener(SetDispositionModal)
    const handleSubmit = (statusToSubmit: CaseStatus) => {
        switch (statusToSubmit) {
            case 'Pending': {
                const pendingReason = pendingReasons.data.find(r => r.id === selectedPendingReason)

                return setCaseStatus({caseId, status: statusToSubmit, pendingReason})
            }
            case 'Resolved': {
                if (!useDisposition) {
                    return setCaseStatus({caseId, status: statusToSubmit})
                }

                const onSave = (
                    disposition: { id: string; name: string } | undefined,
                ) => {
                    setCaseStatus({
                        caseId,
                        status: statusToSubmit,
                        disposition,
                    })
                }

                openSetDipositionModal({service, onSave})
                break
            }
            default:
                setCaseStatus({caseId, status: statusToSubmit})
        }
    }

    const [selectedPendingReason, setSelectedPendingReason] = useState<
        string | undefined
    >(initialPendingReasonId)
    const pendingReasons = useCaseState('casePendingReasons')

    const pendingReasonsOptions = useMemo(() => {
        return pendingReasons.data.map(({id, name}) => ({
            text: name,
            value: id,
        }))
    }, [pendingReasons.data])

    const disabled =
        status === 'Pending'
            ? initialPendingReasonId === selectedPendingReason
            : initialStatus === status
    return (
        <div className="flex items-center gap-2">
            {!!pendingReasonsOptions.length && (
                <Dropdown
                    options={pendingReasonsOptions}
                    placeholder={t.GLOBAL.case_pending_reason_selector_no_value()}
                    selection
                    value={selectedPendingReason}
                    onChange={(_evt, {value}) =>
                        typeof value === 'string' &&
                        setSelectedPendingReason(value)
                    }
                />
            )}
            <div className="flex h-9 shrink-0 rounded border border-primary-legacy hover:border-primary-hover active:border-primary-active">
                <Popup
                    show={showPopup.value}
                    content={
                        <SmList divided>
                            {stateOptions.map(({text, value}) => (
                                <SmList.Item key={value}>
                                    <Button
                                        onClick={() => {
                                            setStatus(value)
                                            showPopup.setFalse()
                                        }}
                                        className="w-full p-2"
                                    >
                                        {text}
                                    </Button>
                                </SmList.Item>
                            ))}
                        </SmList>
                    }
                    onClickOutside={showPopup.setFalse}
                    placement="bottom"
                >
                    <SemanticButton
                        icon={showPopup.value ? 'chevron up' : 'chevron down'}
                        primary
                        basic
                        onClick={showPopup.toggle}
                        className="rounded-r-none bg-transparent !shadow-none"
                    />
                </Popup>
                <SemanticButton
                    onClick={() => handleSubmit(status)}
                    disabled={disabled}
                    className="shrink-0 rounded-l-none border-l border-main-border bg-transparent pl-0 font-normal text-primary-legacy"
                >
                    {`${t.GLOBAL.submitAs()}: ${status}`}
                    {!disabled && (
                        <SmIcon className="ml-1 mr-0" name="check" />
                    )}
                </SemanticButton>
            </div>
        </div>
    )
}
