import type {FC} from 'react'
import React from 'react'
import {getCoreRowModel, useReactTable} from '@tanstack/react-table'
import {useAtomValue} from 'jotai'

import type {QueueItem} from '@bpinc/ad-request-urls'

import {useTeamQueueQuery} from '../../../queries'
import type {SpecificTabProps} from '../../types'
import {
    serviceFilterAtom,
    teamActiveQueueItemAtom,
    teamIdAtom,
    teamPaginationAtom,
    teamSortingAtom,
} from '../atoms'
import {CaseTab} from '../CaseTab'
import {TableHeaderContent} from './TableHeaderContent'
import {TeamCaseFooterContent} from './TeamCaseFooterContent'
import {useColumns} from './useColumns'
import {useTeamControls} from './useTeamControls'


const initialData: QueueItem[] = []

export const TeamTab: FC<SpecificTabProps> = props => {
    const teamId = useAtomValue(teamIdAtom)
    const serviceFilter = useAtomValue(serviceFilterAtom)
    const sorting = useAtomValue(teamSortingAtom)
    const pagination = useAtomValue(teamPaginationAtom)
    const {data, isFetched} = useTeamQueueQuery({
        services: !serviceFilter || serviceFilter === 'all' ? undefined : [serviceFilter],
        team: teamId,
        length: pagination.size,
        skip: pagination.skip,
        sortBy: sorting.field,
        asc: sorting.direction === 'asc',
    })

    const noData = isFetched && !data?.length

    const table = useReactTable({
        columns: useColumns(),
        data: data || initialData,
        getCoreRowModel: getCoreRowModel(),
        getRowId: row => row.caseId,
    })

    const QueueControls = useTeamControls(table)
    return (
        <CaseTab
            noData={noData}
            CaseFooterContent={<TeamCaseFooterContent table={table} canViewCase />}
            TableHeaderContent={
                <TableHeaderContent
                    actualQueueCount={data?.length ?? 0}
                    noData={noData}
                    totalQueueCount={props.totalQueueCount}
                />
            }
            table={table}
            queueItems={data}
            QueueControls={QueueControls}
            activeQueueItemAtom={teamActiveQueueItemAtom}
            {...props}
        />
    )
}
