import type {FormEvent} from 'react'
import React, {useState} from 'react'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {QueueItemToMove} from '@bpinc/ad-case-state-types'
import {SmForm} from '@bpinc/lib-external-ui-elements'
import {TextArea} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import type {User} from './atoms'
import type {CreateNoteMutationParams} from './mutations'
import {UserSelect} from './UserSelect'


type AssignEmailModalProps = CommonModalProps & {
    queueItemsToMove: QueueItemToMove[]
    fromUserId?: string
    createNote: (params: CreateNoteMutationParams) => void
}

export const AssignEmailModal: React.FC<AssignEmailModalProps> = ({
    onClose,
    queueItemsToMove,
    fromUserId,
    createNote,
    ...restProps
}) => {
    const {t} = useLocalizationState()
    const [user, setUser] = useState<User | undefined>(undefined)
    const [notes, setNotes] = useState('')
    const {moveQueueItems} = useCaseStateMethods()
    function handleAssign(e: FormEvent) {
        e.preventDefault()
        assertDefined(user)
        assertDefined(fromUserId)

        moveQueueItems({
            itemsToMove: queueItemsToMove,
            directions: {
                toQueueType: 'PERSONAL',
                fromQueueType: 'PERSONAL',
                toUserId: user.id,
                fromUserId,
            },
        })

        if (notes) {
            queueItemsToMove.forEach(q => {
                createNote({
                    user,
                    notes,
                    caseId: q.caseId,
                })
            })
        }
        onClose()
    }

    return (
        <Modal
            onClose={onClose}
            className="relative w-90 overflow-hidden"
            aria-labelledby="assign_email_modal"
            data-testid="AssignEmailModal"
            {...restProps}
        >
            <Modal.Header role="heading" id="assign_email_modal">
                {t.EMAIL.email_reply_assign_header()}
            </Modal.Header>
            <Modal.Content>
                <SmForm>
                    <SmForm.Field>
                        <label>{t.EMAIL.assign_to()}</label>
                        <UserSelect
                            user={user}
                            setUser={setUser}
                            placeholder={t.EMAIL.find_agent()}
                        />
                    </SmForm.Field>
                    <SmForm.Field>
                        <label>
                            {t.DISPOSITION.disposition_notes_tooltip()}
                        </label>
                        <TextArea
                            placeholder={t.DISPOSITION.disposition_notesEmptyText()}
                            value={notes}
                            onChange={e => setNotes(e.target.value)}
                        />
                    </SmForm.Field>
                </SmForm>
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={() => onClose()}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton primary onClick={handleAssign} disabled={!user}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_button_assign()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
