import {useReducer} from 'react'

import {
    useCalendarEventsDispatchers,
    useChatMessagesDispatcher,
    useInteractionDispatchers,
    useLogoutStateResetter,
    usePageVisibilityChangedDispatcher,
    useTitleBlinker,
} from './hooks'
import {initialState, PageTitleState, reducer} from './reducer'

export function usePageTitle() {
    const [state, dispatch] = useReducer(reducer, initialState)

    useTitleBlinker(state as PageTitleState)
    
    useInteractionDispatchers(dispatch)
    useCalendarEventsDispatchers(dispatch)
    useChatMessagesDispatcher(dispatch)
    usePageVisibilityChangedDispatcher(dispatch)
    
    useLogoutStateResetter(dispatch)
}
