import {useQuery} from '@tanstack/react-query'

import {apiRequests} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {MS_IN_MINUTE} from '@bpinc/lib-helpers'

import {queryKeys} from './query-keys'


export function useGetEmailQuery(emailId: string) {
    const serverOrigin = useSessionState('serverOrigin')

    return useQuery({
        enabled: !!emailId,
        queryKey: queryKeys.getEmail(emailId),
        queryFn: () => apiRequests(serverOrigin).getEmail({emailId}),
        staleTime: MS_IN_MINUTE * 5,
    })
}
