import type {FC} from 'react'
import React from 'react'

import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


type ExitSupervisingWarningModalProps = CommonModalProps & {
    actionCallback: () => void
}

export const ExitSupervisingWarningModal: FC<
    ExitSupervisingWarningModalProps
> = ({actionCallback, ...props}) => {
    const {t} = useLocalizationState()
    return (
        <Modal
            {...props}
            header={t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning()}
            content={t.WINDOW_POPUP_TITLES_AND_MESSAGES.openingEmailFromSupervisingStateConfirmation()}
            actions={
                <Modal.Actions>
                    <SemanticButton onClick={() => props.onClose()} enabledInOffline>
                        {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonClose()}
                    </SemanticButton>
                    <SemanticButton
                        primary
                        onClick={() => {
                            actionCallback()
                            props.onClose()
                        }}
                    >
                        {t.GLOBAL.ok()}
                    </SemanticButton>
                </Modal.Actions>
            }
        />
    )
}
