import uuid from 'uuid/v4'

import {platformInfo} from '@bpinc/ad-build-and-platform-info'
import type {ActivityEmail, EmailAddress} from '@bpinc/ad-case-state-types'
import {sanitizeHtml} from '@bpinc/lib-html-utils'
import type {LocalizationStateData} from '@bpinc/state-localization'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {EmailAddressTransformers} from './email-transformers'


function escapeHtml(html: string): string {
    // Simple HTML escaping function
    return html
        .replace(/&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/"/g, '&quot;')
}

/** This is a transformed to TS copy of one we have in the old AD */
export function useShowOriginalEmailContent(emailActivity: ActivityEmail) {
    const isChrome = platformInfo.browser === 'chrome'
    const {
        t: {EMAIL: i18n, ACTIVITY_HISTORY: i18nAH},
    } = useLocalizationState()

    return () => {
        const url = isChrome ? 'about:blank' : 'screenpop.html'
        const openedWindow = window.open(url, uuid())
        const htmlContent = getHTMLContent(emailActivity, i18n, i18nAH)

        if (openedWindow?.document) {
            openedWindow.document.write(sanitizeHtml(htmlContent))
            openedWindow.document.title = sanitizeHtml(
                emailActivity.emailData.subject,
            )
        }
    }
}

function getHTMLContent(
    crmActivity: ActivityEmail,
    i18n: LocalizationStateData['t']['EMAIL'],
    i18nAH: LocalizationStateData['t']['ACTIVITY_HISTORY'],
) {
    function getEraseString(emailData: ActivityEmail['emailData']): string {
        const {eraseTime, eraseReason, eraseLoginId} = emailData
        const date = eraseTime && new Date(eraseTime).toLocaleDateString()
        const time = eraseTime && new Date(eraseTime).toLocaleTimeString()

        if (!date || !time || !eraseLoginId) {
            return ''
        }

        return i18nAH.activity_view_email_content_erased(
            date,
            time,
            eraseLoginId,
            eraseReason ?? '',
        )
    }

    const styledDirections = new Set(['OUTBOUND', 'REPLY'])

    let htmlContent = `<html><head><style>
    body {
      overflow-wrap: break-word;
    }
    </style></head><body>`

    if (styledDirections.has(crmActivity.direction)) {
        htmlContent += '<div>'
    }

    const appendAddresses = (
        prefix: string,
        addresses: EmailAddress[] | undefined,
    ) => {
        if (addresses && addresses.length > 0) {
            htmlContent += `${escapeHtml(prefix)}: ${escapeHtml(
                addresses.map(EmailAddressTransformers.LABEL).join(', '),
            )}<hr>`
        }
    }

    appendAddresses(
        i18n.email_queue_field_from(),
        crmActivity.emailData.fromAddresses,
    )
    appendAddresses(
        i18n.email_address_to_prefix(),
        crmActivity.emailData.toAddresses,
    )
    appendAddresses(
        i18n.email_address_cc_prefix(),
        crmActivity.emailData.ccAddresses,
    )
    appendAddresses(
        i18n.email_address_bcc_prefix(),
        crmActivity.emailData.bccAddresses,
    )

    htmlContent += `${escapeHtml(i18n.email_subject())}: ${escapeHtml(
        crmActivity.emailData.subject,
    )}<hr>`

    if (crmActivity.emailData.eraseTime) {
        htmlContent += getEraseString(crmActivity.emailData)
    } else {
        htmlContent += crmActivity.emailData.content
    }

    if (styledDirections.has(crmActivity.direction)) {
        htmlContent += '</div>'
    }

    htmlContent += '</body></html>'

    return htmlContent
}
