import {useCallback, useEffect} from 'react'
import {useQueryClient} from '@tanstack/react-query'
import {useAtomValue} from 'jotai'

import {useCaseStateConnector} from '@bpinc/ad-case-state-context'
import {assertDefined} from '@bpinc/lib-types-helpers'

import {selectedTabAtom} from './components/CasePage/atoms'
import {queryKeys} from './queries'


export function useQueryManager() {
    const caseStateConnector = useCaseStateConnector()
    const queryClient = useQueryClient()
    const tab = useAtomValue(selectedTabAtom)

    const invalidateQueriesForCurrentTab = useCallback(() => {
        assertDefined(tab)

        queryClient.invalidateQueries({queryKey: queryKeys.queueCount()})
        queryClient.invalidateQueries({queryKey: queryKeys.queue(tab.queueType)})

    }, [queryClient, tab])


    //TODO: Add optimistic updates
    useEffect(() => {
        const subscription = caseStateConnector.dispenser.subscribe({
            onQueueItemsMoved: invalidateQueriesForCurrentTab,
            onCasesDeleted: invalidateQueriesForCurrentTab,
            onCasesClosedAsSpam: invalidateQueriesForCurrentTab,
        })
        return () => {
            subscription.unsubscribe()
        }
    }, [caseStateConnector.dispenser, invalidateQueriesForCurrentTab, queryClient])
}
