import type {FC} from 'react'
import React from 'react'
import {motion} from 'framer-motion'

import {useActiveInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getMediaIcon} from '@bpinc/ad-interaction-state-methods'
import {EndButton} from '@bpinc/lib-interaction-buttons'
import {InteractionMediaIcon, InteractionTimer} from '@bpinc/lib-ui-elements'

import {interactionBarStyles} from '../styles'
import {ContactInfo} from './ContactInfo'


interface DefaultContentProps {
    onDisconnect: () => void
    disconnectButtonTooltip?: string
}

export const DefaultContent: FC<DefaultContentProps> = ({onDisconnect, disconnectButtonTooltip}) => {
    const iconType = useActiveInteractionProp(getMediaIcon)
    const deliveredStartTime = useActiveInteractionProp(
        i => i?.displayData.deliveredStartTime,
    )

    return (
        <div className={interactionBarStyles}>
            <motion.div className="flex items-center pl-4" whileHover="hover">
                <InteractionMediaIcon className="icon-default" icon={iconType} />
                <InteractionTimer
                    className="ml-1 mr-2"
                    startTime={deliveredStartTime}
                />
                <motion.div
                    initial={{width: 0, opacity: 0, pointerEvents: 'none'}}
                    variants={{
                        hover: {width: 'auto', opacity: 1, pointerEvents: 'auto'},
                    }}
                >
                    <EndButton onClick={onDisconnect} tooltip={disconnectButtonTooltip} withStateLayer />
                </motion.div>
            </motion.div>
            <ContactInfo />
        </div>
    )
}
