import type {FC} from 'react'
import React, {useCallback} from 'react'
import type {Row} from '@tanstack/react-table'
import {flexRender} from '@tanstack/react-table'

import type {QueueItem as QueueItemType} from '@bpinc/ad-request-urls'
import {SmTable} from '@bpinc/lib-external-ui-elements'
import type {StateSetter} from '@bpinc/lib-types-helpers'


interface QueueItemRowProps {
    row: Row<QueueItemType>
    isActive: boolean
    isDisabled?: boolean
    setActiveQueueItem: StateSetter<QueueItemType | undefined>
}

export const QueueItemRow: FC<QueueItemRowProps> =
    ({
        isActive,
        isDisabled,
        setActiveQueueItem,
        row,
    }) => {

        const handleClick = useCallback(
            function handleClickCallback() {
                setActiveQueueItem(row.original)
            },
            [row.original, setActiveQueueItem],
        )


        return (
            <SmTable.Row
                key={row.id}
                data-element="queue-item"
                onClick={handleClick}
                active={isActive}
                disabled={isDisabled}
            >
                {row.getVisibleCells().map(cell => (
                    <SmTable.Cell key={cell.id} >
                        {flexRender(
                            cell.column.columnDef.cell,
                            cell.getContext(),
                        )}
                    </SmTable.Cell>
                ))}
            </SmTable.Row>
        )
    }
