import React, {useCallback, useMemo} from 'react'
import type {OnChangeFn} from '@tanstack/react-table'
import {type RowSelectionState, flexRender, getCoreRowModel, useReactTable} from '@tanstack/react-table'
import {useSetAtom} from 'jotai'

import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import {SmTable} from '@bpinc/lib-external-ui-elements'

import {selectedContactIdAtom} from '../atoms'
import {ContactsTableRow} from './ContactsTableRow'
import {getContactTableColumnWidth, useContactTableColumns} from './useContactTableColumns'


type ContactsTableProps = {
    data: ContactShortType[]
    selectedContactId?: string
}

export const ContactsTable = ({
    data,
    selectedContactId,
}: ContactsTableProps) => {
    const setSelectedContactId = useSetAtom(selectedContactIdAtom)
    const rowSelection = useMemo<RowSelectionState>(() => {
        return selectedContactId ? {[selectedContactId]: true} : {}
    }, [selectedContactId])

    const onRowSelectionChange = useCallback<OnChangeFn<RowSelectionState>>(updaterOrValue => {
        const newValue = typeof updaterOrValue === 'function' ? updaterOrValue(rowSelection) : updaterOrValue
        const contactId = Object.entries(newValue).find(([_, selected]) => selected)?.[0]
        const contact = data.find(c => c.id === contactId)
        setSelectedContactId(contact?.id || data[0]?.id)
    }, [data, rowSelection, setSelectedContactId])

    const table = useReactTable({
        columns: useContactTableColumns(),
        data,
        getCoreRowModel: getCoreRowModel(),
        getRowId: row => row.id,
        enableMultiRowSelection: false,
        state: {
            rowSelection,
        },
        onRowSelectionChange,
    })

    const headers = table.getHeaderGroups()[0].headers

    return (
        <div
            className="overflow-auto"
            onKeyDown={e => {
                if (['Enter', ' '].includes(e.key)) {
                    e.preventDefault()
                }
            }}
        >
            <SmTable compact striped className="w-full border-none">
                <colgroup>
                    {headers.map(h => (
                        <col
                            key={h.id}
                            style={{
                                width: getContactTableColumnWidth(h.column),
                            }}
                        />
                    ))}
                </colgroup>
                <SmTable.Header className="sticky top-0 z-[1]">
                    {headers.map(h => (
                        <SmTable.HeaderCell key={h.id}>
                            {flexRender(
                                h.column.columnDef.header,
                                h.getContext(),
                            )}
                        </SmTable.HeaderCell>
                    ))}
                </SmTable.Header>
                <SmTable.Body>
                    {table.getRowModel().rows.map(row => (
                        <ContactsTableRow
                            key={row.id}
                            row={row}
                        />
                    ))}
                </SmTable.Body>
            </SmTable>
        </div>
    )
}
