import type {FC} from 'react'
import React from 'react'
import {Button} from '@servicepattern/ui'

import {useActiveInteractionProp} from '@bpinc/ad-interaction-state-context'
import {useDispositionsSettings} from '@bpinc/ad-interaction-state-hooks'
import {getItemDisposition} from '@bpinc/ad-interaction-state-methods'
import {useCommunicatorUIState} from '@bpinc/ad-main-page-ui-context'
import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'
import {useOpenDispositionPanel} from '@bpinc/lib-interaction-ui-hooks'
import {IconCheck16} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useInteractionUIActiveTab} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface DispositionButtonProps {
    className?: string
}

export const DispositionButton: FC<DispositionButtonProps> = ({className}) => {
    const {t} = useLocalizationState()
    const interactionId = useNonAssertedInteractionId() ?? ''

    const communicatorFloatingState = useCommunicatorUIState(s => s.floatingState)
    const selectedDisposition = useActiveInteractionProp(getItemDisposition)
    const {useDispositions} = useDispositionsSettings(interactionId)
    const activeTab = useInteractionUIActiveTab(interactionId)
    const handleClick = useOpenDispositionPanel(interactionId)

    const buttonText = selectedDisposition?.id ? t.DISPOSITION.disposition_set() : t.DISPOSITION.set_disposition()

    if (!useDispositions) {
        return null
    }

    return (
        <Button
            onClick={handleClick}
            tooltip={buttonText}
            state={activeTab === 'resolution' && communicatorFloatingState !== 'closed' ? 'disabled' : 'default'}
            size="sm"
            variant="secondary"
            iconLeft={selectedDisposition?.id ? <IconCheck16 className="svg-color-current" /> : undefined}
            className={tcn('ml-6 shrink-0', className)}
        >
            {buttonText}
        </Button>
    )
}
