import {useMemo, useRef, useState} from 'react'
import type {Control} from 'react-hook-form'
import {useController} from 'react-hook-form'
import {debounce} from 'lodash-es'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatEmail} from '@bpinc/ad-formatting-helpers'
import type {EmailContact} from '@bpinc/ad-request-urls'
import {useDidUnmount} from '@bpinc/lib-helpers'

import {useSearchEmailsQuery} from '../../queries/useSearchEmailsQuery'
import type {EmailFormFields, EmailSearchItem} from './types'


export function useSelectedEmailsState(name: 'to' | 'cc' | 'bcc', control: Control<EmailFormFields>) {
    const [value, setValue] = useState('')

    const [searchValue, setSearchValue] = useState('')

    const {field} = useController({
        name,
        control,
        defaultValue: [],
    })

    useDidUnmount(() => {
        field.onChange(undefined)
    })

    const debouncedSetSearchValue = useRef(
        debounce(setSearchValue, 200),
    ).current

    const handleOnChange = (v: string) => {
        setValue(v)
        debouncedSetSearchValue(v)
    }


    const addSelectedItem = (item: EmailSearchItem) => {
        field.onChange([...field.value, item])
        setValue('')
    }

    const removeSelectedItem = (item: EmailSearchItem) => {
        field.onChange(field.value.filter(i => i.address !== item.address))
    }

    const addCustomEmail = (newEmail: string) => {
        addSelectedItem({
            id: newEmail,
            displayText: newEmail,
            address: newEmail,
        })
    }

    const {data: queriedContacts} = useSearchEmailsQuery(searchValue)

    const {formatFullName} = useFormatting()

    const suggestedItems: EmailSearchItem[] = useMemo(() => {
        const getName = (contact: EmailContact) => formatFullName(
            contact.contactFirstName,
            contact.contactLastName,
        )

        return (
            queriedContacts
                ?.filter(c => !!c.email)
                .map(contact => ({
                    id: contact.email!,
                    address: contact.email!,
                    name: getName(contact),
                    displayText: formatEmail({address: contact.email!, name: getName(contact)}),
                    contactId: contact.contactId,
                })) || []
        )
    }, [formatFullName, queriedContacts])

    return {
        value,
        selectedItems: field.value,
        handleOnChange,
        addSelectedItem,
        addCustomEmail,
        removeSelectedItem,
        suggestedItems,
    }
}

