import React, {useMemo} from 'react'

import {sanitizeHtml} from '@bpinc/lib-html-utils'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Frame} from '@bpinc/lib-ui-elements'


interface Props {
    content: string
    className?: string
}

export const FrameTabContent: React.FC<Props> = ({content, className}) => {
    const innerHTML = useMemo(() => ({
        __html: sanitizeHtml(content),
    }), [content])

    return (
        <Frame className={tcn('h-full w-full', className)}>
            <div dangerouslySetInnerHTML={innerHTML} />
        </Frame>
    )
}
