import React, {useMemo} from 'react'
import {Navigate, Route, Routes} from 'react-router-dom'

import {useBrowserTabMetaInfo} from '@bpinc/ad-current-tab-data-context'
import {AppRoutes} from '@bpinc/ad-navigation-state-context'
import {useSessionState} from '@bpinc/ad-session-context'

import {routesConfig} from '../../constants/routes'


export const PrivilegeRouter: React.FC = () => {
    const {privileges, navigationData} = useSessionState(['privileges', 'navigationData'])
    const browserTabInfo = useBrowserTabMetaInfo()
    const appType = browserTabInfo?.app

    const allowedRoutes = useMemo(() => {
        return Object.values(routesConfig).filter(({checkPrivileges}) => !checkPrivileges || checkPrivileges(privileges))
    }, [privileges])

    const fallbackRoute = useMemo(() => {
        const privilegesAreReady = !!Object.values(privileges).length
        if (!privilegesAreReady || !navigationData) {
            return null
        }

        const lastRoute = allowedRoutes.find(r => r.path === navigationData.route[appType])
        if (!lastRoute) {
            return routesConfig[AppRoutes.home]
        }

        return lastRoute
    }, [allowedRoutes, appType, navigationData, privileges])

    return (
        <Routes>
            {allowedRoutes.map(({allowSubRoutes, path, ...routeProps}, index) => (
                <Route
                    key={index}
                    path={`${path}${allowSubRoutes ? '/*' : ''}`}
                    {...routeProps}
                />
            ))}

            {fallbackRoute && (
                <Route
                    path="*"
                    element={
                        <Navigate to={fallbackRoute.path} />
                    }
                />
            )}
        </Routes>
    )
}
