import React, {useCallback, useEffect, useMemo, useState} from 'react'

import {useActivitiesState, useActivitiesStateMethods} from '@bpinc/ad-activities-state-context'
import type {ActivitiesListItemType, PartyUserShortType} from '@bpinc/ad-activities-state-types'
import {useContact} from '@bpinc/ad-contacts-state-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {toHHMMSS} from '@bpinc/lib-helpers'
import {LegacyIconCallInbound, LegacyIconCallOutbound, LegacyIconChat} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {TableColumn} from '@bpinc/lib-ui-elements'
import {Table} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {formatDate, isPartyUserShortType} from '../utils/helpers'
import {Activity} from './Activity'


interface Props {
    contactId: string
}

export const Activities: React.FC<Props> = ({contactId}) => {
    const {
        activitiesList,
        activitiesListLoading,
    } = useActivitiesState(['activitiesList', 'activitiesListLoading'])
    const {getActivitiesList} = useActivitiesStateMethods()
    const {t} = useLocalizationState()
    const {formatFullName} = useFormatting()
    const activitiesRowIconClassName = tcn('mt-px h-4 w-4 align-middle')

    const contact = useContact(contactId)?.data

    const defaultSelectedIndex = activitiesList.length ? 0 : -1

    const columns: TableColumn[] = useMemo(
        function memoizeColumns() {
            return [
                {name: 'icon', title: '', width: '36px'},
                {
                    name: 'time',
                    title: t.UNDEFINED.activity_datetime(),
                    width: '15%',
                },
                {
                    name: 'agent',
                    title: t.UNDEFINED.activity_agent(),
                    width: '20%',
                },
                {
                    name: 'disposition',
                    title: t.UNDEFINED.activity_disposition(),
                    width: '15%',
                },
                {
                    name: 'notes',
                    title: t.UNDEFINED.activity_details(),
                    width: '50%',
                },
            ]
        },
        [t],
    )

    useEffect(() => {
        if (!contact) {
            return
        }
        getActivitiesList({id: contact.Id.oid})
    }, [contact, getActivitiesList])

    const [selectedRowIndex, setSelectedRowIndex] = useState(
        defaultSelectedIndex,
    )

    useEffect(() => {
        setSelectedRowIndex(defaultSelectedIndex)
        // do not track defaultSelectedIndex
        // eslint-disable-next-line
    }, [contact, activitiesList, setSelectedRowIndex])

    const renderItem = useCallback((item: ActivitiesListItemType, column: TableColumn) => {
        switch (column.name) {
            case 'icon': {
                if (item.mediaType === 'CHAT') {
                    return <LegacyIconChat className={activitiesRowIconClassName} />
                }
                if (item.mediaType === 'VOICE') {
                    return item.direction === 'OUTBOUND'
                        ? <LegacyIconCallOutbound className={activitiesRowIconClassName} />
                        : <LegacyIconCallInbound className={activitiesRowIconClassName} />
                }
                return ''
            }
            case 'time': {
                return formatDate(item.createdTime.date)
            }
            case 'agent': {
                return item.parties.filter(isPartyUserShortType)?.map(agent => formatFullName(agent.firstName, agent.lastName)).filter(Boolean).join(', ') ?? ''
            }
            case 'disposition': {
                return item.parties.filter(isPartyUserShortType)?.map(agent => agent.disposition).filter(Boolean).join(', ') ?? ''
            }
            case 'notes': {
                const getNote = (agent: PartyUserShortType) => {
                    const duration = agent.duration && toHHMMSS(agent.duration)
                    const {notes} = agent
                    return [duration, notes].filter(Boolean).join(', ')
                }
                return item.parties.filter(isPartyUserShortType)?.map(getNote).filter(Boolean).join('; ') ?? ''
            }
            default: {
                return ''
            }
        }
    }, [activitiesRowIconClassName, formatFullName])

    const rowClassName = useCallback((item: ActivitiesListItemType, i: number) => {
        return tcn({'bg-highlight-folder-color': selectedRowIndex === i})
    }, [selectedRowIndex])

    const selectedActivity = activitiesList[selectedRowIndex]

    const onRowClick = useCallback((
        _e: React.MouseEvent | React.KeyboardEvent,
        _item: ActivitiesListItemType,
        i: number,
    ) => {
        return setSelectedRowIndex(i)
    }, [])

    const activitiesDividerClassName = tcn(
        'absolute left-1/2 right-0 top-[15px] ml-[-3px] h-full w-1.5',
        'after:m-auto after:block after:w-px after:bg-[--c-activities-divider]',
    )

    return (
        <div className="relative h-full flex-auto">
            <div className="absolute left-0 top-0 flex h-full w-[calc(50%-3px)] flex-col">
                <Table
                    columns={columns}
                    data={activitiesList}
                    isLoading={activitiesListLoading}
                    rowClassName={rowClassName}
                    renderItem={renderItem}
                    onRowClick={onRowClick}
                    onRowEnterPress={onRowClick}
                />
            </div>

            <div className={activitiesDividerClassName} />

            <div className="absolute inset-y-[15px] right-0 w-[calc(50%-3px)] overflow-y-auto overflow-x-hidden">
                {selectedActivity && (
                    <Activity
                        activity={selectedActivity}
                        services={selectedActivity.services}
                        agents={selectedActivity.parties.filter(isPartyUserShortType)}
                    />
                )}
            </div>
        </div>
    )
}
