import {useMemo} from 'react'

import type {CaseStatus} from '@bpinc/ad-request-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const useCaseStatusOptions = () => {
    const {t: {GLOBAL}} = useLocalizationState()

    return useMemo(() => [
        {value: 'New', text: GLOBAL.case_status_new()},
        {value: 'Open', text: GLOBAL.case_status_open()},
        {value: 'Pending', text: GLOBAL.case_status_pending()},
        {value: 'Resolved', text: GLOBAL.case_status_resolved()},
        {value: 'Closed', text: GLOBAL.case_status_closed()},
    ] satisfies {value: CaseStatus; text: string}[], [])
}
