import {useMemo} from 'react'

import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import type {Service} from '@bpinc/ad-services-dispositions-types'


const serviceToOption = ({id, name}: Pick<Service, 'id' | 'name'>) => {
    return {
        value: id,
        text: name,
        'data-testid': 'option',
        'data-service': 'true',
    }
}

export function useEmailServicesOptions() {
    const {services} = useServicesDispositionsState()


    return useMemo(() => {
        return Object.values(services).filter(s => s.type === 'EMAIL').map(serviceToOption)
    }, [services])
}
