import React from 'react'
import type {Table} from '@tanstack/react-table'

import type {CasesListItem} from '@bpinc/ad-case-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {TeamSelector} from './TeamSelector'
import {useCloseAsSpamHandler} from './useCloseAsSpamHandler'
import {useDeleteCaseHandler} from './useDeleteCaseHandler'


type CasesListHeaderProps = {
    table: Table<CasesListItem>
}

export const CasesListHeader: React.FC<CasesListHeaderProps> = ({table}) => {
    const {t} = useLocalizationState()
    const privileges = useSessionState('privileges')
    const handleCloseAsSpam = useCloseAsSpamHandler(table)
    const handleDeleteCase = useDeleteCaseHandler(table)

    return (
        <div className="flex items-center justify-end gap-2 p-2">
            <TeamSelector />
            <SemanticButton
                onClick={handleCloseAsSpam}
                basic
                color="orange"
                className="mr-0"
            >
                {t.EMAIL.button_spam_text()}
            </SemanticButton>
            {privileges.SYS_DELETE_CASES && (
                <SemanticButton
                    onClick={handleDeleteCase}
                    basic
                    color="red"
                    icon="trash"
                    tooltip={t.EMAIL.button_trash_tooltip()}
                />
            )}
        </div>
    )
}
