import {useMutation, useQueryClient} from '@tanstack/react-query'

import type {Email} from '@bpinc/ad-request-urls'
import {apiRequests} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {useEmailAttachmentsUIState} from '@bpinc/lib-attachments-state'
import {useAsRef} from '@bpinc/lib-helpers'

import {emailFormFieldsToEmail, emailFormFieldsToEmailRequest} from '../components/EmailForm/helpers'
import type {EmailFormFields} from '../components/EmailForm/types'
import {queryKeys} from '../queries'


export function useSaveDraftMutation(emailId: string) {
    const serverOrigin = useSessionState('serverOrigin')
    const queryClient = useQueryClient()
    const getAttachmentsList = useEmailAttachmentsUIState('getAttachmentsList')
    const attachmentsRef = useAsRef(getAttachmentsList(emailId))

    return useMutation({
        mutationKey: ['saveDraft', emailId],
        mutationFn: (data: EmailFormFields) => {
            //Optimistically update getEmail query
            queryClient.setQueryData<Email>(
                queryKeys.getEmail(emailId),
                s => s && {...s, ...emailFormFieldsToEmail(data, attachmentsRef.current)},
            )

            //Send REST request
            return apiRequests(serverOrigin).saveEmailDraft({requestBody: emailFormFieldsToEmailRequest(emailId, data)})
        },
    })
}
