import React from 'react'

import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'


type MergeActionsProps = {
    onReset: () => void
    onConfirm: () => void
    isSubmitting: boolean
    disabled: boolean
}

export const MergeActions: React.FC<MergeActionsProps> = ({onReset, onConfirm, isSubmitting, disabled}) => {
    const {t} = useLocalizationState()
    const {openModal} = useModalsStateMethods()

    return (
        <div className="flex items-center justify-center p-3.5">
            <SemanticButton basic primary onClick={onConfirm} disabled={disabled} loading={isSubmitting}>
                {t.GLOBAL.confirm()}
            </SemanticButton>
            <SemanticButton
                basic
                primary
                onClick={() => {
                    openModal({
                        title: t.GLOBAL.reset(),
                        body: t.MERGE_FORM.confirm_reset(),
                        actions: {
                            ok: {
                                title: t.GLOBAL.reset(),
                                action: () => onReset(),
                            },
                            cancel: {
                                title: t.GLOBAL.cancel(),
                            },
                        },
                    })
                }}
                disabled={disabled || isSubmitting}
            >
                {t.GLOBAL.reset()}
            </SemanticButton>
        </div>
    )
}
