import type {FC} from 'react'
import React from 'react'
import {getCoreRowModel, useReactTable} from '@tanstack/react-table'
import {useAtomValue} from 'jotai'

import type {QueueItem} from '@bpinc/ad-request-urls'

import {usePersonalQueueQuery} from '../../../queries'
import type {SpecificTabProps} from '../../types'
import {personalActiveQueueItemAtom, personalPaginationAtom, personalSortingAtom, personalTabUserAtom} from '../atoms'
import {CaseTab} from '../CaseTab'
import {PersonalCaseFooterContent} from './PersonalCaseFooterContent'
import {TableHeaderContent} from './TableHeaderContent'
import {useColumns} from './useColumns'
import {usePersonalControls} from './usePersonalControls'


const initialData: QueueItem[] = []

export const PersonalTab: FC<SpecificTabProps> = props => {
    const sorting = useAtomValue(personalSortingAtom)
    const pagination = useAtomValue(personalPaginationAtom)
    const user = useAtomValue(personalTabUserAtom)
    const {data, isFetched} = usePersonalQueueQuery({
        length: pagination.size,
        skip: pagination.skip,
        sortBy: sorting.field,
        user: user.id,
        asc: sorting.direction === 'asc',
    })

    const noData = isFetched && !data?.length

    const table = useReactTable({
        columns: useColumns(),
        data: data || initialData,
        getCoreRowModel: getCoreRowModel(),
        getRowId: row => row.caseId,
    })

    const QueueControls = usePersonalControls(table)
    return (
        <CaseTab
            table={table}
            noData={noData}
            CaseFooterContent={<PersonalCaseFooterContent canViewCase />}
            TableHeaderContent={
                <TableHeaderContent
                    noData={noData}
                    actualQueueCount={data?.length ?? 0}
                    totalQueueCount={props.totalQueueCount}
                />
            }
            QueueControls={QueueControls}
            queueItems={data}
            activeQueueItemAtom={personalActiveQueueItemAtom}
            {...props}
        />
    )
}
