import type {FC} from 'react'
import React, {useCallback} from 'react'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'


interface DeleteCaseButtonProps {
    caseId: string
    caseNumber: string
    itemId: string
}

export const DeleteCaseButton: FC<DeleteCaseButtonProps> = ({caseId, caseNumber, itemId}) => {
    const {completeItem} = useInteractionStateMethods()
    const {t} = useLocalizationState()
    const {deleteCases} = useCaseStateMethods()
    const {openModal} = useModalsStateMethods()

    const handleClick = useCallback(
        function handleClickCallback() {
            function handleOk() {
                completeItem(itemId)
                deleteCases([caseId], undefined)
            }

            if (itemId) {
                openModal({
                    title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning(),
                    body: t.CASE_SEARCH.delete_case_confirmation_label(caseNumber),
                    actions: {
                        ok: {
                            title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk(),
                            action: handleOk,
                        },
                        cancel: {
                            title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel(),
                        },
                    },
                    className: 'autoSizeModal',
                })
            }
        },
        [
            caseId,
            caseNumber,
            completeItem,
            deleteCases,
            itemId,
            openModal,
            t.CASE_SEARCH,
            t.WINDOW_POPUP_TITLES_AND_MESSAGES,
        ],
    )
    return <SemanticButton basic icon="trash" onClick={handleClick} />
}
