import React, {useEffect, useState} from 'react'
import {pickBy} from 'lodash-es'

import type {ChatTranscriptMessage as Message} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {LegacyIconGear, LegacyIconPerson} from '@bpinc/lib-icons'
import {Avatar} from '@bpinc/lib-ui-elements'


interface Props {
    messages: Message[]
}

const fulfillMessages = (messages: Message[] = []): Message[] => {
    const parties: {[id: string]: Message} = {}
    const isDefined = (v: unknown) => typeof v !== 'undefined'

    messages = messages
        .map(m => {
            const id = m.partyId
            const message = pickBy(m, isDefined) as Message
            parties[id] = {
                ...parties[id],
                ...message,
            }
            return message
        })

    return messages
        .map(m => ({...parties[m.partyId], ...m}))
        .filter(({type, event}) => {
            const ignoreEvents = ['party_changed', 'party_navigation']
            if (type === 'scenario') {
                ignoreEvents.push('party_joined', 'party_left')
            }
            return !ignoreEvents.includes(event)
        })
}

const formatTime = (time: number): string => {
    return new Date(time).toLocaleTimeString(undefined, {hour: 'numeric', minute: 'numeric'})
}

const getMessageText = (message: Message): string => {
    const {event, msg} = message

    switch (event) {
        case 'party_joined': return 'has joined the chat'
        case 'party_message': return msg || ''
        case 'party_file': return 'sent a file'
        case 'party_changed': return ''
        case 'party_navigation': return ''
        case 'party_left': return 'has left the chat'
        default:
            return ''
    }
}

export const ChatContent: React.FC<Props> = ({messages}) => {
    const [list, setList] = useState<Message[]>([])
    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()

    useEffect(() => {
        setList(fulfillMessages(messages))
    }, [messages])

    return (
        <div className="bg-[--c-chat-content-bg]">
            {list.map((message, i) => {
                const {type, userId, firstName, lastName} = message
                const name = formatFullName(firstName, lastName) // || 'Chat BOT'
                const time = formatTime(message.timestamp)
                const text = getMessageText(message)

                return (
                    <div key={i} className="flex gap-2 px-2 py-[5px]">
                        <div className="ml-2.5 h-10 w-10 flex-none">
                            {type === 'internal' ? (
                                <Avatar
                                    size={40}
                                    firstName={firstName}
                                    lastName={lastName}
                                    url={getAvatarUrl({userId, notFoundPlaceholder: true})}
                                />
                            ) : (
                                <div
                                    className="h-full w-full border border-current p-1 text-[--c-chat-content-icon-text]
                                    [&_svg]:h-full [&_svg]:w-full [&_svg]:fill-current"
                                >
                                    {type === 'scenario' ? <LegacyIconGear /> : <LegacyIconPerson />}
                                </div>
                            )}
                        </div>
                        <div className="flex-auto [&_b]:mr-[0.3em]">
                            <div>
                                {name && <b>{name}</b>}
                                <span>{time}</span>
                            </div>
                            <div className="mt-1">
                                {text}
                            </div>
                        </div>
                    </div>
                )
            })}
        </div>
    )
}
