import {useEffect} from 'react'
import type {UseFormReturn} from 'react-hook-form'

import {useInteractionStateConnector} from '@bpinc/ad-interaction-state-context'
import {stringToHTML, useAsRef} from '@bpinc/lib-helpers'

import {HTML_FOOTER_CLASS} from './constants'
import type {EmailFormFields} from './types'


export function usePasteFromKB(interactionId: string, form: UseFormReturn<EmailFormFields>) {
    const interactionStateConnector = useInteractionStateConnector()
    const formRef = useAsRef(form)

    useEffect(function processPasteEvent() {
        const subs = interactionStateConnector.dispenser.subscribe({
            onPasteFromKB(toIneractionId, articleHtml) {
                if (interactionId === toIneractionId) {
                    const emailHtml = formRef.current.getValues('html')
                    const nextEmailHtml = insertBeforeFooter(emailHtml, articleHtml)

                    formRef.current.setValue('html', nextEmailHtml)
                }
            },
        })
        return () => {
            subs.unsubscribe()
        }
    }, [interactionId, interactionStateConnector, formRef])
}

function insertBeforeFooter(emailHtml: string, articleHtml: string): string {
    const element = stringToHTML(emailHtml)

    const footer = element.querySelector(HTML_FOOTER_CLASS)

    if (footer) {
        element.insertBefore(stringToHTML(articleHtml), footer)
        return element.innerHTML
    }
    return emailHtml + articleHtml
}
