import React, {useCallback, useMemo, useState} from 'react'
import type {UseFormReturn} from 'react-hook-form'

import {FormBuilder} from '@bpinc/ad-form-builder'
import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useInteractionWorkspaceState} from '@bpinc/ad-main-page-ui-context'
import {assertDefined} from '@bpinc/lib-types-helpers'

import {useActiveItemFormEffects} from './useActiveItemFormEffects'


export const ActivityFormTab: React.FC<{
    className?: string
}> = ({className}) => {
    const {shownWorkspaceItem} = useInteractionWorkspaceState()
    const {setFormPageId} = useInteractionStateMethods()

    const formDefinition = useMemo(
        () => shownWorkspaceItem?.details?.formDefinitions?.find(
            definition => definition.type === 'SCREENPOP',
        ), [shownWorkspaceItem?.details?.formDefinitions],
    )

    const values = shownWorkspaceItem?.details?.formData?.values
    const variables = shownWorkspaceItem?.details?.formData?.variables
    const pageIndex = shownWorkspaceItem?.details?.formData?.shownPageIndex || 0
    const intents = shownWorkspaceItem?.receivedIntentsIds

    const [form, setForm] = useState<UseFormReturn | null>(null)

    const changeFormPageId = useCallback(async (newPageId: string) => {
        assertDefined(shownWorkspaceItem)
        const isFormValid = await form?.trigger()
        if (!isFormValid) return
        setFormPageId(shownWorkspaceItem.id, newPageId)
    }, [form, setFormPageId, shownWorkspaceItem])

    useActiveItemFormEffects({pageIndex, formDefinition, form})

    if (!formDefinition) {
        return null
    }

    return (
        <div className={className}>
            <FormBuilder
                formDefinition={formDefinition}
                defaultValues={values}
                variables={variables}
                intents={intents}
                pageIndex={pageIndex}
                changeFormPageId={changeFormPageId}
                onFormAvailable={setForm}
                shouldResetOnChange
            />
        </div>
    )
}
