import {useEffect} from 'react'
import {useParams} from 'react-router-dom'
import produce from 'immer'
import {useSetAtom} from 'jotai'

import {useCaseState, useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {AppRoutes} from '@bpinc/ad-navigation-state-context'
import {usePrevious} from '@bpinc/lib-helpers'
import {assertDefined} from '@bpinc/lib-types-helpers'

import {searchPageActiveTabIdAtom, searchPageTabsAtom} from './atoms'
import {useMergeContactsActions} from './useMergeContactsActions'


export function useUrlBasedNavigation() {
    const params = useParams()
    const {formatFullName} = useFormatting()
    const {getContact} = useContactsStateMethods()
    const caseItem = useCaseState(s => params.caseId ? s.cases[params.caseId] : undefined)
    const {getCase} = useCaseStateMethods()

    const setTabs = useSetAtom(searchPageTabsAtom)
    const setActiveTabId = useSetAtom(searchPageActiveTabIdAtom)
    const {addContactForMerge} = useMergeContactsActions()

    /** Fetches contact by contactId from params and opens a corresponding contact tab  */
    useEffect(() => {
        openContactTab()
        async function openContactTab() {
            const contactId = params.contactId
            if (!contactId) {
                return
            }

            const contact = await getContact({id: contactId})

            if (!contact) {
                console.error(`Contact with id ${contactId} not found`)
                return
            }

            setTabs(produce(draft => {
                draft.push({
                    id: contactId,
                    type: 'contact',
                    title: formatFullName(contact.firstName, contact.lastName),
                    closeable: true,
                    props: {editMode: false, onMerge: addContactForMerge ? () => addContactForMerge(contactId) : undefined},
                })
            }))
            setActiveTabId(contactId)
            history.replaceState(null, '', AppRoutes.search)
        }
    }, [formatFullName, getContact, addContactForMerge, params.contactId, setActiveTabId, setTabs])


    /** Fetches case by caseId if there is caseId in params */
    useEffect(() => {
        const caseId = params.caseId
        if (!caseId) {
            return
        }

        getCase(caseId)
    }, [getCase, params.caseId, setActiveTabId, setTabs])

    const prevCaseItem = usePrevious(caseItem)

    /** After the case is fetched in the previous useEffect, we open a case tab in this useEffect */
    useEffect(() => {
        if (params.caseId && !prevCaseItem?.data && caseItem?.data) {
            setTabs(produce(draft => {
                assertDefined(caseItem?.data)
                assertDefined(caseItem.data.number)
                draft.push({
                    id: caseItem.data.id,
                    type: 'case',
                    title: caseItem.data.number?.toString(),
                    closeable: true,
                })
            }))
            setActiveTabId(caseItem.data.id)
            history.replaceState(null, '', AppRoutes.search)
        }
    }, [caseItem, params.caseId, prevCaseItem?.data, setActiveTabId, setTabs])
}
