import React, {useEffect} from 'react'

import {useInteractionStateSelector} from '@bpinc/ad-interaction-state-context'
import {useStatsState, useStatsStateMethods} from '@bpinc/ad-stats-state-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {Metrics} from './Metrics'


export const Dashboard: React.FC = () => {
    const {dashboardConfig} = useStatsState()
    const {getDashboardConfig} = useStatsStateMethods()
    const hasActiveInteraction = useInteractionStateSelector(s => !!s.activeItem)
    useEffect(() => {
        if (!dashboardConfig) {
            getDashboardConfig()
        }
        // onComponentDidMount
        // eslint-disable-next-line
    }, [])

    if (!dashboardConfig?.enabled) return null

    return (
        <div className={tcn('flex flex-auto items-center justify-end overflow-hidden', {'md:hidden': hasActiveInteraction})}>
            <Metrics rotatePeriod={dashboardConfig.rotatePeriod} />
        </div>
    )
}
