import {keepPreviousData, useQuery} from '@tanstack/react-query'

import type {DefaultService} from '@bpinc/ad-request-urls'
import {apiRequests} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'

import {queryKeys} from './query-keys'


type PinnedCasesQueryParams = Parameters<typeof DefaultService.getPinnedCases>[0]

export function usePinnedCasesQuery(params: PinnedCasesQueryParams = {
    limit: 100,
}) {
    const serverOrigin = useSessionState('serverOrigin')
    return useQuery({
        queryKey: queryKeys.queue('FOLLOWUP', params),
        placeholderData: keepPreviousData,
        queryFn: () => apiRequests(serverOrigin).getPinnedCases(params),
        refetchInterval: 20_000,
    })
}
