import type {FC, SyntheticEvent} from 'react'
import React, {useCallback} from 'react'
import {type PrimitiveAtom, useAtom} from 'jotai'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {type DropdownItemProps, type DropdownProps, Dropdown, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {Sorting} from './atoms'


interface SortingControlsProps {
    sortingAtom: PrimitiveAtom<Sorting>
    options: DropdownItemProps[]
    className?: string
}

export const SortingControls: FC<SortingControlsProps> = ({
    options,
    sortingAtom,
    className,
}) => {
    const [sorting, setSorting] = useAtom(sortingAtom)

    const handleSortingDirectionChange = useCallback(
        function handleSortingDirectionChangeCallback() {
            setSorting(s => ({
                ...s,
                direction: s.direction === 'asc' ? 'desc' : 'asc',
            }))
        },
        [setSorting],
    )

    const handleSortingFieldChange = useCallback(
        (_evt: SyntheticEvent, data: DropdownProps) => {
            setSorting(s => {
                if (typeof data.value !== 'string') {
                    return s
                }

                return {
                    ...s,
                    field: data.value,
                }
            })
        },
        [setSorting],
    )

    const {t} = useLocalizationState()

    return (
        <div className={tcn('ml-2 flex items-center gap-1', className)}>
            <p>{t.EMAIL.email_queue_sort_by_label('')}</p>
            <SemanticButton
                className="ml-2"
                icon={sorting.direction === 'asc' ? 'caret up' : 'caret down'}
                onClick={handleSortingDirectionChange}
                basic
                size="mini"
            />
            <Dropdown
                inline
                options={options}
                onChange={handleSortingFieldChange}
                value={sorting.field}
            />
        </div>
    )
}
