import type {ReactElement, ReactFragment} from 'react'
import React from 'react'
import {QueryClient, QueryClientProvider} from '@tanstack/react-query'

import {MS_IN_HOUR} from '@bpinc/lib-helpers'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'


const DEFAULT_CACHE_TIME = 8 * MS_IN_HOUR
const queryClient = new QueryClient({
    defaultOptions: {
        queries: {
            gcTime: DEFAULT_CACHE_TIME,
        },
    },
})

export const TanstackQueryClientProvider: FCWithChildren<ReactElement | ReactFragment> = ({children}) => {
    return (
        <QueryClientProvider client={queryClient}>
            {children}
        </QueryClientProvider>
    )
}
