import type {FC} from 'react'
import React from 'react'
import type {Row} from '@tanstack/react-table'
import {flexRender} from '@tanstack/react-table'
import {useAtom} from 'jotai'

import type {CasesListItem} from '@bpinc/ad-case-state-types'
import {SmTable} from '@bpinc/lib-external-ui-elements'

import {selectedCaseIdAtom} from '../atoms'


type CasesTableRowProps = {
    row: Row<CasesListItem>
}

export const CasesTableRow: FC<CasesTableRowProps> = ({
    row,
}) => {
    const [selectedCaseId, setSelectedCaseId] = useAtom(selectedCaseIdAtom)

    return (
        <SmTable.Row
            key={row.id + '_1'}
            tabIndex={0}
            onClick={() => setSelectedCaseId(row.id)}
            onKeyDown={(e: KeyboardEvent) => {
                if (!['Enter', ' '].includes(e.key)) {
                    return
                }
                setSelectedCaseId(row.id)
            }}
            active={row.id === selectedCaseId}
            className="cursor-pointer border-b-0"
        >
            {row.getVisibleCells().map(cell => (
                <SmTable.Cell key={cell.id}>
                    {flexRender(
                        cell.column.columnDef.cell,
                        cell.getContext(),
                    )}
                </SmTable.Cell>
            ))}
        </SmTable.Row>
    )
}
