import React, {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react'

import {useContact, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {TabI} from '@bpinc/lib-ui-elements'
import {Tabs} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ProfileHeaderView} from './ProfileHeader/ProfileHeaderView'
import {ProfileTabContent} from './ProfileTabContent'


interface Props {
    contactId: string
    className?: string
    editMode?: boolean
    onMerge?: () => void
    onEditMode?: (edit: boolean) => void
}

export const Profile: React.FC<Props> = ({contactId, className, editMode, onMerge, onEditMode}) => {
    const {t} = useLocalizationState()

    const tabs: TabI<string>[] = useMemo(() => [
        {id: 'details', title: t.CRM.crm_details_tab()},
        {id: 'activities', title: t.CRM.crm_history_tab()},
        {id: 'pending', title: t.CRM.crm_pending_tab()},
        {id: 'cases', title: t.CRM.crm_case_tab()},
    ], [t])

    const {
        getContact,
        getContactDetails,
        getContactFormsDefinitions,
    } = useContactsStateMethods()

    const [activeTabId, setActiveTabId] = useState(tabs[0].id)

    useEffect(() => {
        getContactFormsDefinitions({})
        getContact({id: contactId})
        getContactDetails({id: contactId})
    }, [contactId, getContact, getContactDetails, getContactFormsDefinitions])

    const actionsRef = useRef<HTMLDivElement>(null)

    const renderTabContent = useCallback((tab: TabI<string>) => {
        return (
            <ProfileTabContent
                key={contactId}
                tab={tab}
                editMode={editMode}
                contactId={contactId}
                actionsRef={actionsRef}
                onMerge={onMerge}
                onEditMode={onEditMode}
            />
        )
    }, [contactId, editMode, onMerge, onEditMode])

    const contact = useContact(contactId)?.data

    if (!contact) {
        return null
    }

    return (
        <div className={tcn('flex h-full flex-auto flex-col', className)}>
            <ProfileHeaderView
                contact={contact}
                actionsRef={actionsRef}
            />

            <div className="relative flex flex-auto flex-col overflow-auto">
                <Tabs
                    className="flex w-full flex-auto flex-col overflow-y-auto overflow-x-hidden"
                    tabs={tabs}
                    activeTabId={activeTabId}
                    renderTabContent={renderTabContent}
                    onChange={setActiveTabId}
                />
            </div>
        </div>
    )
}
