import React, {memo} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {useSettings} from '@bpinc/ad-main-page-ui-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useUserProfileState} from '@bpinc/ad-user-profile-state-context'
import {Avatar} from '@bpinc/lib-avatar-ui'
import {Popup, PopupButton} from '@bpinc/lib-ui-elements'

import {useMyAvatar} from './hooks/useMyAvatar'
import {useSettingsItems} from './hooks/useSettingsItems'


export const SettingsButton: React.FC = memo(() => {
    const {show, toggleShow} = useSettings()
    const items = useSettingsItems(toggleShow)
    const {avatarSrc} = useMyAvatar()
    const {teamName, lastName, firstName} = useSessionState(['teamName', 'lastName', 'firstName'])
    const userProfile = useUserProfileState('userProfile')
    const {formatFullName} = useFormatting()
    const fullName = formatFullName(firstName, lastName)
    const extension = formatPhone(userProfile?.extension)

    return (
        <PopupButton
            custom
            show={show}
            toggleShow={toggleShow}
            options={items}
            className="flex items-center"
        >
            <Popup
                mode="hover"
                content={
                    <>
                        {!!teamName && <p>{teamName}</p>}
                        {!!fullName && <p>{fullName}</p>}
                        {!!extension && <p>{extension}</p>}
                    </>
                }
            >
                <Avatar
                    firstName={userProfile?.firstName}
                    lastName={userProfile?.lastName}
                    url={avatarSrc}
                />
            </Popup>
        </PopupButton>
    )
})
