import type {ReactNode} from 'react'
import React, {useMemo} from 'react'
import {isEqual, omit} from 'lodash-es'

import type {ServerAddressType, ServerEmailType, ServerPhoneType} from '@bpinc/ad-contacts-state-types'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {STATES} from '@bpinc/ad-location-data'
import {Image} from '@bpinc/lib-form-builder-ui'
import {parseMessengers} from '@bpinc/lib-forms-base'
import {useGetMessengerName} from '@bpinc/lib-ui-elements'

import {MergeContactInfo} from './MergeContactInfo'
import type {
    MergeContactData,
    MergeFormArrayFields,
    MergeFormContactData,
    MergeFormContactRow,
    MergeFormRowData,
} from './types'
import {useGetMergeFormFieldValidator} from './useGetMergeFormFieldValidator'


export function useMergeFormFields(fromRows: MergeFormRowData[], contacts: MergeContactData[]): MergeFormContactData[] {
    const getMessengerName = useGetMessengerName()
    const getMergeFormFieldValidator = useGetMergeFormFieldValidator()


    return useMemo(() => {
        const mergeContactsData: MergeFormContactData[] = []
        const arrayFieldsCount: Record<MergeFormArrayFields, number> = {
            phones: 0,
            emails: 0,
            addresses: 0,
            messengers: 0,
        }

        contacts.forEach(({details: contactDetails, data: contactData, isLoading}) => {
            const contactDataRows: MergeFormContactRow[] = []
            mergeContactsData.push({rows: contactDataRows})

            fromRows.forEach(({mapping, type, element}) => {
                const fields: MergeFormContactRow['fields'] = []
                contactDataRows.push({fields})

                const filedValue = contactDetails && mapping ? contactDetails[mapping] : null
                const stringFileValue = typeof filedValue === 'string' ? filedValue : ''

                if (isLoading || !mapping || (!filedValue && mapping !== 'id')) {
                    return
                }

                const getArrayFieldName = (arrayType: MergeFormArrayFields, value: object) => {
                    const name = mergeContactsData
                        .map(d => d.rows)
                        .flat()
                        .map(r => r.fields)
                        .flat()
                        .find(v => v.value === JSON.stringify(omit(value, 'id')))
                        ?.name

                    if (name) {
                        return name
                    }

                    const index = arrayFieldsCount[arrayType]
                    arrayFieldsCount[arrayType]++

                    return `${mapping}.[${index}]`
                }

                const otherContactsValues = contacts
                    .filter(c => c.data?.Id.oid !== contactData?.Id.oid && c.details)
                    .map(c => c.details?.[mapping])
                    .filter(Boolean)

                const firstContactSringifiedValue = typeof otherContactsValues[0] === 'string'
                    ? otherContactsValues[0]
                    : undefined

                const addSingleField = (content: ReactNode, value: string) => {
                    const allValuesIsEqual = otherContactsValues.length
                        ? otherContactsValues.every(v => isEqual(v, value))
                        : true

                    fields.push({
                        name: mapping,
                        disabled: allValuesIsEqual,
                        defaultValue: firstContactSringifiedValue || value,
                        content,
                        type: allValuesIsEqual ? 'checkbox' : 'radio',
                        value,
                    })
                }

                const addArrayField = (content: ReactNode, value: object, arrayType: MergeFormArrayFields) => {
                    const name = getArrayFieldName(arrayType, value)
                    const hasSameValue = contacts
                        .filter(c => c.data?.Id.oid !== contactData?.Id.oid && c.details)
                        .map(c => c.details?.[mapping])
                        .every(values => {
                            return Array.isArray(values) && values.some(v => isEqual(omit(v, 'id'), omit(value, 'id')))
                        })

                    const stringifiedValue = JSON.stringify(omit(value, 'id'))

                    fields.push({
                        name,
                        disabled: hasSameValue,
                        defaultValue: hasSameValue ? stringifiedValue : undefined,
                        content: content,
                        type: 'checkbox',
                        value: stringifiedValue,
                        validate: getMergeFormFieldValidator(arrayType),
                    })
                }

                if (mapping === 'id') {
                    fields.push({
                        name: mapping,
                        disabled: false,
                        type: 'radio',
                        defaultValue: firstContactSringifiedValue || contactData?.Id.oid || '',
                        content: <MergeContactInfo contact={contactData} />,
                        value: contactData?.Id.oid || '',
                    })
                    return
                }
                switch (element?.type) {
                    case 'phoneList': {
                        const phones = (filedValue as ServerPhoneType[]).filter(item => item.type === type)
                        phones.forEach(phone => {
                            addArrayField(phone.phone, phone, 'phones')
                        })
                        return
                    }
                    case 'emailList': {
                        const emails = (filedValue as ServerEmailType[]).filter(item => item.type === type)
                        emails.forEach(email => {
                            addArrayField(email.email_address, email, 'emails')
                        })
                        return
                    }
                    case 'addressList': {
                        const addresses = (filedValue as ServerAddressType[]).filter(item => item.type === type)
                        addresses.forEach(address => {
                            const formattedAddress = [
                                address.address_line1,
                                address.address_line2,
                                address.city,
                                STATES[address.state as keyof typeof STATES],
                                address.postcode,
                            ]
                                .filter(Boolean)
                                .join(', ')

                            addArrayField(
                                formattedAddress,
                                address,
                                'addresses',
                            )
                        })

                        return
                    }
                    case 'messengers': {
                        const messengers = parseMessengers(filedValue)?.filter(item => item.type === type) || []
                        messengers.forEach(messenger => {
                            addArrayField(
                                `${getMessengerName(messenger.type)} ${messenger?.user_name}`,
                                messenger,
                                'messengers',
                            )
                        })
                        return
                    }
                    case 'dateTime': {
                        addSingleField(formatDate(stringFileValue, {birthday: true}), stringFileValue)
                        return
                    }
                    case 'image': {
                        addSingleField(<Image value={stringFileValue} />, stringFileValue)
                        return
                    }
                    default: {
                        addSingleField(stringFileValue, stringFileValue)
                    }
                }
            })
        })

        return mergeContactsData
    }, [contacts, fromRows, getMessengerName])
}

