import React, {useCallback, useMemo, useState} from 'react'
import {useAtom} from 'jotai'

import {useLocalStorageForState} from '@bpinc/ad-local-settings-context'
import {LegacyIconContacts, LegacyIconSearchCases} from '@bpinc/lib-icons'
import type {SVGRComponent} from '@bpinc/lib-types-helpers'
import {SearchInput, Tabs} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {searchQueryAtom} from './atoms'
import {CasesLayout} from './Cases'
import {Contacts} from './Contacts'


interface SearchPageConditionTabType {
    id: string
    title: string
    Icon: SVGRComponent
}


export const Search: React.FC = () => {
    const {t} = useLocalizationState()
    const [searchQuery, setSearchQuery] = useAtom(searchQueryAtom)

    const conditionTabs: SearchPageConditionTabType[] = useMemo(() => {
        return [
            {
                id: 'contacts',
                title: t.UNDEFINED.search_tab_contacts(),
                Icon: LegacyIconContacts,
            },
            {
                id: 'cases',
                title: t.UNDEFINED.search_tab_cases(),
                Icon: LegacyIconSearchCases,
            },
        ]
    }, [t])

    const [activeConditionTabId, setActiveConditionTabId] = useState(conditionTabs[0].id)

    useLocalStorageForState(activeConditionTabId, 'adSearchActiveTab', setActiveConditionTabId, conditionTabs[0].id)

    const handleKeywordClear = useCallback(() => setSearchQuery(''), [setSearchQuery])
    const handleKeywordChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
        setSearchQuery(e.target.value)
    }, [setSearchQuery])

    const renderHeader = useCallback((content: React.ReactNode) => {
        return (
            <div className="mx-[15%] flex flex-auto flex-row flex-nowrap items-center justify-center">
                <SearchInput
                    className="mr-4"
                    tabIndex={0}
                    value={searchQuery}
                    onChange={handleKeywordChange}
                    placeholder={t.UNDEFINED.contact_search_input_placeholder()}
                    clearable
                    onClear={handleKeywordClear}
                />
                {content}
            </div>
        )
    }, [searchQuery, handleKeywordChange, t.UNDEFINED, handleKeywordClear])

    const renderTab = useCallback(({title, Icon}: SearchPageConditionTabType) => {
        return (
            <div className="flex whitespace-nowrap py-[5px] text-lg leading-[30px]">
                <Icon className="mr-2.5 h-[30px] w-[30px] align-top [&_path]:fill-main" />
                {title}
            </div>
        )
    }, [])

    const renderTabContent = useCallback(
        (tab: SearchPageConditionTabType) => {
            switch (tab.id) {
                case 'contacts':
                    return (
                        <Contacts searchId={searchQuery} />
                    )
                case 'cases':
                    return (
                        <CasesLayout />
                    )
            }
            return null
        },
        [],
    )
    return (
        <Tabs
            className="flex flex-auto flex-col overflow-auto"
            tabsContentClassName="flex-auto h-auto flex flex-col"
            tabs={conditionTabs}
            activeTabId={activeConditionTabId}
            renderHeader={renderHeader}
            renderTab={renderTab}
            renderTabContent={renderTabContent}
            onChange={setActiveConditionTabId}
        />
    )
}
