import type {FC, RefObject} from 'react'
import {useCallback, useMemo, useState} from 'react'
import type {Control} from 'react-hook-form'
import {useController} from 'react-hook-form'
import {Editor} from '@tinymce/tinymce-react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {TinyMCEConfig} from '@bpinc/lib-tinymce'
import {getTinymceScriptSrc, tinymceConfig} from '@bpinc/lib-tinymce'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {Loader} from '@bpinc/lib-ui-elements'

import type {EmailFormFields} from '../types'


interface WysiwygEditorProps {
    control: Control<EmailFormFields>
    emailId: string
    inputRef?: RefObject<Editor>
    referencedEmailId?: string
}

export const WysiwygEditor: FC<WysiwygEditorProps> = ({control, emailId, inputRef, referencedEmailId}) => {
    const {field} = useController({name: 'html', control})
    const [isEditorLoading, setEditorLoading] = useState(true)

    const handleEditorInit = useCallback(() => setEditorLoading(false), [])

    /** Adds the variables plugin */
    const initConfig = useMemo<TinyMCEConfig>(() => ({
        ...tinymceConfig,
        inline: true,
        inline_boundaries: false,
        fixed_toolbar_container: `#email-editor-toolbar-${emailId}`,
        init_instance_callback: function(editor) {
            // This will trick the editor into thinking it was focused
            // without actually focusing it (causing the toolbar to appear)
            editor.fire('focus')
        },
        setup: function(editor) {
            // This prevents the blur event from hiding the toolbar
            editor.on('blur', function() {
                return false
            })
        },
    }), [])

    const handleOnChange = useCallback((value: string) => {
        field.onChange(value)
    }, [field])

    assertDefined(process.env.PUBLIC_URL)

    const id = `email-wysiwyg-editor:${emailId}`

    return (
        <div
            className={tcn('relative mb-4 flex w-full flex-auto', '[&>div]:flex-auto')}
            data-test-id={id}
            data-email-editor-reference-id={referencedEmailId}
            data-email-editor-id={emailId}
        >
            <Editor
                ref={inputRef}
                id={id}
                init={initConfig}
                onEditorChange={handleOnChange}
                onInit={handleEditorInit}
                tinymceScriptSrc={getTinymceScriptSrc(process.env.PUBLIC_URL)}
                value={field.value}
            />
            {isEditorLoading && <Loader active color="primary" size="medium" />}
        </div>
    )
}
