import React from 'react'
import type {Control} from 'react-hook-form'
import {useController, useWatch} from 'react-hook-form'

import {SmCheckbox} from '@bpinc/lib-external-ui-elements'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import type {MergeContactFormType, MergeFormElementField} from './types'


type MergeFieldProps = {
    field: MergeFormElementField
    control: Control<MergeContactFormType>
}

export const MergeField: React.FC<MergeFieldProps> = ({field: fieldData, control}) => {
    const {field: {
        name,
        onChange,
        value,
    }} = useController({
        name: fieldData.name,
        defaultValue: fieldData.defaultValue,
        control,
    })

    const formValues = useWatch({control})

    const label = (
        <label
            htmlFor={name}
            className={tcn('[&:after]:!top-1/2 [&:after]:!-translate-y-1/2 [&:before]:!top-1/2 [&:before]:!-translate-y-1/2', {
                '[&:after]:!scale-[47%]': fieldData.type === 'radio',
                '[&:after]:!opacity-95': value === fieldData.value,
            })}
        >
            {fieldData.content}
        </label>
    )

    return (
        <SmCheckbox
            name={name}
            type={fieldData.type}
            radio={fieldData.type === 'radio'}
            label={label}
            disabled={fieldData.disabled}
            onChange={(_, data) => {
                if (data.checked && fieldData.validate && !fieldData.validate(formValues)) {
                    return
                }
                if (data.type === 'radio') {
                    onChange(data.value)
                } else if (data.type === 'checkbox') {
                    onChange(data.checked ? data.value : null)
                }
            }}
            value={fieldData.value}
            checked={value === fieldData.value}
        />
    )
}
