import {useMemo} from 'react'

import {useLocalizationState} from '@bpinc/state-localization-context'

import {useDefaultServicesByPrivileges} from './useDefaultServicesByPrivileges'


export function useDefaultQueueServiceOptionsByPrivileges() {
    const {t} = useLocalizationState()

    const localizations = useMemo(() => ({
        all: t.EMAIL.email_teamQueue_allServices(),
        router: t.EMAIL.email_teamQueue_allPullServices(),
        team: t.EMAIL.email_teamQueue_allPushServices(),
    }), [t.EMAIL])

    const servicesByPrivileges = useDefaultServicesByPrivileges()
    return useMemo(() => {
        return ['all' as const, ...servicesByPrivileges].map(value => ({
            value,
            text: localizations[value],
            'data-testid': 'option',
            'data-service': 'true',
        }))
    }, [localizations, servicesByPrivileges])

}
