import {useMemo} from 'react'

import type {CasesSortBy} from '@bpinc/ad-request-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const useCaseOrderOptions = () => {
    const {t: {EMAIL}} = useLocalizationState()
    return useMemo(() => [
        {value: 'score', text: EMAIL.email_queue_field_relevance()},
        {value: 'createTime', text: EMAIL.email_queue_field_createTime()},
        {value: 'updateTime', text: EMAIL.email_queue_field_updateTime()},
        {value: 'customerUpdate', text: EMAIL.email_queue_field_customerUpdate()},
        {value: 'slaTarget', text: EMAIL.email_queue_field_slaTarget()},
        {value: 'sla', text: EMAIL.email_queue_field_serviceLevel()},
        {value: 'flag', text: EMAIL.email_queue_field_flag()},
        {value: 'service', text: EMAIL.search_queue_case_content_last_service()},
        {value: 'disposition', text: EMAIL.search_queue_case_content_disposition()},
        {value: 'status', text: EMAIL.search_queue_case_content_state()},
    ] satisfies {value: CasesSortBy; text: string}[], [EMAIL])
}
