import type {FC} from 'react'
import React, {useCallback, useEffect, useMemo, useState} from 'react'
import {Button} from '@servicepattern/ui'
import {useAtomValue} from 'jotai'
import {throttle} from 'lodash-es'

import {
    MAIN_CONTENT_MIN_WIDTH,
    MAX_COMMUNICATOR_WIDTH_IN_PERCENT,
    MIN_COMMUNICATOR_WIDTH,
    useCommunicatorUIState,
    useMainPageUIState,
} from '@bpinc/ad-main-page-ui-context'
import {AppRoutes, useNavigationState} from '@bpinc/ad-navigation-state-context'
import {useResizeObserver} from '@bpinc/lib-helpers'
import {IconPin24, IconPinFilled24} from '@bpinc/lib-jskit-ui'
import {Notifications} from '@bpinc/lib-notifications-context'
import {Notifications as NotificationsUI} from '@bpinc/lib-notifications-ui'
import type {OnDragEndParams} from '@bpinc/lib-ui-elements'
import {canPinFloating, createComponentLoader, Floating} from '@bpinc/lib-ui-elements'
import {parallelItemsAtom} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import styles from '../../styles/MainPage.module.less'


const ERROR_IDS_TO_SHOW = [Notifications.ContactCreationError, Notifications.ContactMergeError]
const EXCEPTION_SELECTORS = ['[data-chat-actions-menu]', '[data-testid="channel-selector"]', '[data-modal="true"]']
const {CommunicatorWidgetLoader} = createComponentLoader('communicator', import('communicator'), 'CommunicatorWidget')
const {ParallelChatInteractionLoader} = createComponentLoader('communicator', import('communicator'), 'ParallelChatInteraction')

export const CommunicationPanel: FC = () => {
    const {floatingState, setFloatingState, width, setWidth, openerRef, setParallelInteractionsWidth, parallelInteractionsWidth} = useCommunicatorUIState()

    const {t} = useLocalizationState()
    const {kbFloatingState, mainContentNode} = useMainPageUIState(['mainContentNode', 'kbFloatingState'])
    const mainContentObserverEntry = useResizeObserver(mainContentNode)

    const [mainContentWidth, setMainContentWidth] = useState(0)
    const throttledSetMainContentWidth = useMemo(() => throttle(size => setMainContentWidth(size), 200), [])

    useEffect(() => {
        throttledSetMainContentWidth(mainContentObserverEntry?.contentRect.width ?? 0)
    }, [mainContentObserverEntry?.contentRect.width, throttledSetMainContentWidth])

    const getLocation = useNavigationState('getLocationState')
    const isWorkspacePage = getLocation() === AppRoutes.interactionWorkspace
    const parallelItems = useAtomValue(parallelItemsAtom)

    const contentMinWidth = MAIN_CONTENT_MIN_WIDTH - (kbFloatingState === 'pinned' ? 15 : 0)

    const showParallelItems = parallelItems.length > 0 && isWorkspacePage

    const onDragEnd = useCallback(
        function onDragEndCallback({size}: OnDragEndParams) {
            if (showParallelItems) {
                setParallelInteractionsWidth(size)
                setWidth(size / (parallelItems.length + 1))
            } else {
                setWidth(size)
            }
        },
        [parallelItems.length, setParallelInteractionsWidth, setWidth, showParallelItems],
    )

    const canPin = useMemo(() => {
        return canPinFloating({
            mainContentWidth,
            floatingState,
            floatingSize: showParallelItems ? parallelInteractionsWidth : width,
            contentMinWidth: contentMinWidth,
        })
    }, [floatingState, width, mainContentWidth, contentMinWidth, showParallelItems, parallelInteractionsWidth])

    const onClickPin = useCallback(() => {
        setFloatingState(state => (state === 'pinned' ? 'floating' : 'pinned'))
    }, [setFloatingState])

    return (
        <Floating
            side="left"
            resizeable
            maxSizeInPercent={MAX_COMMUNICATOR_WIDTH_IN_PERCENT}
            minSize={showParallelItems ? MIN_COMMUNICATOR_WIDTH * (parallelItems.length + 1) : MIN_COMMUNICATOR_WIDTH}
            data-testid="communicator_panel"
            className={styles.communicator}
            state={floatingState}
            setState={setFloatingState}
            showEar={false}
            openerRef={openerRef}
            size={showParallelItems ? parallelInteractionsWidth : width}
            onDragEnd={onDragEnd}
            canPin={canPin}
            unmountOnHide={false}
            exceptionSelectors={EXCEPTION_SELECTORS}
            closeByEscape={floatingState === 'floating'}
        >
            <NotificationsUI show={ERROR_IDS_TO_SHOW} />
            <div className="grid flex-auto grid-cols-[repeat(auto-fit,minmax(100px,1fr))] overflow-hidden bg-neutral-100">
                <div className="flex flex-auto flex-col overflow-hidden bg-primary-background">
                    {canPin && (
                        <Button
                            variant="ghost"
                            className="absolute right-2 top-2"
                            icon={floatingState === 'pinned' ? <IconPinFilled24 /> : <IconPin24 />}
                            onClick={onClickPin}
                            tooltip={t.COMMUNICATION_PANEL.pin_tooltip()}
                        />
                    )}
                    <CommunicatorWidgetLoader />
                </div>
                {showParallelItems && parallelItems.map(i => <ParallelChatInteractionLoader parallelItem={i} key={i.activeItemId} />)}
            </div>
        </Floating>
    )
}
