import React from 'react'

import {useOpenHelpModal} from '@bpinc/ad-modals-ui'
import {IconHelp, IconLogo, IconTopbarNewMessage} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Button} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {Dashboard} from './dashboard'
import {InteractionControlBar} from './interaction-control-bar'
import {SettingsButton} from './SettingsButton'


const iconButtonStyles = tcn(
    'grid size-8 place-content-center',
    'rounded',
    'transition-colors hover:state-layer-opacity-4 focus:state-layer-opacity-4 active:state-layer-opacity-8',
    'state-layer-bg-[--c-main-bars-bg] state-layer-color-black state-layer-opacity-0',
)

export const TopBar: React.FC = () => {
    const {t} = useLocalizationState()
    const openHelpModal = useOpenHelpModal()
    return (
        <div
            data-testid="top-bar"
            className="relative flex h-[--topbar-height] shrink-0 justify-end border-b bg-[--c-main-bars-bg] border-default"
        >
            <InteractionControlBar />
            <div className="flex flex-auto items-center justify-end overflow-hidden">
                <Dashboard />
                <section className="flex items-center py-3.5 pl-4 pr-6">
                    <div className="flex items-center gap-2">
                        <Button transparent className={iconButtonStyles}>
                            <IconTopbarNewMessage />
                        </Button>
                        <Button
                            transparent
                            className={iconButtonStyles}
                            onClick={() => openHelpModal()}
                            tooltip={t.GLOBAL.help()}
                        >
                            <IconHelp />
                        </Button>
                        <SettingsButton />
                    </div>
                    <div className="mx-4 h-4 w-px border-r border-disabled" />
                    <IconLogo />
                </section>
            </div>
        </div>
    )
}
