import React, {useMemo} from 'react'

import type {Party, Service} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {Avatar} from '@bpinc/lib-ui-elements'


interface Props {
    party?: Party
    service?: Service
}

export const CaseActivityService: React.FC<Props> = ({party, service}) => {
    const {notes, disposition, userId, firstName, lastName} = party || {}
    const {serviceName} = service || {}
    const getAvatarUrl = useGetAvatarUrl()
    const {formatFullName} = useFormatting()

    const avatarOptions = useMemo(() => ({userId, notFoundPlaceholder: true}), [userId])

    if (!notes && !disposition) return null

    return (
        <div className="-mx-2 my-2 border-t-[2px] border-white p-2">
            <div className="flex gap-2">
                <div className="mr-2.5 h-8 w-8 flex-none">
                    <Avatar
                        size={40}
                        firstName={firstName}
                        lastName={lastName}
                        url={getAvatarUrl(avatarOptions)}
                    />
                </div>
                <div>
                    <div className="text-gray-500">
                        {formatFullName(firstName, lastName)}
                    </div>
                    <div>
                        {notes}
                    </div>
                </div>
            </div>
            <div className="mt-2 text-gray-500">
                {`${serviceName}: ${disposition}`}
            </div>
        </div>
    )
}
