import type {FC} from 'react'
import React from 'react'
import {useSetAtom} from 'jotai'

import type {QueueItem} from '@bpinc/ad-request-urls'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ViewCaseTab} from './atoms'
import {selectedTabIdAtom, tabsAtom} from './atoms'


interface ViewButtonProps {
    queueItem: QueueItem
}

export const ViewButton: FC<ViewButtonProps> = ({queueItem}) => {
    const {t} = useLocalizationState()
    const setTabs = useSetAtom(tabsAtom)
    const setSelectedTabId = useSetAtom(selectedTabIdAtom)

    const handleClick = () => {
        const viewCaseTab: ViewCaseTab = {
            id: queueItem.caseId,
            case: {
                id: queueItem.caseId,
                serviceId: queueItem.service,
                number: Number(queueItem.caseNumber),
            },
            type: 'view_case',
            closeable: true,
            queueType: queueItem.queueType,
        }

        setTabs(tabs => [...tabs, viewCaseTab])
        setSelectedTabId(viewCaseTab.id)
    }
    return (
        <SemanticButton basic primary onClick={handleClick}>
            {t.EMAIL.email_view_button()}
        </SemanticButton>
    )
}
