import React from 'react'

import {ActiveCase} from '@bpinc/ad-case-ui'
import {useInteractionWorkspaceState} from '@bpinc/ad-main-page-ui-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Iframe} from '@bpinc/lib-ui-elements'

import {ActivityFormTab} from './ActivityForm/ActivityFormTab'
import {FrameTabContent} from './FrameTabContent'
import {Profile} from './Profile'


export const TabContent: React.FC = () => {
    const {
        shownWorkspaceTabId,
        shownWorkspaceTabs,
    } = useInteractionWorkspaceState()

    return (
        <>
            {shownWorkspaceTabs?.map(tab => {
                const hiddenStyles = tcn({
                    'hidden': shownWorkspaceTabId !== tab.id,
                })

                switch (tab.type) {
                    case 'profile':
                        return (
                            <Profile
                                key={tab.id}
                                contactId={tab.contactId}
                                className={hiddenStyles}
                            />
                        )
                    case 'iframe':
                        return (
                            <Iframe
                                src={tab.url}
                                key={tab.id}
                                className={tcn('h-full w-full', hiddenStyles)}
                            />
                        )
                    case 'case':
                        return <ActiveCase key={tab.id} id={tab.id} className={hiddenStyles} />
                    case 'iframe-html':
                        return <FrameTabContent key={tab.id} content={tab.content} className={hiddenStyles} />
                    case 'activityForm':
                        return <ActivityFormTab key={tab.id} className={hiddenStyles} />
                    default:
                        return null
                }
            })}
        </>
    )

}
