import type {FC} from 'react'
import React, {useCallback, useEffect, useMemo, useState} from 'react'

import {useGetUserPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import {useInteraction, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {getFirstAndLastName, getUserId} from '@bpinc/ad-interaction-state-methods'
import {useLocalSettings} from '@bpinc/ad-local-settings-context'
import {useCommunicatorFloatingStateSetter} from '@bpinc/ad-main-page-ui-context'
import {AvatarLegacy} from '@bpinc/lib-avatar-ui'
import {Chat} from '@bpinc/lib-chat-ui'
import {useBoolean} from '@bpinc/lib-helpers'
import type {OnDragEndParams, ResizePanelProps} from '@bpinc/lib-ui-elements'
import {Window} from '@bpinc/lib-ui-elements'
import {useInteractionUIState} from '@bpinc/state-interaction-ui'

import styles from './ChatWindow.module.less'


interface ChatWindowProps {
    itemId: string
}

const MIN_HEIGHT = 383

export const ChatWindow: FC<ChatWindowProps> = ({itemId}) => {
    const item = useInteraction(itemId)
    const [initialSize, setInitialSize] = useState(MIN_HEIGHT)
    const {activateItem} = useInteractionStateMethods()
    const {setItemState} = useInteractionUIState()
    const setCommunicatorFloatingState = useCommunicatorFloatingStateSetter()
    const {value: minimized, toggle: toggleMinimized} = useBoolean()
    const getUserPictureUrl = useGetUserPictureUrl()

    const localSettings = useLocalSettings()

    useEffect(
        function getInitialSize() {
            localSettings.get('adChatPopupHeight').then(height => {
                if (typeof height === 'number') {
                    setInitialSize(height)
                }
            })
        },
        [localSettings],
    )

    const onDragEnd = useCallback(
        function onDragEndCallback({size}: OnDragEndParams) {
            localSettings.set('adChatPopupHeight', size)
        }, [localSettings],
    )
    const resizeOptions: ResizePanelProps = useMemo(
        function memoizeResizeProps() {
            return {
                id: 'chat-in-window',
                direction: 'top',
                minSize: MIN_HEIGHT,
                onDragEnd,
                initialSize,
                containerClass: minimized ? styles.windowMinimized : styles.window,
                borderClass: minimized ? styles.resizerMinimized : styles.resizer,
            }
        },
        [initialSize, minimized, onDragEnd],
    )

    const onClose = useCallback(
        function onCloseCallback() {
            setItemState(itemId, s => ({...s, chatView: 'embedded'}))
        },
        [itemId, setItemState],
    )

    const onRestore = useCallback(
        function onRestoreCallback() {
            onClose()
            setCommunicatorFloatingState(s => (s === 'closed' ? 'floating' : s))
            activateItem(itemId)
        },
        [activateItem, itemId, onClose, setCommunicatorFloatingState],
    )

    const avatarIcon = useMemo(
        function memoizeAvatarIcon() {
            const userId = getUserId(item)
            const isAgent = !!userId
            const names = getFirstAndLastName(item)
            const url = getUserPictureUrl(userId)

            return (
                <AvatarLegacy
                    className={styles.avatar}
                    url={url}
                    headPhones={isAgent}
                    smallerWithHeadphones
                    type={isAgent ? 'user' : 'contact'}
                    {...names}
                />
            )
        },
        [getUserPictureUrl, item],
    )

    return (
        <Window
            resizeable={resizeOptions}
            onMinimize={toggleMinimized}
            onClose={onClose}
            onRestore={onRestore}
            minimized={minimized}
            title={item?.displayData.displayName ?? ''}
            icon={avatarIcon}
        >
            <Chat itemId={itemId} scope="chatWindow" />
        </Window>
    )
}
