import type {FC, ReactElement, ReactNode} from 'react'
import React from 'react'
import type {Table} from '@tanstack/react-table'
import type {PrimitiveAtom} from 'jotai'
import {useAtom, useAtomValue} from 'jotai'
import {flow, property} from 'lodash-es'

import type {QueueItem} from '@bpinc/ad-request-urls'
import {ResizePanel} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {Case} from '../Case'
import {queueTableWidthAtom} from './atoms'
import {QueueTable} from './QueueTable'
import {useActivateQueueWhenItemMoved} from './useActivateQueueWhenItemMoved'
import {useFirstQueueItemActivator} from './useFirstQueueItemActivator'


export interface CaseTabProps {
    table: Table<QueueItem>
    activeQueueItemAtom: PrimitiveAtom<QueueItem | undefined>
    QueueControls?: ReactElement
    noSelection?: boolean
    CaseFooterContent: ReactNode
    TableHeaderContent?: ReactNode
    queueItems: QueueItem[] | undefined
    totalQueueCount: number | undefined
    noData: boolean
}

export const CaseTab: FC<CaseTabProps> = ({
    table,
    noData,
    queueItems,
    noSelection,
    QueueControls,
    TableHeaderContent,
    CaseFooterContent,
    activeQueueItemAtom,
}) => {
    const {t} = useLocalizationState()
    const [queueTableWidth, setQueueTableWidth] = useAtom(queueTableWidthAtom)
    const activeQueueItem = useAtomValue(activeQueueItemAtom)
    useFirstQueueItemActivator(queueItems, activeQueueItemAtom)
    useActivateQueueWhenItemMoved(queueItems, activeQueueItemAtom)

    return (
        <section className="flex h-full items-start overflow-hidden">
            <ResizePanel
                className="h-full"
                minSize={300}
                id="queue-panel"
                maxSizeInPercent={70}
                initialSize={queueTableWidth}
                onDragEnd={flow(property('size'), setQueueTableWidth)}
                direction="right"
                containerClass="h-full"
            >
                <aside className="flex h-full min-w-[300px] flex-auto flex-col border-r border-gray-200">
                    {QueueControls}
                    <QueueTable
                        table={table}
                        noSelection={noSelection}
                        activeQueueItemAtom={activeQueueItemAtom}
                        HeaderContent={TableHeaderContent}
                    />
                </aside>
            </ResizePanel>

            <article className="relative flex h-full w-full flex-col items-center justify-center">
                {!activeQueueItem || noData ? (
                    <p className="text-2xl">
                        {t.GLOBAL.no_data()}
                    </p>
                ) : (
                    <Case
                        id={activeQueueItem?.caseId}
                        serviceId={activeQueueItem?.service}
                        className="w-full overflow-auto"
                        FooterContent={CaseFooterContent}
                    />
                )}
            </article>
        </section>
    )
}
