import React, {useEffect, useMemo, useState} from 'react'

import {sanitizeHtml} from '@bpinc/lib-html-utils'


interface Props {
    html: string
    title?: string
}

export const EmailContent: React.FC<Props> = ({html, title}) => {
    const [content, setContent] = useState<string>('')

    useEffect(() => {
        setContent(sanitizeHtml(html))
    }, [html])

    const innerHTML = useMemo(() => ({__html: content}), [content])

    return (
        <div>
            {title && <div className="mb-2 text-lg font-bold">{title}</div>}
            <div className="relative">
                <div dangerouslySetInnerHTML={innerHTML} />
            </div>
        </div>
    )
}
