import {useEffect} from 'react'

import {useInteractionWorkspaceState} from '@bpinc/ad-main-page-ui-context'
import {AppRoutes, useNavigationState} from '@bpinc/ad-navigation-state-context'


export function useOpenInteractionWorkspacePage() {
    const {shownWorkspaceTabs} = useInteractionWorkspaceState()
    const {getLocation, getParams, goTo} = useNavigationState()

    const hasTabs = !!shownWorkspaceTabs

    useEffect(() => {
        const pathname = getLocation()
        const locationParams = getParams()
        const isInteractionWorkspacePage = pathname === AppRoutes.interactionWorkspace

        if (!isInteractionWorkspacePage && hasTabs) {
            goTo(AppRoutes.interactionWorkspace, {returnUrl: pathname})
            return
        }

        if (isInteractionWorkspacePage && !hasTabs) {
            const url = locationParams.returnUrl || '/'
            goTo(String(url))
        }
    }, [hasTabs, goTo, getLocation, getParams])

}
