import {useMemo} from 'react'

import type {ServerAddressType, ServerEmailType, ServerPhoneType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import type {FormElement, FormElementType} from '@bpinc/lib-form-builder'
import {parseMessengers} from '@bpinc/lib-forms-base'
import {useGetMessengerName} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {MergeContactData, MergeFormRowData} from './types'


const SKIPPED_ELEMENT_TYPES: FormElementType[] = [
    'checkbox',
    'select',
    'spacer',
    'button',
    'radio',
    'label',
]


export function useMergeFormRows(formElements: FormElement[], contacts: MergeContactData[]) {
    const {t} = useLocalizationState()
    const getMessengerName = useGetMessengerName()
    const {formatEmailType, formatPhoneType, formatAddressType} = useFormatting()
    return useMemo(() => {
        const visibleRows: MergeFormRowData[] = formElements.length ? [
            {
                label: t.MERGE_FORM.master_record(),
                mapping: 'id',
                element: undefined,
            },
        ] : []

        formElements.forEach(element => {
            const {mapping, type: elementType} = element
            if (!mapping || !elementType) {
                return
            }
            if (SKIPPED_ELEMENT_TYPES.includes(elementType)) {
                return
            }

            const values = contacts.map(c => c.details?.[mapping]).filter(Boolean).flat()
            if (!values.length) {
                return
            }

            switch (elementType) {
                case 'phoneList': {
                    visibleRows.push({
                        label: t.MERGE_FORM.phone_number(),
                        element,
                    })
                    const allPhonesTypes = (values as ServerPhoneType[]).map(v => v.type)
                    const phonesTypes = Array.from(new Set(allPhonesTypes))
                    phonesTypes.forEach(type => {
                        visibleRows.push({
                            label: formatPhoneType(type),
                            mapping,
                            type,
                            element,
                        })
                    })
                    break
                }
                case 'emailList': {
                    visibleRows.push({
                        label: t.MERGE_FORM.email(),
                        element,
                    })
                    const allEmailTypes = (values as ServerEmailType[]).map(v => v.type)
                    const emailTypes = Array.from(new Set(allEmailTypes))
                    emailTypes.forEach(type => {
                        visibleRows.push({
                            label: formatEmailType(type),
                            mapping,
                            type,
                            element,
                        })
                    })
                    break
                }
                case 'addressList': {
                    visibleRows.push({
                        label: t.MERGE_FORM.address(),
                        element,
                    })
                    const allAddressTypes = (values as ServerAddressType[]).map(v => v.type)
                    const addressTypes = Array.from(new Set(allAddressTypes))
                    addressTypes.forEach(type => {
                        visibleRows.push({
                            label: formatAddressType(type),
                            mapping,
                            type,
                            element,
                        })
                    })
                    break
                }
                case 'messengers': {
                    visibleRows.push({
                        label: t.MERGE_FORM.messenger(),
                        element,
                    })
                    const allMessengersTypes = values.map(parseMessengers).flat().map(v => v?.type)
                    const messengersTypes = Array.from(new Set(allMessengersTypes))
                    messengersTypes.forEach(type => {
                        if (!type) {
                            return
                        }
                        visibleRows.push({
                            label: getMessengerName(type),
                            mapping,
                            type,
                            element,
                        })
                    })
                    break
                }
                default: {
                    visibleRows.push({
                        label: element.label || '',
                        mapping,
                        element,
                    })
                }
            }
        })

        return visibleRows
    }, [contacts, formElements, formatAddressType, formatEmailType, formatPhoneType, getMessengerName, t.MERGE_FORM])
}
