import React, {useCallback} from 'react'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useHandleUseContact, useIsUseContactButtonVisible} from '@bpinc/lib-contact-details'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'

import {useContactsActions} from '../useContactsActions'
import {useMergeContactsActions} from '../useMergeContactsActions'


type ContactActionsProps = {
    selectedContact: ContactShortType | undefined
}

export const ContactActions = ({
    selectedContact,
}: ContactActionsProps) => {
    const {t} = useLocalizationState()
    const {privileges} = useSessionState()
    const {openModal} = useModalsStateMethods()
    const {formatFullName} = useFormatting()
    const {deleteContact} = useContactsStateMethods()
    const {showNotification} = useNotificationsState('methods')
    const iseUseButtonVisible = useIsUseContactButtonVisible()
    const handleUse = useHandleUseContact()
    const {handleContactCreate, handleContactOpen} = useContactsActions()
    const {addContactForMerge} = useMergeContactsActions()

    const handleOpen = useCallback(() => {
        if (selectedContact) {
            handleContactOpen(selectedContact)
        }
    }, [selectedContact, handleContactOpen])

    const handleEdit = useCallback(() => {
        if (selectedContact) {
            handleContactOpen(selectedContact, true)
        }
    }, [handleContactOpen, selectedContact])

    const handleMerge = useCallback(() => {
        if (selectedContact && addContactForMerge) {
            addContactForMerge(selectedContact.id)
        }
    }, [addContactForMerge, selectedContact])

    const handleDelete = useCallback(() => {
        const onError = () =>
            showNotification({
                type: 'error',
                id: Notifications.ContactDeleteError,
                tags: ['current-view'],
                message: t.CONTACT.delete_error(),
            })
        const onConfirm = () => {
            if (selectedContact) {
                deleteContact(selectedContact.id)
                    .catch(onError)
            } else {
                onError()
            }
        }
        const name =
            formatFullName(
                selectedContact?.firstName,
                selectedContact?.lastName,
            ) || ''

        openModal({
            title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning(),
            body: t.UNDEFINED.contact_deleting_confirmation(name),
            actions: {
                ok: {
                    title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk(),
                    action: onConfirm,
                },
                cancel: {
                    title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel(),
                },
            },
        })
    }, [
        formatFullName,
        selectedContact,
        openModal,
        t.WINDOW_POPUP_TITLES_AND_MESSAGES,
        t.UNDEFINED,
        t.CONTACT,
        showNotification,
        deleteContact,
    ])


    return (
        <div className="flex items-center justify-center p-3.5">
            <SemanticButton onClick={handleOpen} basic primary>
                {t.UNDEFINED.button_view_contact_text()}
            </SemanticButton>
            {privileges.EDIT_CONTACTS && (
                <SemanticButton basic primary onClick={handleEdit}>
                    {t.UNDEFINED.button_edit_contact_text()}
                </SemanticButton>
            )}
            {addContactForMerge && (
                <SemanticButton basic primary onClick={handleMerge}>
                    {t.GLOBAL.merge()}
                </SemanticButton>
            )}
            {iseUseButtonVisible && (
                <SemanticButton
                    basic
                    primary
                    onClick={() => {
                        assertDefined(selectedContact)
                        handleUse(selectedContact)
                    }}
                >
                    {t.UNDEFINED.button_use_contact_text()}
                </SemanticButton>
            )}
            <SemanticButton basic primary onClick={() => handleContactCreate()}>
                {t.UNDEFINED.button_create_contact_text()}
            </SemanticButton>
            {privileges.DELETE_CONTACTS && (
                <SemanticButton basic color="red" onClick={handleDelete}>
                    {t.UNDEFINED.button_delete_contact_text()}
                </SemanticButton>
            )}
        </div>
    )
}
