import {useCallback} from 'react'
import type {Table} from '@tanstack/react-table'
import produce from 'immer'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {QueueItem} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'


export function useDeleteCaseHandler(
    queueItems: QueueItem[],
    table?: Table<QueueItem>,
) {
    const {t} = useLocalizationState()
    const privileges = useSessionState('privileges')
    const {deleteCases} = useCaseStateMethods()

    const {openModal} = useModalsStateMethods()

    return useCallback(
        function handleDeleteCallback() {
            const selectedItemsCount = queueItems.length

            if (!privileges.SYS_DELETE_CASES || !selectedItemsCount) {
                return
            }

            openModal({
                title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning(),
                body:
                    selectedItemsCount > 1
                        ? t.EMAIL.trash_confirm_message_multiple(
                            selectedItemsCount.toString(),
                        )
                        : t.EMAIL.trash_confirm_message_single(
                            queueItems[0].caseNumber ?? '',
                        ),
                actions: {
                    ok: {
                        title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk(),
                        action: handleOk,
                    },
                    cancel: {
                        title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel(),
                    },
                },
                className: 'autoSizeModal',
            })

            function handleOk() {
                deleteCases(
                    queueItems.map(v => v.caseId),
                    'TEAM',
                )
                table?.resetRowSelection()

                unselectItems()

                function unselectItems() {
                    table?.setRowSelection(
                        produce(draft => {
                            queueItems.forEach(q => {
                                delete draft[q.caseId]
                            })
                        }),
                    )
                }
            }
        },
        [
            deleteCases,
            openModal,
            privileges.SYS_DELETE_CASES,
            queueItems,
            t.EMAIL,
            t.WINDOW_POPUP_TITLES_AND_MESSAGES,
            table,
        ],
    )
}
