import type {ReactNode, RefObject} from 'react'
import type React from 'react'
import ReactDOM from 'react-dom'


interface ProfileActionsProps {
    profileActionsRef: RefObject<HTMLDivElement>
    children: ReactNode
}

export const ProfileActions: React.FC<ProfileActionsProps> = ({profileActionsRef, children}) => {
    return profileActionsRef.current && ReactDOM.createPortal(children, profileActionsRef.current)
}
