import {useEffect} from 'react'

import {useCaseStateConnector, useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useOpenInfoModal} from '@bpinc/lib-modals-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {ExitSupervisingWarningModal} from './ExitSupervisingWarningModal'


export function useCaseStateSubscription() {
    const caseStateConnector = useCaseStateConnector()
    const {openCase, replyEmail} = useCaseStateMethods()
    const {t} = useLocalizationState()
    const openInfoModal = useOpenInfoModal()
    const openExitSupervisingWarningModal = useModalComponentOpener(
        ExitSupervisingWarningModal,
    )

    useEffect(() => {
        const subscription = caseStateConnector?.dispenser.subscribe({
            onQueueMovementFailed: (failedItemsCount: number) => {
                openInfoModal({
                    body: t.EMAIL.itemsWithoutServiceStillInPersonalQueue(
                        failedItemsCount.toString(),
                    ),
                })
            },
            onCaseOpenAttemptedWhileSupervising: params => {
                openExitSupervisingWarningModal({
                    actionCallback: () =>
                        openCase({
                            ...params,
                            forceOpenWhileSupervising: true,
                        }),
                })
            },
            onReplyEmailAttemptedWhileSupervising: params => {
                openExitSupervisingWarningModal({
                    actionCallback: () =>
                        replyEmail({
                            ...params,
                            forceOpenWhileSupervising: true,
                        }),
                })
            },
        })
        return () => {
            subscription?.unsubscribe()
        }
    }, [
        caseStateConnector?.dispenser,
        openCase,
        openExitSupervisingWarningModal,
        openInfoModal,
        replyEmail,
        t.EMAIL,
        t.GLOBAL,
        t.WINDOW_POPUP_TITLES_AND_MESSAGES,
    ])
}
