import React, {type ReactElement, useCallback, useEffect, useState} from 'react'
import {produce} from 'immer'
import {noop, uniq} from 'lodash-es'
import {createContext, useContextSelector} from 'use-context-selector'

import type {CustomFieldFilters, FiltersOrder, FilterType, QuerySettings} from '@bpinc/ad-evaluation-state-types'
import {
    type Filters,
    CUSTOM_FIELD_ORDER_PREFIX,
    isKeyArrayNonValueFilter,
    isKeyArrayValueFilter,
    isKeyBooleanFilter,
    isKeyTimeRangeValue,
} from '@bpinc/ad-evaluation-state-types'
import {useBoolean} from '@bpinc/lib-helpers'


type UpdateValueType = <K extends keyof Filters, >(
    key: K,
    newValue: Required<Filters>[K]['value'],
) => void

type FiltersContext = {
    filters: Filters
    order: FiltersOrder
    searchName: string
    customFilters: CustomFieldFilters
    updateFilterValue: UpdateValueType
    addFilter: (type: FilterType, filterKey: keyof Filters) => void
    removeFilter: (filterKey: keyof Filters) => void
    saveSearch: () => void
    makeSearch: () => void
    addCustomFilter: (type: FilterType, key: string, name?: string) => void
    removeCustomFilter: (key: string) => void
    updateCustomFilterValue: (key: string, value: string[]) => void
    setSearchName: (value: string) => void
    changeRandomize: () => void
    isRandomize: boolean
    searchResultsMode: boolean
    onClosePanel?: () => void
    onCloneFilters?: () => void
    resetFilters: () => void
}

export const SearchFiltersContext = createContext<FiltersContext>({
    filters: {},
    customFilters: {},
    searchName: '',
    updateFilterValue: noop,
    addFilter: noop,
    removeFilter: noop,
    saveSearch: noop,
    addCustomFilter: noop,
    removeCustomFilter: noop,
    updateCustomFilterValue: noop,
    setSearchName: noop,
    changeRandomize: noop,
    isRandomize: false,
    order: {
        and: [],
        or: [],
    },
    searchResultsMode: false,
    onClosePanel: noop,
    onCloneFilters: noop,
    makeSearch: noop,
    resetFilters: noop,
})

SearchFiltersContext.displayName = 'SearchFiltersContext'

type FiltersProviderProps = {
    children: ReactElement
    query: QuerySettings
    onSave: (newQuery: QuerySettings) => void
    searchResultsMode?: boolean
    onClosePanel?: () => void
    onCloneFilters?: () => void
    onSearchByQuery?: (newQuery: QuerySettings) => void
}

export const SearchContextProvider: React.FC<FiltersProviderProps> = ({
    children, onSave, query, searchResultsMode = false, onClosePanel, onCloneFilters, onSearchByQuery,
}) => {
    const {value: isRandomize, toggle: toggleRandomize} = useBoolean(query.randomizeSearch || false)
    const [stateFilters, updateFilters] = useState(() => query.filters)
    const [searchName, setSearchName] = useState(() => query.name)
    const [stateOrder, updateOrder] = useState(() => query.order)
    const [customFilters, updateCustomFilters] = useState<CustomFieldFilters>(() => query.customFilters)

    useEffect(() => {
        updateOrder(query.order)
        updateFilters(query.filters)
        setSearchName(query.name)
        updateCustomFilters(query.customFilters)
    }, [query])

    useEffect(() => {
        toggleRandomize(query.randomizeSearch || false)
    }, [query.randomizeSearch, toggleRandomize])

    const updateFilterValue = useCallback<UpdateValueType>((key, newValue) => {
        updateFilters(produce(draft => {
            const existed = draft[key]
            if (existed) {
                existed.value = newValue
                if (!newValue && 'emptyValue' in existed) {
                    existed.emptyValue = true
                }
                if (newValue && 'emptyValue' in existed) {
                    existed.emptyValue = false
                }
            }
            return draft
        }))
    }, [updateFilters])

    const addCustomFilter = useCallback((type: FilterType, key: string, name?: string) => {
        updateCustomFilters(produce(draft => {
            draft[key] = {
                filterType: type,
                value: [],
                name,
            }
            return draft
        }))

        if (type === 'AND') {
            updateOrder(produce(draft => {
                draft.and.push(`${CUSTOM_FIELD_ORDER_PREFIX}${key}`)
                draft.and = uniq(draft.and)
                return draft
            }))
            return
        }
        updateOrder(produce(draft => {
            draft.or.push(key)
            draft.or = uniq(draft.or)
            return draft
        }))

    }, [updateCustomFilters])

    const removeCustomFilter = useCallback((key: string) => {
        updateCustomFilters(produce(draft => {
            if (key in draft) {
                delete draft[key]
            }
            return draft
        }))
        updateOrder(produce(draft => {
            draft.and = draft.and.filter(existingKey => existingKey !== `${CUSTOM_FIELD_ORDER_PREFIX}${key}`)
            draft.or = draft.or.filter(existingKey => existingKey !== `${CUSTOM_FIELD_ORDER_PREFIX}${key}`)
            return draft
        }))

    }, [updateCustomFilters, updateOrder])

    const updateCustomFilterValue = useCallback((key: string, value: string[]) => {
        updateCustomFilters(produce(draft => {
            if (key in draft) {
                draft[key].value = value
            }
            return draft
        }))
    }, [updateCustomFilters])

    const addFilter = useCallback((type: FilterType, key: keyof Filters) => {
        updateFilters(produce(draft => {
            if (key === 'kmTemplate') {
                draft[key] = {
                    filterType: type,
                    value: {
                        kmTemplateUsed: true,
                        values: [],
                    },
                }
                return draft
            }
            if (isKeyArrayValueFilter(key)) {
                draft[key] = {
                    filterType: type,
                    value: [],
                }
            }

            if (isKeyBooleanFilter(key)) {
                draft[key] = {
                    filterType: type,
                    value: true,
                }
                return draft
            }

            if (isKeyTimeRangeValue(key)) {
                draft[key] = {
                    filterType: type,
                    value: {type: 'Last24h'},
                }
                return draft
            }

            if (key === 'sentiment') {
                draft[key] = {
                    filterType: type,
                    value: {},
                }
                return draft
            }

            if (isKeyArrayNonValueFilter(key)) {
                draft[key] = {
                    filterType: type,
                    emptyValue: true,
                }
            }
            return draft
        }))

        if (type === 'AND') {
            updateOrder(produce(draft => {
                draft.and.push(key)
                draft.and = uniq(draft.and)
                return draft
            }))
            return
        }
        updateOrder(produce(draft => {
            draft.or.push(key)
            draft.or = uniq(draft.or)
            return draft
        }))
    }, [updateFilters, updateOrder])

    const removeFilter = useCallback((key: keyof Filters) => {
        updateFilters(produce(draft => {
            delete draft[key]
            return draft
        }))

        updateOrder(produce(draft => {
            draft.and = draft.and.filter(existingKey => existingKey !== key)
            draft.or = draft.or.filter(existingKey => existingKey !== key)
            return draft
        }))
    }, [updateFilters, updateOrder])

    const saveSearch = useCallback(() => {
        onSave({filters: stateFilters, name: searchName, order: stateOrder, customFilters, randomizeSearch: isRandomize})
    }, [stateFilters, searchName, onSave, stateOrder, customFilters, isRandomize])

    const makeSearch = useCallback(() => {
        onSearchByQuery?.({filters: stateFilters, name: searchName, order: stateOrder, customFilters, randomizeSearch: isRandomize})
    }, [stateFilters, searchName, onSearchByQuery, stateOrder, customFilters, isRandomize])

    const resetFilters = useCallback(() => {
        updateFilters(query.filters)
        updateCustomFilters(query.customFilters)
    }, [query.filters, query.customFilters])

    const value = {
        filters: stateFilters,
        customFilters,
        searchName,
        order: stateOrder,
        updateFilterValue,
        addFilter,
        removeFilter,
        saveSearch,
        addCustomFilter,
        removeCustomFilter,
        updateCustomFilterValue,
        setSearchName: setSearchName,
        isRandomize: isRandomize,
        changeRandomize: toggleRandomize,
        searchResultsMode,
        onClosePanel,
        onCloneFilters,
        makeSearch,
        resetFilters,
    }

    return (
        <SearchFiltersContext.Provider value={value} >
            {children}
        </SearchFiltersContext.Provider >
    )
}

export const useGetFilters = () => useContextSelector(SearchFiltersContext, value => value.filters)
export const useGetCustomFilters = () => useContextSelector(SearchFiltersContext, value => value.customFilters)
export const useGetName = () => useContextSelector(SearchFiltersContext, value => value.searchName)
export const useUpdateName = () => useContextSelector(SearchFiltersContext, value => value.setSearchName)
export const useGetOrder = () => useContextSelector(SearchFiltersContext, value => value.order)
export const useGetFilter = <K extends keyof Filters>(key: K) => useContextSelector(SearchFiltersContext, value => value.filters[key])
export const useGetCustomFilter = (key: string) => useContextSelector(SearchFiltersContext, value => value.customFilters[key])
export const useUpdateFilters = () => useContextSelector(SearchFiltersContext, value => value.updateFilterValue)
export const useAddFilter = () => useContextSelector(SearchFiltersContext, value => value.addFilter)
export const useRemoveFilter = () => useContextSelector(SearchFiltersContext, value => value.removeFilter)
export const useSaveSearch = () => useContextSelector(SearchFiltersContext, value => value.saveSearch)
export const useRandomize = () => useContextSelector(SearchFiltersContext, value => value.isRandomize)
export const useChangeRandomize = () => useContextSelector(SearchFiltersContext, value => value.changeRandomize)
export const useAddCustomFilter = () => useContextSelector(SearchFiltersContext, value => value.addCustomFilter)
export const useRemoveCustomFilter = () => useContextSelector(SearchFiltersContext, value => value.removeCustomFilter)
export const useUpdateCustomFilterValue = () => useContextSelector(SearchFiltersContext, value => value.updateCustomFilterValue)
export const useGetSearchResultMode = () => useContextSelector(SearchFiltersContext, value => value.searchResultsMode)
export const useClosePanel = () => useContextSelector(SearchFiltersContext, value => value.onClosePanel)
export const useCloneFilters = () => useContextSelector(SearchFiltersContext, value => value.onCloneFilters)
export const useMakeSearch = () => useContextSelector(SearchFiltersContext, value => value.makeSearch)
export const useResetFilters = () => useContextSelector(SearchFiltersContext, value => value.resetFilters)
