import type {ReactNode} from 'react'
import React from 'react'
import type {NavLinkProps} from 'react-router-dom'
import {NavLink, useMatch} from 'react-router-dom'

import {ActiveStatusControl} from '@bpinc/lib-agent-state-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {UnreadBadge} from '@bpinc/lib-ui-elements'

import type {AppRoutesType} from '../../constants/routes'
import {routesConfig} from '../../constants/routes'
import {CommunicatorButton} from './components/CommunicatorButton'
import {useInteractionChatsUnread} from './hooks/useInteractionChatsUnread'
import {useMainMenuItems} from './hooks/useMainMenuItems'


export const leftBarStyles = {
    base: tcn(
        'relative flex h-full w-[76px] shrink-0 flex-col items-center overflow-hidden px-1',
        'border-r border-r-neutral-100 bg-[--c-main-bars-bg]',
    ),
    button: {
        base: tcn(
            'relative mb-px flex flex-col items-center justify-center rounded',
            'text-primary-850 caption-xs',
            'transition-colors',
            'group',
        ),
        icon: (state?: 'active' | 'disabled') => tcn(
            'flex size-[44px] shrink-0 items-center justify-center',
            'rounded',
            'transition-colors state-layer-bg-neutral-0',
            '[&_path]:transition-colors',
            'group-hover:state-layer-color-black group-hover:state-layer-opacity-4',
            {'state-layer-bg-primary-600 svg-color-neutral-0': state === 'active'},
            {'svg-color-neutral-400': state === 'disabled'},
        ),
    },
    unreadBadge: tcn('absolute bottom-5 right-2 leading-[16px]'),
}


export const LeftBar: React.FC = () => {
    const interactionsUnread = useInteractionChatsUnread()
    const menuItems = useMainMenuItems()


    return (
        <div
            className={leftBarStyles.base}
            data-testid="leftbar"
        >
            <ActiveStatusControl />
            <CommunicatorButton>
                <UnreadBadge
                    unreadAmount={interactionsUnread}
                    className={tcn(
                        leftBarStyles.unreadBadge,
                        'bottom-0.5',
                    )}
                />
            </CommunicatorButton>
            <div
                className="flex shrink grow flex-col justify-between border-t border-t-neutral-200 py-1"
            >
                <div className="relative flex h-full flex-col gap-4">
                    {menuItems.map(
                        ({
                            icon,
                            title,
                            route,
                            disabled,
                            testId,
                            badge,
                        }) => (
                            <RouterNavLink
                                key={route}
                                route={route}
                                title={title}
                                icon={icon}
                                data-testid={testId}
                                disabled={disabled}
                                badge={badge}
                            />
                        ),
                    )}
                </div>
            </div>
        </div>
    )
}

type RouterNavLinkProps = {
    route: AppRoutesType
    disabled?: boolean
    icon: ReactNode
    badge?: ReactNode
    title: string
} & Omit<NavLinkProps, 'to' | 'children' | 'title'>

const RouterNavLink: React.FC<RouterNavLinkProps> = ({
    route,
    disabled,
    icon,
    badge,
    title,
    ...restProps
}) => {
    const {path} = routesConfig[route]
    const isActive = !!useMatch(route)
    const state = disabled ? 'disabled' : isActive ? 'active' : undefined

    const content = (
        <>
            <div className={leftBarStyles.button.icon(state)}>
                {icon}
            </div>
            <div className="flex max-w-full">
                <span className="truncate">{title}</span>
            </div>
            {badge}
        </>
    )

    if (disabled) {
        return (
            <div
                className={tcn(
                    leftBarStyles.button.base,
                    'pointer-events-none text-neutral-disabled',
                )}
                title={title}
            >
                {content}
            </div>
        )
    }

    return (
        <NavLink
            to={path}
            title={title}
            aria-label={title}
            {...restProps}
            className={leftBarStyles.button.base}
        >
            {content}
        </NavLink>
    )
}
