import {useQuery} from '@tanstack/react-query'

import type {DefaultService} from '@bpinc/ad-request-urls'
import {apiRequests} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'

import {queryKeys} from './query-keys'


type QueueCountQueryParams = Parameters<typeof DefaultService.getQueueCountByService>[0]

export function useQueueCountByServiceQuery(params: QueueCountQueryParams = {}) {
    const serverOrigin = useSessionState('serverOrigin')

    return useQuery({
        queryKey: queryKeys.queueCountByService(),
        queryFn: () => apiRequests(serverOrigin).getQueueCountByService(params),
    })
}
