import type {FC, RefObject} from 'react'
import React, {useCallback, useEffect, useLayoutEffect, useState} from 'react'
import {usePopper} from 'react-popper'
import {partial} from 'lodash-es'

import type {BooleanManager} from '@bpinc/lib-helpers'
import {isHTMLInputElement} from '@bpinc/lib-html-utils'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Button, ClickOutsideWrapper} from '@bpinc/lib-ui-elements'

import type {EmailSearchItem} from './types'


interface SuggestionsPopupProps {
    suggestedItems: EmailSearchItem[]
    onSelect: (item: EmailSearchItem) => void
    selectionInputRootRef: RefObject<HTMLElement>
    showEmailSuggestions: BooleanManager
}

export const SuggestionsPopup: FC<SuggestionsPopupProps> = ({
    suggestedItems,
    showEmailSuggestions,
    selectionInputRootRef,
    onSelect,
}) => {
    const [popperElement, setPopperElement] = useState<HTMLDivElement | null>(null)
    const [referenceElement, setReferenceElement] =
    useState<HTMLInputElement | undefined>(undefined)

    const {styles: popperStyles, attributes, update} = usePopper(referenceElement, popperElement, {
        placement: 'bottom-start',
    })

    const handleSelect = useCallback((item: EmailSearchItem) => {
        showEmailSuggestions.setFalse()
        onSelect(item)
    }, [onSelect, showEmailSuggestions])

    const [selectedOption, setSelectedOption] = useState<EmailSearchItem>(
        suggestedItems[0],
    )

    useLayoutEffect(() => {
        setReferenceElement(getReferenceInputElement(selectionInputRootRef.current))
    }, [selectionInputRootRef])

    useLayoutEffect(() => {
        if (showEmailSuggestions.value) {
            update?.()
        }
    }, [showEmailSuggestions.value, update])

    useEffect(function handleKeyboardNavigation() {
        const handleKeyDown = (evt: KeyboardEvent) => {
            if (evt.key === 'ArrowDown') {
                evt.stopPropagation()
                const currentIndex = suggestedItems.findIndex(
                    i => i.id === selectedOption?.id,
                )
                const nextIndex = (currentIndex + 1) % suggestedItems.length
                setSelectedOption(suggestedItems[nextIndex])
            } else if (evt.key === 'ArrowUp') {
                evt.stopPropagation()
                const currentIndex = suggestedItems.findIndex(
                    i => i.id === selectedOption?.id,
                )
                const nextIndex = currentIndex === 0 ? suggestedItems.length - 1 : currentIndex - 1
                setSelectedOption(suggestedItems[nextIndex])
            } else if (evt.key === 'Enter') {
                evt.stopPropagation()
                handleSelect(selectedOption)
            }
        }
        const rootElement = selectionInputRootRef.current

        if (selectedOption) {
            rootElement?.addEventListener('keydown', handleKeyDown)
        }

        return () => {
            rootElement?.removeEventListener('keydown', handleKeyDown)
        }

    }, [handleSelect, selectionInputRootRef, selectedOption, showEmailSuggestions, suggestedItems])
    return (
        <ClickOutsideWrapper
            show={showEmailSuggestions.value}
            onClickOutside={showEmailSuggestions.setFalse}
        >
            <div
                ref={setPopperElement}
                className="bottom-auto right-auto z-[10000] transform-none rounded border bg-primary-contrast p-2 shadow"
                style={popperStyles.popper}
                {...attributes.popper}
            >
                {suggestedItems.map((i, index) => (
                    <Button
                        key={index}
                        data-element="email-suggestion"

                        className={tcn('block w-full p-2', {
                            'bg-blue-50': selectedOption?.id === i.id,
                        })}
                        onClick={partial(handleSelect, i)}
                    >
                        {i.displayText}
                    </Button>
                ))}
            </div>
        </ClickOutsideWrapper>
    )
}

/**
 * @param selectionInputRoot root element of the selection input
 * @returns the currently focused input element within the selection input.
 */
function getReferenceInputElement(selectionInputRoot: HTMLElement | null) {
    const activeElement = document.activeElement

    const activeElementIsInput =
        selectionInputRoot?.contains(activeElement) &&
        isHTMLInputElement(activeElement)

    if (!activeElement || !activeElementIsInput) {
        return
    }

    return activeElement
}
