import React, {useCallback} from 'react'

import {useInteractionWorkspaceState} from '@bpinc/ad-main-page-ui-context'
import type {ScreenPopTab} from '@bpinc/ad-screen-pop-state-types'
import {Tabs} from '@bpinc/lib-ui-elements'

import {useAutoRedirectToActivityFormTab} from '../hooks/useAutoRedirectToActivityFormTab'
import {TabContent} from './TabContent'
import {TabTitleContent} from './TabTitleContent'


const DEFAULT_PROFILE_PAGE_TABS: ScreenPopTab[] = []

export const InteractionWorkspacePage: React.FC = () => {
    const {
        shownWorkspaceTabs,
        shownWorkspaceTabId,
        activateItemTab,
    } = useInteractionWorkspaceState()

    const tabs = shownWorkspaceTabs || DEFAULT_PROFILE_PAGE_TABS

    useAutoRedirectToActivityFormTab()

    const changeActiveTab = useCallback((tabId: string) => {
        activateItemTab(tabId)
    }, [activateItemTab])

    if (!tabs.length || !shownWorkspaceTabId) {
        return null
    }

    return (
        <Tabs
            activeTabId={shownWorkspaceTabId}
            renderTabContent={TabContent}
            renderTab={tab => <TabTitleContent tab={tab} /> }
            tabs={tabs}
            onChange={changeActiveTab}
            className="flex h-full w-full flex-1 flex-col"
        />
    )
}
