import {useLayoutEffect} from 'react'
import type {PrimitiveAtom} from 'jotai'
import {useAtom} from 'jotai'

import type {QueueItem} from '@bpinc/ad-request-urls'


export function useFirstQueueItemActivator(queueItems: QueueItem[] | undefined, activeQueueItemAtom: PrimitiveAtom<QueueItem | undefined>) {
    const [activeQueueItem, setActiveQueueItem] = useAtom(activeQueueItemAtom)

    useLayoutEffect(() => {
        if (!queueItems?.some(q => q.caseId === activeQueueItem?.caseId)) {
            setActiveQueueItem(queueItems?.[0])
        }
    }, [activeQueueItem, queueItems, setActiveQueueItem])
}
