import {useCallback} from 'react'
import type {Table} from '@tanstack/react-table'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {CasesListItem} from '@bpinc/ad-case-state-types'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'


export function useCloseAsSpamHandler(table: Table<CasesListItem>) {
    const {openModal} = useModalsStateMethods()
    const {closeCasesAsSpam} = useCaseStateMethods()
    const {t} = useLocalizationState()

    return useCallback(
        function handleCloseAsSpamCallback() {
            const selectedRows = table.getSelectedRowModel().flatRows
            const selectedItemsCount = selectedRows.length

            if (!selectedItemsCount) {
                return
            }

            openModal({
                title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning(),
                body:
                selectedItemsCount > 1
                    ? t.EMAIL.spam_confirm_message_multiple(selectedItemsCount.toString())
                    : t.EMAIL.spam_confirm_message_single(selectedRows[0].original.title ?? ''),
                actions: {
                    ok: {
                        title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk(),
                        action: handleOk,
                    },
                    cancel: {
                        title: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel(),
                    },
                },
                className: 'autoSizeModal',
            })

            function handleOk() {
                closeCasesAsSpam(selectedRows.map(s => s.original.id))
                table.resetRowSelection()
            }
        },
        [closeCasesAsSpam, openModal, t.EMAIL, t.WINDOW_POPUP_TITLES_AND_MESSAGES, table],
    )
}
