import React from 'react'

import {useOpenInteractionWorkspacePage} from '@bpinc/ad-interaction-workspace-ui'
import {useAppDefaultSettings} from '@bpinc/ad-local-settings-context'
import {useMainPageUIState} from '@bpinc/ad-main-page-ui-context'
import {usePageTitle} from '@bpinc/ad-page-title'
import {useSupervisionUIContext} from '@bpinc/ad-supervision-ui-context'
import {useIncomingChatMessageAudioNotifications} from '@bpinc/lib-helpers-ui'
import {useInteractionModalsAutoOpener} from '@bpinc/lib-interaction-ui-hooks'
import {useInteractionNotifications} from '@bpinc/lib-system-notifications'
import {createComponentLoader} from '@bpinc/lib-ui-elements'
import {useInteractionPopupItemIdSelector} from '@bpinc/state-interaction-ui'

import {defaultModalsConfig} from '../constants'
import {useStateSubscriptions} from '../hooks/state-subscription/useStateSubscriptions'
import {useModulesImportByPrivileges} from '../hooks/useModulesImportByPrivileges'
import {CommunicationPanel} from './CommunicationPanel/CommunicationPanel'
import {ChatWindow} from './ChatWindow'
import {PrivilegeRouter} from './PrivilegeRouter'
import {RightBar} from './RightBar'

import styles from '../styles/MainPage.module.less'


const {
    CalendarModalsAutoOpenerLoader,
} = createComponentLoader('calendar', () => import('calendar'), 'CalendarModalsAutoOpener')

export const MainPage: React.FC = function MainPage() {
    const setMainContentNode = useMainPageUIState('setMainContentNode')
    const setQmMainContentRef = useSupervisionUIContext('setMainContentNode')

    const communicationPanelIsVisible = useMainPageUIState(
        s => s.communicator.floatingState !== 'closed',
    )

    usePageTitle()
    useInteractionNotifications()
    useAppDefaultSettings({modals: defaultModalsConfig})
    useStateSubscriptions()
    useIncomingChatMessageAudioNotifications(communicationPanelIsVisible)
    useModulesImportByPrivileges()
    useOpenInteractionWorkspacePage()

    useInteractionModalsAutoOpener()

    const chatWindowId = useInteractionPopupItemIdSelector()
    return (
        <>
            <CommunicationPanel />
            <div
                className={styles.content}
                ref={(el: HTMLDivElement) => {
                    setMainContentNode(el)
                    setQmMainContentRef(el)
                }}
            >
                <PrivilegeRouter />

                {/** Used for {@link @bpinc-ad-modals-ui/IncomingMessageModal.tsx} for createPortal */}
                <div
                    className={styles.incomingMessagesModalsStack}
                    data-incoming-messages-modals-parent-node="true"
                />
            </div>
            <RightBar />
            {chatWindowId && <ChatWindow itemId={chatWindowId} />}
            <CalendarModalsAutoOpenerLoader />
        </>
    )
}
