import React from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import type {ContactType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {AvatarLegacy} from '@bpinc/lib-avatar-ui'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useMergeContactsActions} from '../useMergeContactsActions'


type MergeContactInfoProps = {
    contact: ContactType | undefined
}

export const MergeContactInfo: React.FC<MergeContactInfoProps> = ({contact}) => {
    const {t} = useLocalizationState()
    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()
    const {removeContactFromMerge} = useMergeContactsActions()

    const url = getAvatarUrl({contactId: contact?.Id.oid})

    return (
        <div className="flex gap-2">
            <AvatarLegacy
                size={40}
                url={url}
                firstName={contact?.firstName}
                lastName={contact?.lastName}
                type="contact"
            />
            <div>
                <p className="font-bold">{formatFullName(contact?.firstName, contact?.lastName)}</p>
                {/*TODO: update after backend implementation*/}
                <p className="text-main-secondary">{t.MERGE_FORM.created('--/--/----')}</p>
            </div>
            <SemanticButton
                transparent
                icon="close"
                onClick={() => removeContactFromMerge(contact?.Id.oid || '')}
            />
        </div>
    )
}
