import type {Dispatch} from 'react'
import {useEffect} from 'react'

import {useCalendarAlertState} from '@bpinc/cal-alert-state-context'
import {usePrevious} from '@bpinc/lib-helpers'

import type {Action} from '../reducer'


export function useCalendarEventsDispatchers(dispatch: Dispatch<Action>) {
    const calendarEventAlerts = useCalendarAlertState('calendarEventAlerts')

    const prevCalendarItems = usePrevious(calendarEventAlerts)
    const prevCalendarItemsLength = prevCalendarItems?.length ?? 0

    useEffect(() => {
        if (calendarEventAlerts.length > prevCalendarItemsLength) {
            const lastCalendarAlert = calendarEventAlerts[calendarEventAlerts.length - 1]
            dispatch({type: 'calendarEventAppeared', id: lastCalendarAlert.id})
        } else if (calendarEventAlerts.length < prevCalendarItemsLength) {
            const lastCalendarAlert = prevCalendarItems?.[prevCalendarItems.length - 1]
            lastCalendarAlert && dispatch({type: 'calendarEventDisappeared', id: lastCalendarAlert.id})
        }
    }, [calendarEventAlerts, calendarEventAlerts.length, dispatch, prevCalendarItems, prevCalendarItemsLength])
}
