import React, {useCallback, useMemo} from 'react'
import {useAtom, useAtomValue} from 'jotai'

import {Tabs} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useQueueCountInvalidator, useQueueCountQuery} from '../../queries'
import {useQueryManager} from '../../useQueryManager'
import {Case} from '../Case'
import {PersonalCaseFooterContent} from './PersonalTab/PersonalCaseFooterContent'
import {PersonalTab} from './PersonalTab/PersonalTab'
import {usePersonalTabName} from './PersonalTab/usePersonalTabName'
import {PinnedCaseFooterContent} from './PinnedTab/PinnedCaseFooterContent'
import {PinnedTab} from './PinnedTab/PinnedTab'
import {TeamCaseFooterContent} from './TeamTab/TeamCaseFooterContent'
import {TeamTab} from './TeamTab/TeamTab'
import {useTeamTabName} from './TeamTab/useTeamTabName'
import type {CaseTab} from './atoms'
import {
    personalTabUserAtom,
    selectedTabIdAtom,
    serviceFilterAtom,
    tabsAtom,
    teamIdAtom,
} from './atoms'


type CaseTabWithTitle = CaseTab & { title: string }

export const CasePageContent: React.FC = () => {
    const {t} = useLocalizationState()
    const [tabId, setTabId] = useAtom(selectedTabIdAtom)
    const [tabs, setTabs] = useAtom(tabsAtom)
    const user = useAtomValue(personalTabUserAtom)
    const teamId = useAtomValue(teamIdAtom)
    const serviceFilter = useAtomValue(serviceFilterAtom)

    const {data: queueCount} = useQueueCountQuery({
        team: teamId,
        service: serviceFilter,
        user: user.id,
    })

    useQueueCountInvalidator()

    const teamTabName = useTeamTabName()
    const personalTabName = usePersonalTabName()

    const tabsWithTitle = useMemo(
        function memoizeTabs() {
            function insertCount(
                label: string,
                count = 0,
                highlightedCount = 0,
            ) {
                if (highlightedCount) {
                    return `${label} (${highlightedCount}/${count})`
                }

                return `${label} (${count})`
            }

            function getTitle(tab: CaseTab) {
                switch (tab.type) {
                    case 'queue': {
                        switch (tab.queueType) {
                            case 'TEAM':
                                return insertCount(
                                    teamTabName,
                                    queueCount?.team,
                                )
                            case 'PERSONAL':
                                return insertCount(
                                    personalTabName,
                                    queueCount?.personal,
                                    queueCount?.personalHighlighted,
                                )
                            default:
                                return insertCount(
                                    t.EMAIL.emailFollowUpQueue_header(),
                                    queueCount?.followUp,
                                )
                        }
                    }
                    case 'view_case': {
                        return tab.case.number.toString()
                    }
                }
            }

            return tabs.map(tab => ({
                ...tab,
                title: getTitle(tab),
            }))
        },
        [
            personalTabName,
            queueCount?.followUp,
            queueCount?.personal,
            queueCount?.personalHighlighted,
            queueCount?.team,
            t.EMAIL,
            tabs,
            teamTabName,
        ],
    )

    const renderTab = useCallback(function renderTabCallback({
        title,
    }: CaseTabWithTitle) {
        return title
    },
    [])

    const renderTabContent = useCallback(
        function renderTabContentCallback(tab: CaseTab) {
            if (tab.type === 'queue') {
                switch (tab.queueType) {
                    case 'TEAM':
                        return <TeamTab totalQueueCount={queueCount?.team} />
                    case 'PERSONAL':
                        return (
                            <PersonalTab
                                totalQueueCount={queueCount?.personal}
                            />
                        )
                    default:
                        return (
                            <PinnedTab totalQueueCount={queueCount?.followUp} />
                        )
                }
            }

            if (tab.type === 'view_case') {
                const FooterContent =
                    tab.queueType === 'PERSONAL' ? (
                        <PersonalCaseFooterContent />
                    ) : tab.queueType === 'TEAM' ? (
                        <TeamCaseFooterContent />
                    ) : (
                        <PinnedCaseFooterContent />
                    )

                return (
                    <Case
                        id={tab.case.id}
                        serviceId={tab.case.serviceId}
                        FooterContent={FooterContent}
                    />
                )
            }
        },
        [queueCount?.followUp, queueCount?.personal, queueCount?.team],
    )

    const [selectedTabId, setSelectedTabId] = useAtom(selectedTabIdAtom)
    const onTabClose = useCallback(
        function onTabCloseCallback(id: string) {
            setTabs(t => t.filter(tab => tab.id !== id))

            if (selectedTabId === id) {
                setSelectedTabId(tabs[0].id)
            }

        },
        [selectedTabId, setSelectedTabId, setTabs, tabs],
    )
    useQueryManager()

    return (
        <div className="flex h-full flex-col" data-testid="cases-page">
            <Tabs
                onChange={setTabId}
                className="flex h-full min-h-0 flex-col"
                tabsContentClassName="flex flex-col flex-auto h-auto"
                tabs={tabsWithTitle}
                activeTabId={tabId}
                renderTab={renderTab}
                onTabClose={onTabClose}
                renderTabContent={renderTabContent}
            />
        </div>
    )
}
