import React from 'react'
import {useAtom} from 'jotai'

import {Checkbox, PopupButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {casesStatusesAtom} from '../atoms'
import {ALL_CASE_STATUSES} from './constants'
import {useCaseStatusOptions} from './useCaseStatusOptions'


export const CasesStatusesSelect: React.FC = () => {
    const {t} = useLocalizationState()
    const [statuses, setStatuses] = useAtom(casesStatusesAtom)
    const caseStatusOptions = useCaseStatusOptions()
    const isAllSelected = ALL_CASE_STATUSES.every(s => statuses.includes(s))
    const selected = ALL_CASE_STATUSES
        .filter(s => statuses.includes(s))
        .map(s => caseStatusOptions.find(option => option.value === s)?.value || '')
        .join(', ')


    return (
        <PopupButton
            transparent
            className="text-[14px] !text-main"
            popupContent={<div className="flex flex-col gap-1.5 p-2">
                <Checkbox
                    checked={isAllSelected}
                    onChange={() => {
                        setStatuses(isAllSelected ? [] : ALL_CASE_STATUSES)
                    }}
                    label={t.EMAIL.search_queue_case_content_all()}
                />
                {caseStatusOptions
                    .map(({value, text}) => {
                        return (
                            <Checkbox
                                key={value}
                                onChange={() => {
                                    setStatuses(curStatuses => {
                                        if (curStatuses.includes(value)) {
                                            return curStatuses.filter(s => s !== value)
                                        }
                                        return [...curStatuses, value]
                                    })
                                }}
                                checked={statuses.includes(value)}
                                label={text}
                            />
                        )
                    })}
            </div>}
        >
            {t.EMAIL.search_queue_case_status_label()}
            {': '}
            <span className="text-main-secondary">{(isAllSelected || !selected) ? t.EMAIL.search_queue_case_content_all() : selected}</span>
        </PopupButton>
    )
}
