import React, {useEffect, useMemo} from 'react'
import {useForm} from 'react-hook-form'
import {DevTool} from '@hookform/devtools'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Loader} from '@bpinc/lib-ui-elements'

import {useMergeContactsActions} from '../useMergeContactsActions'
import {AddContactForMere} from './AddContactForMere'
import {MergeActions} from './MergeActions'
import {MergeField} from './MergeField'
import type {MergeContactFormType} from './types'
import {useLoadMergeContactsData} from './useLoadMergeContactsData'
import {useMergeFormData} from './useMergeFormData'
import {useOnSubmit} from './useOnSubmit'


function getFieldClassName(rowIndex: number) {
    return tcn('row-auto bg-main-bg px-4 py-3', {
        'bg-black bg-opacity-5': rowIndex % 2,
        'sticky top-0 z-10 border-b border-main-border': rowIndex === 0,
    })
}


export const Merge: React.FC = () => {
    useLoadMergeContactsData()
    const {canAddContactForMerge} = useMergeContactsActions()
    const {formRows, contactsData, mergeContactsData} = useMergeFormData()
    const subgridRowSan = useMemo(() => ({gridRow: `1/${formRows.length + 1}`}), [formRows.length])

    const {
        control,
        handleSubmit,
        formState: {
            isSubmitting,
        },
        reset,
    } = useForm<MergeContactFormType>()

    useEffect(() => {
        if (mergeContactsData.length) {
            reset()
        }
    }, [mergeContactsData.length, reset])

    const isLoading = contactsData.some(item => item.isLoading)
    const onSubmit = useOnSubmit({handleSubmit})

    return (
        <div className="flex flex-1 flex-col overflow-hidden" >
            <div className="flex flex-1 flex-col overflow-hidden border-b border-main-border bg-main-bg">
                <div className="relative grid auto-cols-fr auto-rows-max overflow-y-auto">
                    <div className="grid grid-rows-subgrid" style={subgridRowSan}>
                        {formRows.map(({label, mapping}, rowIndex) => (
                            <div key={rowIndex} className={tcn(getFieldClassName(rowIndex), {'font-bold': !mapping})}>
                                {label}
                            </div>
                        ))}
                    </div>
                    {mergeContactsData.map(({rows}, contactIndex) => {
                        return (
                            <div key={contactIndex} className="relative grid grid-rows-subgrid" style={subgridRowSan}>
                                {rows.map(({fields}, rowIndex) => (
                                    <div key={rowIndex} className={getFieldClassName(rowIndex)}>
                                        {
                                            !isLoading && fields.map((field, fieldIndex) => (
                                                <div key={fieldIndex} >
                                                    <MergeField field={field} control={control} />
                                                </div>
                                            ))
                                        }
                                    </div>
                                ))}
                                {isLoading && <Loader active color="primary" size="huge" className="z-10" />}
                            </div>
                        )
                    })}
                    {
                        canAddContactForMerge && (
                            <div className="grid grid-rows-subgrid" style={subgridRowSan}>
                                {formRows.map((_, rowIndex) => (
                                    <div key={rowIndex} className={tcn(getFieldClassName(rowIndex), {'flex items-center justify-end': !rowIndex})}>
                                        {!rowIndex && <AddContactForMere />}
                                    </div>
                                ))}
                            </div>
                        )
                    }
                </div>
                {!formRows.length && <Loader active color="primary" size="huge" className="z-10" />}
            </div>
            <MergeActions
                disabled={isLoading}
                onReset={() => reset()}
                onConfirm={onSubmit}
                isSubmitting={isSubmitting}
            />
            {process.env.NODE_ENV === 'development' && <DevTool control={control} />}
        </div>
    )
}
