import React from 'react'

import {Notifications} from '@bpinc/lib-notifications-context'
import {Notifications as NotificationsUI} from '@bpinc/lib-notifications-ui'
import {OfflineBar} from '@bpinc/lib-offline-state-ui'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'

import {LeftBar} from './leftbar'
import {TopBar} from './topbar'


const errorIdsToOmit = [
    Notifications.DispositionNotSelectedError,
    Notifications.ContactCreationError,
    Notifications.ContactMergeError,
]

export const Navbar: FCWithChildren = ({children}) => {
    return (
        <div className="relative flex h-full flex-col overflow-hidden">
            <NotificationsUI omit={errorIdsToOmit} />
            <OfflineBar />
            <div className="relative flex grow overflow-hidden">
                <LeftBar />
                <div id="mainContainer" className="relative flex flex-auto flex-col overflow-hidden">
                    <TopBar />
                    <div className="flex flex-auto flex-col overflow-auto">
                        {children}
                    </div>
                </div>
            </div>
        </div>
    )
}
