import React from 'react'
import {DndProvider} from 'react-dnd'
import {HTML5Backend} from 'react-dnd-html5-backend'

import {createPlatformSpecific} from '@bpinc/ad-platform-specific-web-impl'
import {UIContextProvider} from '@bpinc/ad-ui-context'
import {AuthorizationModule} from '@bpinc/lib-authorization-module'
import {createProviderWrapper} from '@bpinc/lib-helpers'

import {FullscreenEvaluation} from './FullscreenEvaluation'


const EvaluationProviders = createProviderWrapper([
    UIContextProvider,
])

export const FullscreenEvaluationApp: React.FC = () => {
    return (
        <AuthorizationModule
            platformSpecific={createPlatformSpecific('omni-qm')}
            app="omni-qm"
            appRemoteName="omniQm"
        >
            {() => (
                <DndProvider backend={HTML5Backend}>
                    <EvaluationProviders>
                        <FullscreenEvaluation />
                    </EvaluationProviders>
                </DndProvider>
            )}
        </AuthorizationModule>
    )
}
