import React, {useCallback} from 'react'
import {useAtom, useAtomValue} from 'jotai'

import {Case} from '@bpinc/ad-case-ui'
import {Profile, ProfileCreate} from '@bpinc/ad-interaction-workspace-ui'
import {Tabs} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {searchPageActiveTabIdAtom, searchPageTabsAtom} from './atoms'
import {Merge} from './Merge'
import {Search} from './Search'
import {useContactsActions} from './useContactsActions'
import {useSearchPageTabsActions} from './useSearchPageTabsActions'
import {useUrlBasedNavigation} from './useUrlBasedNavigation'
import type {SearchPageTab} from './utils'


export const SearchPage: React.FC = () => {
    const {t} = useLocalizationState()

    const tabs = useAtomValue(searchPageTabsAtom)
    const [activeTabId, setActiveTabId] = useAtom(searchPageActiveTabIdAtom)

    const {handleTabClose} = useSearchPageTabsActions()
    const {handleContactCreateCancel} = useContactsActions()

    useUrlBasedNavigation()

    const renderTab = useCallback(({title, id}: SearchPageTab) => {
        if (id === 'search') {
            return t.SCREENPOP_FORM.searchButton()
        }
        return title
    }, [t.SCREENPOP_FORM])

    const renderTabContent = useCallback(
        (tab: SearchPageTab) => {
            switch (tab.type) {
                case 'search':
                    return (
                        <Search />
                    )
                case 'create_contact':
                    return <ProfileCreate {...tab.props} onCancel={handleContactCreateCancel} />
                case 'contact':
                    return <Profile contactId={tab.id} {...tab.props} />
                case 'case':
                    //TODO: Render appropriate Footer (FooterContent prop)
                    return <Case id={tab.id} />
                case 'merge_contacts':
                    return <Merge />
            }
        },
        [handleContactCreateCancel],
    )

    return (
        <Tabs
            data-testid="search-page"
            className="flex flex-auto flex-col overflow-auto"
            tabsContentClassName="flex-auto flex flex-col h-auto"
            tabs={tabs}
            activeTabId={activeTabId}
            renderTabContent={renderTabContent}
            renderTab={renderTab}
            onChange={setActiveTabId}
            onTabClose={handleTabClose}
        />
    )
}
