import type {HTMLAttributes} from 'react'
import {type FC, createElement} from 'react'

import {useContactDisplayName} from '@bpinc/ad-contacts-state-hooks'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {useActiveInteractionContactId, useActiveInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getDisplayName, getPhoneFromItem, isCallConference, isChatConference} from '@bpinc/ad-interaction-state-methods'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface InteractionBarDisplayNameProps extends HTMLAttributes<HTMLElement> {
    className?: string
    as?: 'p' | 'button'
}

export const InteractionBarDisplayName: FC<InteractionBarDisplayNameProps> = ({
    className,
    as = 'p',
    ...htmlProps
}) => {
    const {t} = useLocalizationState()
    const contactId = useActiveInteractionContactId()
    const interactionDisplayName = useActiveInteractionProp(getDisplayName)
    const isConference = useActiveInteractionProp(
        i => isCallConference(i) || isChatConference(i),
    )
    const contactDisplayName = useContactDisplayName(contactId)
    const phoneNumber = useActiveInteractionProp(getPhoneFromItem)
    const displayName = contactDisplayName || interactionDisplayName
    const conferenceMembersAmount = useActiveInteractionProp(i =>
        isChatConference(i)
            ? (i?.chatParties?.length ?? 0)
            : isCallConference(i)
                ? (i?.parties?.length ?? 0)
                : 0,
    )

    function getTextContent() {
        if (isConference) {
            return t.INTERACTIONS.conference(conferenceMembersAmount)
        }

        if (displayName) {
            return displayName
        }

        if (phoneNumber) {
            return `${t.GLOBAL.anonymous()} ${formatPhone(phoneNumber)}`
        }

        return t.GLOBAL.anonymous()
    }

    return createElement(as, {
        className: tcn(
            'cursor-default truncate font-semibold text-primary-on-primary body',
            className,
        ),
        children: getTextContent(),
        ...htmlProps,
    })


}
