import type {FC} from 'react'
import React, {useEffect, useMemo, useRef} from 'react'
import type {Control} from 'react-hook-form'
import {useWatch} from 'react-hook-form'
import {uniqBy} from 'lodash-es'

import {EMAIL_REGEX} from '@bpinc/ad-regexp-constants'
import {useBoolean} from '@bpinc/lib-helpers'
import {SelectionInput} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {SuggestionsPopup} from './SuggestionsPopup'
import type {EmailFormFields} from './types'
import {useSelectedEmailsState} from './useSelectedEmailsState'


interface EmailSelectionInputProps {
    name: 'to' | 'cc' | 'bcc'
    control: Control<EmailFormFields>
    label: string
}

export const EmailSelectionInput: FC<EmailSelectionInputProps> = ({
    label,
    name,
    control,
}) => {
    const {t} = useLocalizationState()
    const showEmailSuggestions = useBoolean()
    const {
        selectedItems,
        suggestedItems,
        addCustomEmail,
        addSelectedItem,
        handleOnChange,
        removeSelectedItem,
        value,
    } = useSelectedEmailsState(name, control)


    useEffect(() => {
        showEmailSuggestions.toggle(!!suggestedItems.length)
        //Change only when suggestedItems change
        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, [suggestedItems])

    const to = useWatch({name: 'to', control})
    const cc = useWatch({name: 'cc', control})
    const bcc = useWatch({name: 'bcc', control})


    const suggestedItemsExcludingSelected = useMemo(() => {
        const allSelectedItems = [...to ?? [], ...cc ?? [], ...bcc ?? []]
        return uniqBy(
            suggestedItems.filter(s => !allSelectedItems.some(i => i.address === s.address)),
            'address')
    }
    , [bcc, cc, suggestedItems, to])

    const rootRef = useRef<HTMLDivElement>(null)


    return (
        <div ref={rootRef} className="flex flex-auto overflow-auto">
            <label className="leading-[34px]">{label}</label>
            <SelectionInput
                items={selectedItems}
                className="w-full border-none"
                value={value}
                onChange={handleOnChange}
                validateCustomValue={v => {
                    if (v && !new RegExp(EMAIL_REGEX).test(v)) {
                        showEmailSuggestions.setFalse()
                        return {
                            valid: false,
                            message: t.ERROR_MESSAGES.error_email_format(),
                        }
                    } else {
                        return {valid: true}
                    }
                }}
                addCustomValue={addCustomEmail}
                onItemRemoved={removeSelectedItem}
                autoFocus
            />
            {showEmailSuggestions.value && (
                <SuggestionsPopup
                    showEmailSuggestions={showEmailSuggestions}
                    suggestedItems={suggestedItemsExcludingSelected}
                    onSelect={addSelectedItem}
                    selectionInputRootRef={rootRef}
                />
            )}
        </div>
    )
}
