import React from 'react'
import {get} from 'lodash-es'

import type {ActivityVoice as ActivityVoiceType, Party, Recordings} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {LegacyIconCallInbound} from '@bpinc/lib-icons'
import {AudioPlayer, Avatar} from '@bpinc/lib-ui-elements'

import {CaseActivityService} from './CaseActivityService'


interface Props {
    activity: ActivityVoiceType
    recordings?: Recordings
}

const getFirstAndLastNames = (party?: Party): Array<string | undefined> => {
    const names = party ? [party.firstName, party.lastName].filter(Boolean) : []
    return names.length ? names : ['Anonymous']
}

export const ActivityVoice: React.FC<Props> = ({activity, recordings = []}) => {
    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()
    const {createdTime, parties, services} = activity
    const partyInternal = parties.find(p => p.partyType === 'USER')
    const partyExternal = parties.find(p => p.partyType !== 'USER')

    const fullNameInternal = formatFullName(...getFirstAndLastNames(partyInternal))
    const fullNameExternal = formatFullName(...getFirstAndLastNames(partyExternal))
    const date = formatDate(createdTime)

    const title = [fullNameExternal, 'called on', `${date}, handled by`, fullNameInternal].join(' ')

    const avatar = {
        firstName: get(partyExternal, 'firstName'),
        lastName: get(partyExternal, 'lastName'),
        size: 40,
        url: getAvatarUrl({
            contactId: partyExternal?.contactId,
            notFoundPlaceholder: true,
        }),
    }

    return (
        <div className="relative flex p-2 [&:not(:first-child)]:pt-0">
            <div className="mr-2 flex-none">
                <Avatar
                    size={avatar.size}
                    firstName={avatar.firstName}
                    lastName={avatar.lastName}
                    url={avatar.url}
                />
            </div>
            <div className="flex-auto bg-[--c-activity-bg] p-2">
                <div className="flex">
                    <div className="mr-2 h-4 w-4 flex-none self-center [&_svg]:h-full [&_svg]:w-full">
                        <LegacyIconCallInbound />
                    </div>
                    <div className="mr-2 text-gray-500">
                        {title}
                    </div>
                </div>

                <div className="mt-2">
                    {recordings.map((recording: string) => {
                        return (
                            <div key={recording} className="rounded-[24px] bg-[--c-activity-record-bg] p-3 [&:not(:first-child)]:mt-2">
                                <AudioPlayer src={recording} />
                            </div>
                        )
                    })}
                </div>

                <CaseActivityService party={partyInternal} service={services?.[0]} />
            </div>
        </div>
    )
}
