import React, {useCallback} from 'react'

import {useActiveInteraction} from '@bpinc/ad-interaction-state-context'
import {
    isAfterCallWork,
    isCall,
    isChatConference,
    isOutboundInteraction,
    isPotentialInteraction,
} from '@bpinc/ad-interaction-state-methods'
import {useDisconnectButtonClickHandler} from '@bpinc/lib-helpers-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {DefaultContent} from './DefaultContent'
import {DialingInteractionContent} from './DialingInteractionContent'
import {OngoingCallContent} from './OngoingCallContent'
import {WrapUpContent} from './WrapUpContent'


const InteractionControlBar: React.FC = () => {
    const activeInteraction = useActiveInteraction()
    const disconnectItem = useDisconnectButtonClickHandler()
    const t = useLocalizationState('t')

    const onDisconnect = useCallback(
        function memoizeOnDisconnect() {
            return disconnectItem(activeInteraction)
        },
        [activeInteraction, disconnectItem],
    )

    if (
        (isOutboundInteraction(activeInteraction) &&
            activeInteraction?.callData?.callState === 'requesting') ||
        activeInteraction?.callData?.callState === 'dialing' ||
        activeInteraction?.callData?.callState === 'ringing'
    ) {
        return <DialingInteractionContent onDisconnect={onDisconnect} />
    }

    if (isAfterCallWork(activeInteraction)) {
        return <WrapUpContent />
    }

    if (isCall(activeInteraction)) {
        return <OngoingCallContent onDisconnect={onDisconnect} />
    }

    if (!activeInteraction || isPotentialInteraction(activeInteraction)) {
        return <div />
    }

    return (
        <DefaultContent
            onDisconnect={onDisconnect}
            disconnectButtonTooltip={isChatConference(activeInteraction) ? t.INTERACTIONS.end_conference() : t.INTERACTIONS.end_interaction()}
        />
    )
}

export {InteractionControlBar}
