import type {Party} from '@bpinc/ad-case-state-types'


export const getFirstAndLastNames = (
    party?: Party,
): Array<string | undefined> => {
    const names = party
        ? [party.firstName, party.lastName].filter(Boolean)
        : []
    return names.length ? names : ['Anonymous']
}

export function getAction(disposition: string | undefined) {
    return typeof disposition === 'undefined'
        ? 'left a note'
        : 'dispositioned interaction'
}
