import {useMemo} from 'react'

import {useCaseState} from '@bpinc/ad-case-state-context'
import type {ActivityCase} from '@bpinc/ad-case-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {
    selectAgentsString,
    selectDispositionsString,
    selectServicesAndDispositionsString,
} from '../activity-selectors'


interface UseActivityCaseTitleParams {
    activity: ActivityCase
    serviceId?: string
}

export function useActivityCaseTitle({activity, serviceId}: UseActivityCaseTitleParams) {
    const {t} = useLocalizationState()
    const {formatFullName} = useFormatting()
    const agentsString = selectAgentsString(formatFullName)(activity)
    const servicesAndDispositionsString =
      selectServicesAndDispositionsString(activity)
    const dispositionString = selectDispositionsString(activity)
    const pendingReasons = useCaseState('casePendingReasons')
    const canHaveDispositions = useServicesDispositionsState(
        s => serviceId && s.services[serviceId].useDispositions,
    )

    return useMemo(() => {
        const date = new Date(activity.createdTime).toLocaleDateString()
        const time = new Date(activity.createdTime).toLocaleTimeString()
        switch (activity.event) {
            case 'PULLED':
                return t.ACTIVITY_HISTORY.activity_view_case_pulled_label(
                    servicesAndDispositionsString,
                    agentsString,
                    date,
                    time,
                )
            case 'PUSHED':
                return t.ACTIVITY_HISTORY.activity_view_case_pushed_label(
                    servicesAndDispositionsString,
                    agentsString,
                    date,
                    time,
                )
            case 'ASSIGNED': {
                const assignedByName = formatFullName(
                    activity.assignedByFirstName,
                    activity.assignedByLastName,
                )

                return activity.parties.length
                    ? t.ACTIVITY_HISTORY.activity_view_case_assigned_to_agent_label(
                        assignedByName,
                        agentsString,
                        date,
                        time,
                    )
                    : t.ACTIVITY_HISTORY.activity_view_case_assigned_to_queue_label(
                        assignedByName,
                        servicesAndDispositionsString,
                        date,
                        time,
                    )
            }
            case 'TRANSFERRED': {
                const fromUser = formatFullName(
                    activity.transferredFromFirstName,
                    activity.transferredFromLastName,
                )
                return activity.parties.length
                    ? t.ACTIVITY_HISTORY.activity_view_case_transferred_to_agent_label(
                        fromUser,
                        agentsString,
                        date,
                        time,
                    )
                    : t.ACTIVITY_HISTORY.activity_view_case_transferred_to_queue_label(
                        fromUser,
                        servicesAndDispositionsString,
                        date,
                        time,
                    )
            }
            case 'FOLLOWUP_FROM':
            case 'FOLLOWUP_CREATED':
                return t.ACTIVITY_HISTORY.activity_view_case_created_event_label(
                    agentsString,
                    date,
                    time,
                )
            case 'STATE_CHANGED': {
                switch (activity.status) {
                    case 'Closed': {
                        return t.ACTIVITY_HISTORY.activity_case_state_closed(
                            date,
                            time,
                        )
                    }
                    case 'Open':
                        return t.ACTIVITY_HISTORY.activity_case_state_changed(
                            agentsString,
                            activity.status,
                            date,
                            time,
                        )
                    case 'Pending': {
                        return pendingReasons.data.length
                            ? t.ACTIVITY_HISTORY.activity_case_state_pending(
                                agentsString,
                                activity.status,
                                date,
                                time,
                                activity.pendingReasonName || t.ACTIVITY_HISTORY.activity_case_state_no_message(),
                            )
                            : t.ACTIVITY_HISTORY.activity_case_state_changed(
                                agentsString,
                                activity.status,
                                date,
                                time,
                            )
                    }
                    case 'Resolved': {
                        return canHaveDispositions
                            ? t.ACTIVITY_HISTORY.activity_case_state_disposition(
                                agentsString,
                                activity.status,
                                date,
                                time,
                                dispositionString || t.ACTIVITY_HISTORY.activity_case_state_no_message(),
                            )
                            : t.ACTIVITY_HISTORY.activity_case_state_changed(
                                agentsString,
                                activity.status,
                                date,
                                time,
                            )
                    }
                }
            }
        }
    }, [
        pendingReasons.data.length,
        canHaveDispositions,
        dispositionString,
        agentsString,
        servicesAndDispositionsString,
        activity,
        formatFullName,
        t.ACTIVITY_HISTORY,
    ])
}
