import {type FC} from 'react'
import {motion} from 'framer-motion'

import {useActiveInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getIsCallEstablished, getMediaIcon} from '@bpinc/ad-interaction-state-methods'
import {EndCallButton} from '@bpinc/lib-interaction-buttons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {InteractionMediaIcon, InteractionTimer} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {interactionBarStyles} from '../styles'
import {InteractionBarDisplayName} from './InteractionBarDisplayName'


interface DialingInteractionContentProps {
    onDisconnect: () => void
}

export const DialingInteractionContent: FC<DialingInteractionContentProps> = ({
    onDisconnect,
}) => {
    const iconType = useActiveInteractionProp(getMediaIcon)
    const isCallEstablished = useActiveInteractionProp(getIsCallEstablished)
    const pendingStartTime = useActiveInteractionProp(
        i => i?.displayData.pendingStartTime,
    )

    const {t} = useLocalizationState()

    function formatTimer(str: string) {
        return `${t.CALL_DETAILS_FORM.call_status_dialing()} | ${str}`
    }

    return (
        <div className={interactionBarStyles}>
            <motion.div className="flex items-center pl-4" whileHover="hover">
                <InteractionMediaIcon
                    icon={iconType}
                    className={tcn('icon-default', {
                        'animate-pulse-light':
                            !isCallEstablished && iconType === 'call_outbound',
                    })}
                />
                <InteractionTimer
                    className="ml-1 mr-2"
                    startTime={pendingStartTime}
                    format={formatTimer}
                />
                <motion.div
                    initial={{width: 0, opacity: 0, pointerEvents: 'none'}}
                    variants={{
                        hover: {width: 'auto', opacity: 1, pointerEvents: 'auto'},
                    }}
                >
                    <EndCallButton onClick={onDisconnect} />
                </motion.div>
            </motion.div>
            <InteractionBarDisplayName className="ml-2" />
        </div>
    )
}
